<template>
  <div class="the-futures-and-my-orders" :class="[ `the-futures-and-my-orders__${theme}`]">
    <div v-if="demoStep === 10" class="the-futures-and-my-orders__demo">
      <e-demo title="Positions" arrowPosition="bottom" :isPrevDisabled="true" :nextAction="scrollToPositionClose">
        Congratulations, you have opened your first position! To understand what each number means, simply hover your mouse over the column title to see an explanation.
      </e-demo>
    </div>

    <div
      class="the-futures-and-my-orders__tabs"
    >
      <e-tabs
        :tabs="tabs"
        v-model="activeTab"
      />
    </div>

    <the-news v-if="activeTab === NEWS_AND_ORDERS_TAB_NEWS && !isLogin"/>
    <the-futures-positions v-if="activeTab === NEWS_AND_ORDERS_TAB_POSITIONS"/>
    <the-futures-positions-closed v-if="activeTab === NEWS_AND_ORDERS_TAB_CLOSED_POSITION"/>
  </div>
</template>

<script>
/**
 * Компонент для отображения новостей и моих ордеров
 *
 * @version 1.0.5
 */

import { mapGetters } from 'vuex';

// Сomponents
import TheNews from '@/containers/blocks/TheNews';
import TheFuturesPositions from '@/containers/blocks/TheFuturesPositions';
import TheFuturesPositionsClosed from '@/containers/blocks/TheFuturesPositionsClosed';
import TheFuturesMyOrders from '@/containers/blocks/TheFuturesMyOrders';
import ETabs from '@/components/ETabs';
import EDemo from '@/components/EDemo';

const TAB_NEWS = {
  id: 4,
  title: 'News',
};

const TAB_POSITIONS = {
  id: 0,
  title: 'Positions',
};

const TAB_CLOSED_POSITIONS = {
  id: 2,
  title: 'Closed positions',
};

const TABS_FOR_USER_WITH_WALLET = [
  TAB_POSITIONS,
  TAB_CLOSED_POSITIONS,
];

const TABS_FOR_USER_WITHOUT_WALLET = [TAB_POSITIONS, TAB_NEWS];

export default {
  name: 'the-futures-positions-and-my-orders',

  computed: {
    /**
     * Номер выбранной по умолчанию вкладки
     */
    activeTab: {
      get() {
        return this.$store.state.ui.positionsTab;
      },

      set(activeTab) {
        this.$store.commit('ui/SET_POSITIONS_TAB', activeTab);
      },
    },

    /**
     * Массив со вкладками
     * В зависимости от того, залогинен или нет пользователь показываются либо только Новости, либо Позиции и Ордера
     */
    tabs() {
      return this.isLogin ? TABS_FOR_USER_WITH_WALLET : TABS_FOR_USER_WITHOUT_WALLET;
    },

    ...mapGetters({
      activeWallet: 'wallets/activeWallet',
      demoStep: 'ui/demoStep',
      isLogin: 'wallets/isLogin',
      theme: 'ui/getTheme',
    }),
  },

  methods: {
    scrollToPositionClose() {
      const positionsTableWrapper = document.getElementById('positionsTableWrapper');
      positionsTableWrapper.scrollLeft = 1000;
      this.$store.commit('ui/SET_DEMO_STEP', this.demoStep + 1);
    },
  },

  components: {
    TheNews,
    TheFuturesPositions,
    TheFuturesPositionsClosed,
    TheFuturesMyOrders,
    ETabs,
    EDemo,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-futures-and-my-orders {
  $this: &;

  position: relative;
  height: calc(50% - 6px);
  margin-top: 12px;
  background: #fff;
  border: 1px solid #DBDCDD;
  border-radius: 3px;

  @include r(1279) {
    height: auto;
  }

  &__demo {
    position: absolute;
    z-index: 20;
    top: 0;
    left: 0;
    transform: translate(calc(-50% + 35px), calc(-100% - 10px));

    @include r(1279) {
      left: 0;
      transform: translate(0, calc(-100% - 10px));
    }
  }

  .e-demo {
    width: 290px;
  }

  &__tabs {
    margin-bottom: 0;
    padding: 8px 10px;
    background: #fff;
    border-bottom: 1px solid #DBDCDD;
  }

  .e-container-scroll {
    overflow-y: scroll;
    height: calc(100% - 34px);

    &::-webkit-scrollbar-track {
      background-color: #fff;
    }
  }

  &__dark {
    background: #121722;
    border-color: #282C38;

    #{$this}__tabs {
      background: #121722;
      border-color: #1e202b;
    }

    .e-container-scroll {
      &::-webkit-scrollbar-track {
        background-color: #121722;
      }
    }
  }
}
</style>



