<template>
  <div
    class="the-trades"
    :class="{ 'the-trades__loading': isPendingOrUnsync, 'the-trades__dark': theme === 'dark' }"
  >
    <div class="the-trades__tabs">
      <e-tabs
        :value="activeTabId"
        :tabs="tabs"
        @input="handleTabChange"
      />
    </div>

    <!-- Прелоудер который отображается на во время загрузки данных -->
    <e-preloader v-if="isPendingOrUnsync"/>

    <div
      v-if="trades.length !== 0"
      class="the-trades__body"
    >
      <!-- BEGIN: Таблица с трейдами -->
      <div class="the-trades__inner the-trades__inner_no-pagination">
        <div class="the-trades__inner-head">
          <div class="the-trades__inner-head-item the-trades__inner-head-name"><span>Price</span></div>
          <div class="the-trades__inner-head-item the-trades__inner-head-volume">Size</div>
          <div class="the-trades__inner-head-item the-trades__inner-head-eth">USD</div>
          <div class="the-trades__inner-head-item the-trades__inner-head-date">Time</div>
          <div class="the-trades__inner-head-item the-trades__inner-head-exchange">E</div>
        </div>

        <e-container-scroll
          class="the-trades__wrapper"
          ref="scroll"
        >
          <div class="the-trades__list">
            <e-futures-transaction
              v-for="item in trades"
              :key="item.id"
              :type="item.type"
              :amount-filled="item.amountFilled"
              :price="item.price"
              :decimalsAsset="item.decimals"
              :amountDec="activeContract.amountDec"
              :time="item.createdAt"
              :exchange="item.exchange"
              :confirmation-state="mapOperationStatus(item.isConfirmed)"
              :decimals="contractBaseToken.decimals"
              :is-my="isTabMyTradesActive"
              @click="showDetailsIfTradeIsMine(item)"
            />
          </div>
        </e-container-scroll>
      </div>
      <!-- END: Таблица с трейдами -->
    </div>

    <div
      class="the-trades__error"
      v-else
    >
      No data
      <!--<e-panel-error-->
        <!--:icon="errorIcon"-->
        <!--buttons-->
      <!--&gt;-->
        <!--<template slot="title">Your Trades</template>-->

        <!--<template slot="descr">No active trades found {{ activeContractName ? `with ${activeContractName}` : '' }}</template>-->

        <!--<div-->
          <!--slot="after"-->
          <!--class="the-trades__error-btns"-->
        <!--&gt;-->
          <!--<div class="the-trades__error-btn the-trades__error-btn_sell">-->
            <!--<div class="the-trades__error-btn-arrow">-->
              <!--<e-icon :src="iconLoss"/>-->
            <!--</div>-->
            <!--<div class="the-trades__error-btn-text">-->
              <!--sell {{ activeContractName }}-->
            <!--</div>-->
          <!--</div>-->
          <!--<div class="the-trades__error-separator">or</div>-->
          <!--<div class="the-trades__error-btn the-trades__error-btn_buy">-->
            <!--<div class="the-trades__error-btn-text">-->
              <!--buy {{ activeContractName }}-->
            <!--</div>-->
            <!--<div class="the-trades__error-btn-arrow">-->
              <!--<e-icon :src="iconProfit"/>-->
            <!--</div>-->
          <!--</div>-->
        <!--</div>-->
      <!--</e-panel-error>-->
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapState } from 'vuex';
import { mapOperationStatus } from '@/api/backend/helpers';
import { ASYNC_STATES } from '@/util/constants';
import { onReconnect, removeReconnectListener } from '@/api/backend';
import { getTrades, onTradeChange, removeTradeChangeHandler, tradeTransformer } from '@/api/backend/futures/trades';
import { onResume, removeResumeListener } from '@/util/IdleEmitter';
import { PAGE_SIZE_TRADES } from '@/config';

// Components
import EContainerScroll from '@/components/EContainerScroll';
import EPanelError from '@/components/EPanelError';
import EPreloader from '@/components/EPreloader';
import ETabs from '@/components/ETabs';
import EFuturesTransaction from '@/components/EFuturesTransaction';
import ESwitch from '@/components/ESwitch';
import EIcon from '@/components/EIcon';

// Icons
import errorIcon from '@/icons/e-icon_empty-orders.svg';
import iconLoss from '@/icons/e-icon_loss.svg';
import iconProfit from '@/icons/e-icon_profit.svg';

const TAB_ALL_TRADES = {
  id: 0,
  title: 'Trade history',
};

const TAB_MY_TRADES = {
  id: 1,
  title: 'my trades',
};

const TABS_FOR_USER_WITH_WALLET = [
  TAB_ALL_TRADES,
  // TAB_MY_TRADES,
];

const TABS_FOR_USER_WITHOUT_WALLET = [TAB_ALL_TRADES];

const getTradesWithTradeUpserted = ({ trade, trades }) => {
  const currentIndexOfNextElement = trades.findIndex(t => t.id <= trade.id);
  const result = [...trades];
  if (currentIndexOfNextElement !== -1 && trades[currentIndexOfNextElement].id === trade.id) {
    result[currentIndexOfNextElement] = trade;
    return result;
  }
  const indexToInsert = currentIndexOfNextElement !== -1 ? currentIndexOfNextElement : trades.length;
  result.splice(indexToInsert, 0, trade);
  return result.slice(0, PAGE_SIZE_TRADES);
};

export default {
  name: 'the-futures-trades',

  data() {
    return {
      activeTabId: TAB_ALL_TRADES.id,

      myTrades: [],
      myTradesStatus: ASYNC_STATES.INITIAL,
      myTradesHandleNewTrade: null,
      myTradesHandleReconnect: null,
      myTradesHandleResume: null,
    };
  },

  computed: {
    ...mapGetters({
      userAddress: 'wallets/activeWalletAddress',
      activeContractName: 'futuresActiveContract/contractName',
      contractBaseToken: 'futuresActiveContract/contractBaseToken',
      latestTradesLoading: 'futuresTrades/latestTradesLoading',
      theme: 'ui/getTheme',
    }),

    ...mapState({
      activeContract: state => state.futuresActiveContract.activeContract,
    }),

    isTabMyTradesActive() {
      return this.activeTabId === TAB_MY_TRADES.id;
    },

    isPendingOrUnsync() {
      const tabAllTradesIsPendingOrUnsync = this.activeTabId === TAB_ALL_TRADES.id && this.latestTradesLoading;
      const tabMyTradesPendingOrUnsync = (
        this.activeTabId === TAB_MY_TRADES.id &&
        [ASYNC_STATES.INITIAL, ASYNC_STATES.PENDING, ASYNC_STATES.UNSYNC].includes(this.myTradesStatus)
      );

      return tabAllTradesIsPendingOrUnsync || tabMyTradesPendingOrUnsync;
    },

    tabs() {
      return this.$store.getters['wallets/isLogin'] ? TABS_FOR_USER_WITH_WALLET : TABS_FOR_USER_WITHOUT_WALLET;
    },

    trades() {
      return this.activeTabId === TAB_ALL_TRADES.id
        ? this.$store.state.futuresTrades.latestTrades
        : this.myTrades;
    },
  },

  watch: {
    userAddress() {
      if (this.activeTabId === TAB_ALL_TRADES.id) {
        // nothing
      } else {
        this.myTradesStatus = ASYNC_STATES.INITIAL;
        this.fetchMyTrades().then();
      }
    },

    activeContractName() {
      if (this.activeTabId === TAB_ALL_TRADES.id) {
        this.$store.dispatch('futuresTrades/loadLatestTrades');
      } else {
        this.myTradesStatus = ASYNC_STATES.INITIAL;
        this.fetchMyTrades().then();
      }
    },
  },

  created() {
    Object.assign(this, {
      errorIcon,
      iconProfit,
      iconLoss,
    });
    this.fetchMyTrades = this.fetchMyTrades.bind(this);
    this.startListeningToAllTrades();
  },

  beforeDestroy() {
    if (this.activeTabId === TAB_ALL_TRADES.id) {
      this.stopListeningToAllTrades();
    } else {
      this.stopListeningToMyTrades();
    }
  },

  methods: {
    mapOperationStatus,

    handleTabChange(nextActiveTabId) {
      this.activeTabId = nextActiveTabId;
      if (this.activeTabId === TAB_ALL_TRADES.id) {
        this.stopListeningToMyTrades();
        this.startListeningToAllTrades();
      } else {
        this.stopListeningToAllTrades();
        this.startListeningToMyTrades();
      }
    },

    async fetchMyTrades() {
      const activeContract = this.$store.state.futuresActiveContract.activeContract;
      const userAddress = this.$store.getters['wallets/activeWalletAddress'];

      if ([ASYNC_STATES.INITIAL, ASYNC_STATES.UNSYNC].includes(this.myTradesStatus)) {
        this.myTradesStatus = ASYNC_STATES.PENDING;
      }
      try {
        const myTradesResp = await getTrades({
          userAddress,
          futuresAssetName: activeContract.asset,
          isNotUnconfirmed: true,
        });
        this.myTrades = myTradesResp.data.map(tradeTransformer);
        if (this.myTradesStatus === ASYNC_STATES.PENDING) {
          this.myTradesStatus = ASYNC_STATES.SUCCESS;
        }
      } catch (e) {
        if (this.myTradesStatus === ASYNC_STATES.PENDING) {
          this.myTradesStatus = ASYNC_STATES.ERROR;
        }
        throw e;
      }
    },

    /**
     * Part of the code in watchers
     */
    startListeningToMyTrades() {
      this.myTradesHandleNewTrade = (tradeEvt) => {
        const trade = tradeTransformer(tradeEvt.data);
        const userAddress = this.$store.getters['wallets/activeWalletAddress'];
        const initializedAndNotSyncing = this.myTradesStatus === ASYNC_STATES.SUCCESS;
        if (
          trade.futuresAssetName === this.activeContract.futuresAssetName &&
          (trade.makerAddress === userAddress || trade.takerAddress === userAddress) &&
          trade.isConfirmed !== false &&
          initializedAndNotSyncing
        ) {
          this.myTrades = getTradesWithTradeUpserted({ trade, trades: this.myTrades });
        }
      };
      onTradeChange(this.myTradesHandleNewTrade);

      // add handlers
      const unsyncHandler = () => {
        this.myTradesStatus = ASYNC_STATES.UNSYNC;
        this.fetchMyTrades().then();
      };
      this.myTradesHandleReconnect = unsyncHandler;
      onReconnect(this.myTradesHandleReconnect);
      this.myTradesHandleResume = unsyncHandler;
      onResume(this.myTradesHandleResume);

      // init
      this.fetchMyTrades().then();
    },

    stopListeningToMyTrades() {
      // remove handlers
      removeTradeChangeHandler(this.myTradesHandleNewTrade);
      this.myTradesHandleNewTrade = null;
      removeReconnectListener(this.myTradesHandleReconnect);
      this.myTradesHandleReconnect = null;
      removeResumeListener(this.myTradesHandleResume);
      this.myTradesHandleResume = null;

      // reset state
      this.myTrades = [];
      this.myTradesStatus = ASYNC_STATES.INITIAL;
    },

    startListeningToAllTrades() {
      this.$store.dispatch('futuresTrades/listenToTradesChanges');
      this.$store.dispatch('futuresTrades/loadLatestTrades');
    },

    stopListeningToAllTrades() {
      this.$store.dispatch('futuresTrades/stopListeningToTradesChanges');
    },

    ...mapActions({
      showDetailsIfTradeIsMine: 'futuresTrades/showDetailsIfTradeIsMine',
    }),
  },

  components: {
    EContainerScroll,
    EPanelError,
    EPreloader,
    ETabs,
    EFuturesTransaction,
    ESwitch,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-trades {
  $this: &;
  position: relative;
  height: calc(100% - 2px);
  background: #fff;
  border: 1px solid #DBDCDD;
  border-radius: 3px;
  overflow: hidden;

  &__body {
    height: calc(100% - 27px);
    overflow: hidden;
    position: relative;
  }

  &__tabs {
    margin-bottom: 0;
    padding: 8px 10px;
    background: #fff;
    border-bottom: 1px solid #DBDCDD;

    @include r(1439) {
      padding: 8px 6px;
    }
  }

  &__error {
    display: flex;
    align-items: center;
    justify-content: center;
    color: $color-blue;
    font-size: 11px;
    font-weight: 500;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    padding-bottom: 28px;
    pointer-events: none; /* hack to allow menu clicking */
  }

  &__inner {
    height: calc(100% - 30px);
    transition: opacity 0.5s ease-in-out;

    &.is-loading {
      opacity: 0.2;
    }
  }

  &__inner_no-pagination {
    height: 100%;
  }

  &__pagination {
    margin-top: 10px;
  }

  &__wrapper {
    padding-left: 0;
    padding-right: 0;
  }

  &__external-switch {
    position: absolute;
    top: 0;
    right: 14px;
  }

  &__error-btns {
    display: flex;
    margin-top: 18px;
  }

  &__error-btn-arrow {
    height: 16px;
    width: 18px;
    display: inline-block;
    position: relative;
    top: 4px;
  }

  &__error-btn-text {
    color: $color-semi-blue;
    font-size: 13px;
    text-transform: uppercase;
    display: inline-block;
    font-weight: 700;
    transition: color .15s ease-in-out;
  }

  &__error-btn_sell {
    #{$this}__error-btn-arrow {
      margin-right: 7px;
    }
  }

  &__error-btn_buy {
    #{$this}__error-btn-arrow {
      margin-left: 7px;
    }
  }

  &__error-separator {
    font-size: 12px;
    color: $color-semi-blue;
    font-weight: 400;
    margin: 0 11px;
    padding-top: 1px;
    transition: color .15s ease-in-out;
  }

  &__inner-head {
    display: flex;
    font-size: 8px;
    text-transform: uppercase;
    color: $color-light-grey;
    letter-spacing: .5px;
    padding: 0 2px 0 0;
    background: #FBFBFD;
    border-bottom: 1px solid #F0F6F9;

    @include r(1279) {
      padding-left: 0;
    }
  }

  &__inner-head-item {
    padding: 8px 5px;
    box-sizing: border-box;
    border-right: 1px solid #F0F6F9;

    &:last-child {
      border-right: 0;
    }

    @include r(1439) {
      padding: 8px 5px;
    }
  }

  &__inner-head-name {
    display: flex;
    flex: 0 0 calc(40% - 40px);
    align-items: center;
    justify-content: flex-end;
  }

  &__inner-head-volume {
    flex: 0 0 calc(30% - 23px);
    text-align: right;
  }

  &__inner-head-eth {
    flex: 0 0 calc(30% - 15px);
    text-align: right;
  }

  &__inner-head-date {
    flex: 0 0 54px;
    text-align: right;
  }

  &__inner-head-exchange {
    flex: 0 0 24px;
    text-align: center;
  }

  .e-container-scroll {
    overflow-y: scroll;
    height: calc(100% - 34px);

    &::-webkit-scrollbar-track {
      background-color: #fff;
    }
  }

  &__dark {
    background: #141924;
    border-color: #292d3d;

    .e-container-scroll {
      &::-webkit-scrollbar-track {
        background-color: #121722;
      }
    }

    #{$this}__error {
      color: #D9D9D9;
    }

    #{$this}__tabs {
      background: #121722;
      border-color: #1e202b;
    }

    #{$this}__inner-head-item {
      border-color: #282C38;
    }

    #{$this}__inner-head {
      color: $color-dark-grey;
      background: #1C222D;
      border-color: #282C38;
    }
  }
}

// Анимации
.trades-list-move {
  transition: all .4s;
  z-index: 1;
}

.the-trades__loading .trades-list-move {
  transition: none;
}

.trades-list-enter-active > .e-transaction__bg {
  transition: all .4s;
}

.trades-list-enter > .e-transaction__bg {
  opacity: 1;
  transition: all .4s;
}

.trades-list-enter-to > .e-transaction__bg {
  opacity: 0;
}
</style>



