<template>
  <form
    class="the-order"
    :class="[ `the-order__${theme}`]"
    @submit.prevent="submitOrder"
  >
    <div class="the-order__wrapper">
      <div class="the-order__order-type">
        <button
          type="button"
          class="the-order__order-type-item"
          @click="orderType = ORDER_TYPE_MARKET"
          :class="{'active': orderType === ORDER_TYPE_MARKET}"
        >{{ ORDER_TYPE_MARKET }}</button>
        <button
          type="button"
          class="the-order__order-type-item"
          @click="orderType = ORDER_TYPE_LIMIT"
          :class="{'active': orderType === ORDER_TYPE_LIMIT}"
        >{{ ORDER_TYPE_LIMIT }}</button>
        <button
          type="button"
          class="the-order__order-type-item"
          @click="orderType = ORDER_TYPE_STOP_LIMIT"
          :class="{'active': orderType === ORDER_TYPE_STOP_LIMIT}"
        >{{ ORDER_TYPE_STOP_LIMIT }}</button>
      </div>

      <!--<div v-if="!positionToClose" class="the-order__switch-option" :class="[buyOrSell === BUY_TOKEN_ORDER ? 'isBuy' : 'isSell' ]" @click="switchOptionClick">-->
        <!--<div-->
          <!--class="the-order__switch-option-buy"-->
          <!--:class="{'active': buyOrSell === BUY_TOKEN_ORDER}"-->
          <!--@click="buyOrSell = BUY_TOKEN_ORDER"-->
        <!--&gt;-->
          <!--Buy-->
        <!--</div>-->
        <!--<div-->
          <!--class="the-order__switch-option-sell"-->
          <!--:class="{'active': buyOrSell === SELL_TOKEN_ORDER}"-->
          <!--@click="buyOrSell = SELL_TOKEN_ORDER"-->
        <!--&gt;-->
          <!--Sell-->
        <!--</div>-->
      <!--</div>-->

      <div v-if="positionToClose" class="the-order__position-close">
        Close <b>{{positionToClose.side ? 'LONG' : 'SHORT' }}</b> position of <b><e-big-number :value="positionToClose.size" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="4"/> BTC</b>
        <br>
        @ <b><e-big-number :value="positionToClose.price" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="2"/> USD</b>

        <button type="button" class="the-order__position-close-close" @click="closePositionInfo">
          Close
        </button>
      </div>

      <div v-if="!positionToClose">
        <div class="the-order__expiration">
          <div v-if="demoStep === 5" class="the-order__demo-expiration">
            <e-demo title="Expiration term" arrowPosition="right" arrowPositionMobile="top">
              The expiration term defines the maximum life of your position. After expiration the position is automatically closed at the Mark Price.
              <br /><br />
              <b>H</b> = Hours<br />
              <b>D</b> = Days
            </e-demo>
          </div>

          <div class="the-order__expiration-label">Expiration Term</div>
          <div class="the-order__expiration-wrapper">
          <div
              class="the-order__expiration-wrapper-item"
              v-for="contract of margins[activeContract.margin]"
              :key="contract[activeContract.side].id"
              :class="{ 'active': activeContract.expiresInSeconds === contract[activeContract.side].expiresInSeconds, 'perpetual': contract[activeContract.side].expiresInSeconds === 0 }"
              @click="changeActiveContractRoute(contract[activeContract.side].futuresContractHash)">
              <span v-if="formatExpires(contract[activeContract.side].expiresInSeconds) !== '∞'">{{formatExpires(contract[activeContract.side].expiresInSeconds)}}</span>
              <e-icon v-else :src="iconInfinity" />
            </div>
          </div>
        </div>
      </div>

      <div v-if="!positionToClose">
        <div class="the-order__percentage">
          <div class="the-order__percentage-item" @click="setAmountPercent(0.25)">25%</div>
          <div class="the-order__percentage-item" @click="setAmountPercent(0.5)">50%</div>
          <div class="the-order__percentage-item" @click="setAmountPercent(0.75)">75%</div>
          <div class="the-order__percentage-item" @click="setAmountPercent(1)">100%</div>
        </div>
      </div>

      <component
        :is="({
          [ORDER_TYPE_LIMIT]: 'the-futures-order-form-limit',
          [ORDER_TYPE_MARKET]: 'the-futures-order-form-market',
          [ORDER_TYPE_STOP_LIMIT]: 'the-futures-order-form-stop-limit',
        })[orderType]"
        :key="orderType"
        v-bind="orderParams"
        :anyDirty.sync="anyDirty"
        :buy-or-sell="buyOrSell"
        :setAmountPercent="setAmountPercent"
        class="the-order__type-inputs"
        @input="v => { orderParams = v }"
        @update:invalid="v => { invalid = v }"
      >
        <template slot="collateral">
          <div v-if="positionToClose" class="the-order__change-wrapper">
            <div v-if="orderParams.limitPrice && changePrice" class="the-order__profit-value">
              <span class="the-order__change the-order__profit" v-if="changePrice.isGreaterThanOrEqualTo(0)">
                <e-big-number :value="changePrice.dividedBy(this.ethUsdPrice)" :decimals="0" :decimal-places="8"/>
              </span>
              <span class="the-order__change the-order__loss" v-else>
                <e-big-number :value="changePrice.dividedBy(this.ethUsdPrice)" :decimals="0" :decimal-places="8"/>
              </span>
            </div>
            <div v-if="orderParams.limitPrice" class="the-order__change-label">
              <span v-if="changePrice.isGreaterThanOrEqualTo(0)">
                Profit
              </span>
              <span v-else>
                Loss
              </span>
            </div>
          </div>
          <!--<a v-else :href="priceSource.url" class="the-order__price-source" target="_blank">-->
          <!--<div class="the-order__price-source-label">Price Source</div>-->
          <!--<div class="the-order__price-source-value">{{priceSource.label}}</div>-->
          <!--</a>-->
        </template>
      </component>

      <div v-if="!positionToClose">
        <div class="the-order__leverage">
          <div v-if="demoStep === 6" class="the-order__demo-leverage">
            <e-demo title="Leverage" arrowPosition="right" arrowPositionMobile="top">
              Leverage determines the amount of funds that will be locked up as collateral (margin) for your position. The higher the leverage, the higher the risk and reward.
              <br /><br />
              <b>Be careful when trading with high leverage!</b>
            </e-demo>
          </div>

          <div class="the-order__leverage-label">Leverage</div>
          <div class="the-order__leverage-wrapper">
            <div
              class="the-order__leverage-wrapper-item"
              v-for="(_unused, marginName) in margins"
              :key="marginName"
              @click="handleSelectMargin(marginName)"
              :class="{ 'fill': activeContract.margin > marginName, 'active': activeContract.margin == marginName }">
              <span>{{marginName}}x</span>
            </div>
          </div>
        </div>
      </div>

      <div class="the-order__prices">
        <div v-if="!positionToClose" class="the-order__prices-item">
          <div class="the-order__prices-item-label">
            <e-tooltip position="right">
              <template slot="label">
                <div class="the-order__info-wrapper">
                  <div class="the-order__info-icon">
                    <e-icon :src="iconInfo"/>
                  </div>
                  Margin:
                </div>
              </template>
              <template slot="message">
                The amount that will be reserved on your account to cover the maximum possible loss for this order. The margin will be released once the position is closed or settled.
              </template>
            </e-tooltip>
          </div>
          <div class="the-order__prices-item-value">
            <span v-if="collateral !== null">
              <span class="the-order__prices-item-num">
                <e-big-number :value="collateral" :sign="false" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="decimalBalance"/>
              </span>
              {{currencySlug}}
            </span>
            <span v-else>--</span>
          </div>
        </div>

        <div class="the-order__prices-item">
          <div class="the-order__prices-item-label">
            <e-tooltip position="left">
              <template slot="label">
                <div class="the-order__info-wrapper">
                  <div class="the-order__info-icon">
                    <e-icon :src="iconInfo"/>
                  </div>
                  Funding rate:
                </div>
              </template>
              <template slot="message">
                Funding Cost is paid on the total value of your position for the period the position is open.
              </template>
            </e-tooltip>
          </div>
          <div class="the-order__prices-item-value">
            <span v-if="true">
              {{fundingRatePct}}% / 8h
            </span>
            <span v-else>--</span>
          </div>
        </div>
        <div class="the-order__prices-item full">
          <div class="the-order__prices-item-label">
            <e-tooltip position="left">
              <template slot="label">
                <div class="the-order__info-wrapper">
                  <div class="the-order__info-icon">
                    <e-icon :src="iconInfo"/>
                  </div>
                  Order Value:
                </div>
              </template>
              <template slot="message">
                The total value of your order in USD (Price x Amount)
              </template>
            </e-tooltip>
          </div>
          <div class="the-order__prices-item-value">
            <span v-if="orderValue !== null">
              <e-big-number :value="orderValue" :decimals="16" :decimal-places="2"/> USD
            </span>
            <span v-else>&#45;&#45;</span>
          </div>
        </div>

      </div>

      <div v-if="!positionToClose" class="the-order__submit-buttons">
        <div v-if="demoStep === 8" class="the-order__demo-submit">
          <e-demo title="Choose direction" arrowPosition="right" :isNextDisabled="true" arrowPositionMobile="top">
            Choose whether you want to Buy (go Long) or Sell (go short) and press the apropriate button.
          </e-demo>
        </div>

        <button
          key="buy"
          @click="buyOrSell = BUY_TOKEN_ORDER"
          type="submit"
          class="the-order__submit-btn the-order__buy-btn"
          :disabled="invalid"
        >
          <span class="the-order__submit-btn-type-buy" :class="{ 'full': orderParams.amount || orderParams.stopPrice }">
            Buy / Long
          </span>
          <!-- <span v-if="orderParams.amount" class="the-order__submit-btn-amount">
            {{ orderParams.amount | shortenNumber }} @ <span v-if="orderType === ORDER_TYPE_MARKET">MARKET</span><span v-else>{{ orderParams.limitPrice }}</span>
          </span>
          <span v-if="orderParams.stopPrice" class="the-order__submit-btn-stop">
            Trigger: ≥{{ orderParams.stopPrice }}
          </span> -->
        </button>

        <button
          key="sell"
          @click="buyOrSell = SELL_TOKEN_ORDER"
          type="submit"
          class="the-order__submit-btn the-order__sell-btn"
          :disabled="invalid"
        >
          <span class="the-order__submit-btn-type-sell" :class="{ 'full': orderParams.amount || orderParams.stopPrice }">
            Sell / Short
          </span>
          <!-- <span v-if="orderParams.amount" class="the-order__submit-btn-amount">
            {{ orderParams.amount | shortenNumber }} @ <span v-if="orderType === ORDER_TYPE_MARKET">MARKET</span><span v-else>{{ orderParams.limitPrice }}</span>
          </span>
          <span v-if="orderParams.stopPrice" class="the-order__submit-btn-stop">
            Trigger: ≤{{ orderParams.stopPrice }}
          </span> -->
        </button>
      </div>

      <div v-if="positionToClose" class="the-order__submit">
        <div v-if="demoStep === 12" class="the-order__demo-position">
          <e-demo title="Close Position" arrowPosition="right" :isNextDisabled="true" arrowPositionMobile="top">
            Click on the “Close” button to close your position.
          </e-demo>
        </div>

        <button
          v-if="buyOrSell === SELL_TOKEN_ORDER"
          key="sell"
          type="submit"
          class="the-order__submit-btn the-order__sell-btn"
          :disabled="invalid"
        >
          Close {{ orderParams.amount | shortenNumber }} {{ activeContractName }}
        </button>
        <button
          v-else
          key="buy"
          type="submit"
          class="the-order__submit-btn the-order__buy-btn"
          :disabled="invalid"
        >
          Close {{ orderParams.amount | shortenNumber }} {{ activeContractName }}
        </button>
      </div>
    </div>

    <!--<div class="the-order__market-status">-->
      <!--<div class="the-order__market-status-label">-->
        <!--Market Status-->
      <!--</div>-->

      <!--<div class="the-order__market-status-type open">-->
        <!--Open-->
      <!--</div>-->
    <!--</div>-->
  </form>
</template>

<script>
import BigNumber from 'bignumber.js';
import get from 'lodash/get';
import { mapState, mapGetters } from 'vuex';
import balanceMixin from '@/util/mixins/balanceMixin';
import shortenNumber from '@/util/format/shortenNumber';

import TheFuturesOrderFormLimit from '@/containers/blocks/TheFuturesOrderFormLimit';
import TheFuturesOrderFormMarket from '@/containers/blocks/TheFuturesOrderFormMarket';
import TheFuturesOrderFormStopLimit from '@/containers/blocks/TheFuturesOrderFormStopLimit';
import EBigNumber from '@/components/EBigNumber';
import EIcon from '@/components/EIcon';
import ETooltip from '@/components/ETooltip';
import EDemo from '@/components/EDemo';

import iconArrowDowner from '@/icons/e-icon_arrow_downer.svg';
import iconClose from '@/icons/e-icon_close.svg';
import iconInfo from '@/icons/e-icon__info.svg';
import iconInfinity from '@/icons/e-icon__infinity.svg';
import { setTourStep } from '@/api/backend/statistics';

import { CREATE_ORDER_PENDING, CREATE_ORDER_SUCCESS } from '@/store/constants';
import {
  ASYNC_STATES,
  BUY_TOKEN_ORDER,
  ETH_BASE_ADDRESS,
  BASE_TOKEN_BTC,
  SELL_TOKEN_ORDER,
  FUTURES_PRICE_DECIMALS,
  FUTURES_AMOUNT_DECIMALS,
  ORDER_TYPE_MARKET,
} from '@/util/constants';
import match from '@/util/match';
import futuresCalc from '@/util/futuresCalc';
import { getContracts } from '@/api/backend/futures/contracts';
import { getOneExternalTicker } from '@/api/backend';

export default {
  name: 'the-futures-order',

  data() {
    return {
      dropdownOrderTypeExpanded: false,
      invalid: true,
      anyDirty: false,
      dropdownMarginTypeExpanded: false,
      dropdownExpirationDateExpanded: false,
      expirationDate: '1july',
      loadMarginsState: ASYNC_STATES.INITIAL,
      // Margins
      margins: {},
      refreshMarginsIntervalId: null,
      refreshEthUsdPriceIntervalId: null,
      changePrice: BigNumber(0),
    };
  },

  computed: {
    activeContractAsset() {
      return this.activeContract.asset;
    },

    /**
     * Token balance
     * @returns {{privateBalance: BigNumber, contractBalance: BigNumber, inOrders: BigNumber, sum: BigNumber}}
     */
    balance() {
      return this.$store.getters['balance/getUserBalanceByToken'](this.baseToken);
    },

    isETH() {
      return this.baseToken === ETH_BASE_ADDRESS;
    },

    currencySlug() {
      if (this.baseToken === ETH_BASE_ADDRESS) {
        return 'ETH';
      } else if (this.baseToken === BASE_TOKEN_BTC) {
        return 'BTC';
      }

      return 'DAI';
    },

    currencyTicker() {
      if (this.baseToken === ETH_BASE_ADDRESS) {
        return 'ETHUSD';
      } else if (this.baseToken === BASE_TOKEN_BTC) {
        return 'BTCUSD';
      }

      return 'DAI';
    },

    decimalBalance() {
      switch (this.currencySlug) {
        case 'DAI': return 2;
        default: return 4;
      }
    },

    orderType: {
      get() {
        return this.$store.state.futuresActiveOrder.orderType;
      },

      set(orderType) {
        this.$store.commit('futuresActiveOrder/SET_CREATE_ORDER_TYPE', orderType);
        this.hideDropdownOrderType();
      },
    },

    buyOrSell: {
      get() {
        return this.$store.state.futuresActiveOrder.buyOrSell;
      },

      set(buyOrSell) {
        this.$store.commit('futuresActiveOrder/SET_CREATE_ORDER_BUY_OR_SELL', buyOrSell);
      },
    },

    side() {
      return this.buyOrSell === BUY_TOKEN_ORDER;
    },

    orderParams: {
      get() {
        return this.$store.state.futuresActiveOrder.orderParams;
      },

      set(orderParams) {
        this.$store.commit('futuresActiveOrder/SET_CREATE_ORDER_NUMBERS', orderParams);
      },
    },

    collateral() {
      if (!this.activeContract || !this.orderParams.amount || !this.orderParams.limitPrice || !this.ethUsdPrice) {
        return null;
      }

      const limitPrice = BigNumber(this.orderParams.limitPrice).shiftedBy(FUTURES_PRICE_DECIMALS);
      const amount = BigNumber(this.orderParams.amount).shiftedBy(FUTURES_AMOUNT_DECIMALS);
      const multiplier = futuresCalc.calculateMultiplier(this.activeContract.baseToken, this.ethUsdPrice);

      // Use same collateral function, because leverage has not 8 decimals
      const calcCollFn = match(this.activeFuturesSmartContract.version, [
        [v => v <= 7, futuresCalc.calculateCollateralV7],
      ], futuresCalc.calculateCollateralV7);

      return calcCollFn(
        amount,
        limitPrice,
        this.activeContract.margin,
        multiplier,
      );
    },

    orderValue() {
      if (!this.activeContract || !this.orderParams.amount || !this.orderParams.limitPrice || !this.ethUsdPrice) {
        return null;
      }

      const limitPrice = BigNumber(this.orderParams.limitPrice).shiftedBy(FUTURES_PRICE_DECIMALS);
      const amount = BigNumber(this.orderParams.amount).shiftedBy(FUTURES_AMOUNT_DECIMALS);

      return amount.times(limitPrice);
    },

    fundingRatePct() {
      return this.activeContract.fundingRate
        ? BigNumber(this.activeContract.fundingRate)
          .times(2057)
          .div('1e18')
          .times(100)
          .toFormat(3)
        : '0.033';
    },

    readyToSubmit() {
      const loaded = !this.$store.getters['futuresActiveOrder/orderMinEthValue'].isNaN();
      return (
        loaded &&
        this.lastActiveOrderStatus !== CREATE_ORDER_PENDING &&
        !this.invalid
      );
    },

    priceSource() {
      return this.activeContract.chartSymbol.includes('BINANCE') ? { url: 'https://www.binance.com', label: 'Binance' } : { url: 'https://www.bitstamp.net', label: 'Bitstamp' };
    },

    ...mapState({
      lastActiveOrderStatus: state => state.futuresActiveOrder.status,
      activeContract: state => state.futuresActiveContract.activeContract,
      activeFuturesSmartContract: state => state.contracts.activeFuturesContract,
    }),

    ...mapGetters({
      activeWallet: 'wallets/activeWallet',
      demoStep: 'ui/demoStep',
      isLogin: 'wallets/isLogin',
      activeContractName: 'futuresActiveContract/contractName',
      ethUsdPrice: 'futuresActiveOrder/ethUsdPrice',
      baseToken: 'balance/baseToken',
      positionToClose: 'futuresPositions/positionToClose',
      previousContractHash: 'futuresPositions/previousContractHash',
      theme: 'ui/getTheme',
    }),
  },

  watch: {
    activeContractName: 'resetForm',

    lastActiveOrderStatus(status) {
      if (status === CREATE_ORDER_SUCCESS) {
        this.resetForm();
      }
    },

    activeContract() {
      // Load margins when active futures contract is changed
      this.loadMargins();
    },

    activeContractAsset() {
      this.resetForm();
    },

    positionToClose() {
      if (this.positionToClose) {
        this.buyOrSell = this.positionToClose.side ? SELL_TOKEN_ORDER : BUY_TOKEN_ORDER;
        this.orderType = ORDER_TYPE_MARKET;

        setTimeout(() => {
          this.setPriceFromPositionToClose();
        }, 300);

        setTimeout(() => {
          this.setPriceFromPositionToClose();
        }, 600);
      } else {
        this.orderParams.amount = '';
      }
    },

    orderParams() {
      if (this.positionToClose && this.orderParams.limitPrice && this.orderParams.amount) {
        this.setChangePrice();
      }
    },

    side(side) {
      const contractsBySide = get(this.margins, [this.activeContract.margin, this.activeContract.expiresInSeconds]);
      if (contractsBySide === undefined) {
        return;
      }

      const availSides = Object.keys(contractsBySide);
      // If only one contract when stop here
      if (availSides.length === 1) {
        return;
      }

      this.changeActiveContractRoute(contractsBySide[side].futuresContractHash);
    },
  },

  created() {
    Object.assign(this, { iconArrowDowner, iconClose, iconInfo, iconInfinity });
    this.$store.dispatch('updateAppConfig'); // for orderMinEthValue
    this.startListeningToEthBalance();
    this.startListeningToTokenBalance();

    this.loadMargins();
    this.loadEthUsdPrice();

    this.refreshMarginsIntervalId = setInterval(this.loadMargins.bind(this), 60e3);
    this.refreshEthUsdPriceIntervalId = setInterval(this.loadEthUsdPrice.bind(this), 60e3);
  },

  beforeDestroy() {
    clearInterval(this.refreshMarginsIntervalId);
    clearInterval(this.refreshEthUsdPriceIntervalId);

    this.closePositionInfo();

    this.stopListeningToTokenBalance();
    this.stopListeningToEthBalance();
  },

  methods: {
    showDropdownOrderType() {
      this.dropdownOrderTypeExpanded = true;
    },

    hideDropdownOrderType() {
      this.dropdownOrderTypeExpanded = false;
    },


    setPriceFromPositionToClose() {
      this.orderParams.amount = this.positionToClose.size.shiftedBy(-FUTURES_AMOUNT_DECIMALS).toString();
      this.setChangePrice();
    },

    setChangePrice() {
      const price = this.positionToClose.price.shiftedBy(-FUTURES_PRICE_DECIMALS);
      const limitPrice = BigNumber(this.orderParams.limitPrice);
      const amount = BigNumber(this.orderParams.amount);

      this.changePrice = this.positionToClose.side ?
        limitPrice.minus(price).times(amount)
        :
        price.minus(limitPrice).times(amount);
    },

    setAmountPercent(percentage) {
      this.orderParams = { ...this.orderParams, amount: '1' };

      setTimeout(() => {
        const multiplier = futuresCalc.calculateMultiplier(this.activeContract.baseToken, this.ethUsdPrice);
        const amount = this.balance.availableBalance
          .times(percentage)
          .times('1e8')
          .idiv(multiplier)
          .div(this.orderParams.limitPrice)
          .times(this.activeContract.margin)
          .times(0.9999);
        this.orderParams = { ...this.orderParams, amount: amount.toFixed(3) };
      }, 0);
    },

    switchOptionClick() {
      this.closePositionInfo();
    },

    toggleDropdownMarginType() {
      this.dropdownMarginTypeExpanded = !this.dropdownMarginTypeExpanded;
    },

    hideDropdownMarginType() {
      this.dropdownMarginTypeExpanded = false;
    },

    toggleDropdownExpirationDate() {
      this.dropdownExpirationDateExpanded = !this.dropdownExpirationDateExpanded;
    },

    hideDropdownExpirationDate() {
      this.dropdownExpirationDateExpanded = false;
    },

    submitOrder() {
      if (this.isLogin) {
        setTimeout(() => {
          if (!this.positionToClose && this.collateral && this.collateral.gt(this.balance.availableBalance.shiftedBy(FUTURES_PRICE_DECIMALS))) {
            this.$store.commit('ui/ADD_NOTIFICATION', {
              status: 'error',
              headline: 'Order Creation Error',
              message: `Insufficient Funds. You need at least ${BigNumber(this.collateral).shiftedBy(-FUTURES_PRICE_DECIMALS).toFixed()} ${this.currencySlug} available balance`,
            });
          } else {
            if (this.demoStep && !this.positionToClose) {
              setTourStep({ walletAddress: this.activeWallet.address, step: 8 });
              this.$store.commit('ui/SET_DEMO_STEP', 9);
            }
            if (this.demoStep && this.positionToClose) {
              setTourStep({ walletAddress: this.activeWallet.address, step: 12 });
              this.$store.commit('ui/SET_DEMO_STEP', 13);
            }

            this.$store.dispatch('futuresActiveOrder/startOrderConfirmation', {
              buyOrSell: this.buyOrSell,
              orderType: this.orderType,
              orderParams: this.orderParams,
            });
          }
        }, 0);
      } else {
        this.$router.push({ name: 'signup' });
      }
    },

    handleSelectMargin(marginName) {
      // if (this.demoStep) {
      //   this.$store.commit('ui/SET_DEMO_STEP', 7);
      // }

      const contractsWithSameExpire = get(this.margins, [marginName, this.activeContract.expiresInSeconds]);
      if (contractsWithSameExpire) {
        if (contractsWithSameExpire[this.activeContract.side]) {
          this.changeActiveContractRoute(contractsWithSameExpire[this.activeContract.side].futuresContractHash);
        } else {
          this.changeActiveContractRoute(contractsWithSameExpire[Object.keys(contractsWithSameExpire)[0]].futuresContractHash);
        }
        return;
      }

      const contractsExpires = Object.keys(this.margins[marginName]);
      const contractsWithFirstExpires = this.margins[marginName][contractsExpires[0]];

      if (contractsWithFirstExpires[this.activeContract.side]) {
        this.changeActiveContractRoute(contractsWithFirstExpires[this.activeContract.side].futuresContractHash);
      } else {
        this.changeActiveContractRoute(contractsWithFirstExpires[Object.keys(contractsWithFirstExpires)[0]].futuresContractHash);
      }
    },

    async loadMargins() {
      let contracts = [];
      this.loadMarginsState = ASYNC_STATES.PENDING;
      try {
        contracts = await getContracts({
          // contractAddress: this.activeFuturesSmartContract.contractAddress,
          futuresAssetHash: this.activeContract.futuresAssetHash,
          isModel: true,
          isHidden: false,
          limit: 500,
        });
        this.loadMarginsState = ASYNC_STATES.SUCCESS;
      } catch (err) {
        this.loadMarginsState = ASYNC_STATES.ERROR;
      }

      const result = {};

      for (let i = 0; i < contracts.length; i += 1) {
        const contract = contracts[i];

        if (result[contract.margin] === undefined) {
          result[contract.margin] = {};
        }

        if (result[contract.margin][contract.expiresInSeconds] === undefined) {
          result[contract.margin][contract.expiresInSeconds] = {};
        }

        result[contract.margin][contract.expiresInSeconds][contract.side] = contract;
      }

      const contractAfterRefresh = get(result, [this.activeContract.margin, this.activeContract.expiresInSeconds, this.activeContract.side]);
      if (!contractAfterRefresh) {
        // If another contract with same margin and exp. date not found, then wait until new contract will be available
        return;
      }
      if (get(contractAfterRefresh, 'id') !== get(this.activeContract, 'id') && !this.positionToClose) {
        this.changeActiveContractRoute(contractAfterRefresh.futuresContractHash);
        return;
      }

      // Set margins
      this.margins = result;
    },

    async loadEthUsdPrice() {
      // If "currencyTicker" returns "DAI", do not call ticker endpoint
      if (this.currencyTicker === 'DAI') {
        this.$store.commit('futuresActiveOrder/UPDATE_ETHUSD_PRICE', BigNumber(1));
        return;
      }

      const ticker = await getOneExternalTicker(this.currencyTicker);
      this.$store.commit('futuresActiveOrder/UPDATE_ETHUSD_PRICE', BigNumber(ticker.lastPrice));
    },

    changeActiveContractRoute(contractHash) {
      // if (this.demoStep) {
      //   this.$store.commit('ui/SET_DEMO_STEP', 6);
      // }

      this.$router.push({ name: 'future', params: { contract: contractHash } });
    },

    formatExpires(seconds) {
      // 31536000 seconds = 1 year (aka perpetual)
      if (seconds === 0 || seconds === 31536000) {
        return '∞';
      }

      const days = Math.round(seconds / 86400);
      if (days >= 1) {
        // return `${days} day${days > 1 ? 's' : ''}`;
        return `${days}D`;
      }

      const hours = Math.round(seconds / 3600);

      // return `${hours} hour${hours > 1 ? 's' : ''}`;
      return `${hours}H`;
    },

    resetForm() {
      this.orderParams = {
        amount: '',
      };
      this.anyDirty = false;
    },

    closePositionInfo() {
      if (this.previousContractHash) {
        this.$router.push({ name: 'future', params: { contract: this.previousContractHash } });
      }

      this.$store.commit('futuresPositions/SET_POSITION_TO_CLOSE', null);
      this.$store.commit('futuresPositions/SET_PREVIOUS_CONTRACT_HASH', null);
    },
  },

  components: {
    EDemo,
    TheFuturesOrderFormLimit,
    TheFuturesOrderFormMarket,
    TheFuturesOrderFormStopLimit,
    EIcon,
    EBigNumber,
    ETooltip,
  },

  filters: { shortenNumber },

  mixins: [balanceMixin],
};
</script>

