      color: $color-dark-grey;
    }


    #{$this}__leverage-wrapper-item {
      color: #00DB9C;

      &::before {
        background: #282C38;
      }

      &::after {
        border: 1px solid #5C6062;
        background: #1C222D;
      }

      &:hover {
        &::after {
          border-color: #00DB9C;
        }
      }

      &:last-child {
        &.active {
          &::before {
            background: #00DB9C;
          }
        }
      }

      &.fill {
        &::before {
          background: #00DB9C;
        }

        &::after {
          border-color: #00DB9C;
        }
      }

      &.active {
        color: #fff;

        &::after {
          transform: scale(1.6);
          background: #fff;
          border-color: #fff;
        }
      }
    }

    #{$this}__select {
      background: $dark-background-lighter;
      border-color: $dark-background-lighter;
    }

    #{$this}__prices-item-value {
      color: #fff;
    }

    #{$this}__select-label {
      color: $color-dark-grey;
    }

    #{$this}__select-value {
      color: #0097FE;
    }

    #{$this}__select-value-hint {
      color: #fff;
    }

    #{$this}__select .e-icon svg {
      fill: #fff;
    }

    #{$this}__select-dropdown {
      border-color: #1a1a1a;
      border-top-color: $dark-background-lighter;
    }

    #{$this}__select-dropdown-item {
      background: $dark-background-lighter;
      color: #fff;
      border-color: rgba(#ffffff, .1);

      &:hover {
        background: rgba($dark-background,.92);
      }
    }

    .e-input-number-order__input {
      color: #fff;
    }

    .e-input-number-order.focused {
      .e-input-number-order__box {
        &::before {
          border: none;
        }
      }

      .e-input-number-order__unit {
        color: #fff;
      }
    }

    .e-input-number-order__box {
      border-color: transparent !important;
      background-color: transparent !important;
    }

    .e-input-number-order__unit {
      color: $color-dark-grey;
    }

    .e-input-number-order:hover:not(.e-input-number-order_non-editable):not(.focused) .e-input-number-order__unit {
      color: #fff;
    }

    #{$this}__submit-btn {
      &:disabled {
        /*background: #8D9098;*/
        /*color: #5C6062;*/
      }
    }

    #{$this}__price-source {
      background: #121722;
      border-color: #282C38;
    }

    #{$this}__price-source-label {
      color: $color-dark-grey;
    }

    #{$this}__price-source-value {
      color: #FFFFFF;
    }

    #{$this}__market-status {
      background-color: transparent;
      border-color: #282C38;
    }

    #{$this}__market-status-label {
      color: #D9D9D9;
    }

    #{$this}__info-icon {
      background: #343E4E;
    }

    #{$this}__percentage-item {
      color: #fff;

      &:hover {
        color: $color-green-dark;
      }
    }

    #{$this}__prices-item-label {
      color: $color-dark-grey;
    }
  }
}
</style>



