<template>
  <div class="e-order-book-placeholder">
    <div class="e-order-book-placeholder__left"></div>
    <div class="e-order-book-placeholder__mid"></div>
    <div class="e-order-book-placeholder__right"></div>
  </div>
</template>

<script>
export default {
  name: 'e-order-book-placeholder',
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-order-book-placeholder {
  background-color: rgba(#FFF, .6);
  display: flex;
  padding-left: 5px;
  padding-right: 8px;
  height: 28px;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 1px 3px rgba(black, .08);
  margin-bottom: 1px;
  transition: all .15s ease-in-out;

  @include r(1200) {
    height: 22px;
    padding-left: 5px;
  }

  @include h(750) {
    height: 22px;
  }

  @include rwh(2400, 1280) {
    height: 46px;
    padding-left: 5px;
    padding-right: 19px;
  }

  &__left {
    position: relative;
    height: 7px;
    flex-basis: 32%;
    max-width: 32%;

    @include r(1279) {
      flex-basis: 36%;
      max-width: 36%;
    }

    &:after {
      content: '';
      border-radius: 5px;
      position: absolute;
      top: 0;
      left: 0;
      height: 7px;
      width: 84px;
      background-color: #E3E3EE;
      transition: background-color .15s ease-in-out;
    }

    @include rwh(2400, 1280) {
      height: 10px;
    }
  }

  &__mid {
    position: relative;
    height: 7px;
    flex-basis: 27%;
    max-width: 27%;
    border-radius: 5px;
    margin-right: -15px;

    &:after {
      content: '';
      border-radius: 5px;
      position: absolute;
      top: 0;
      left: 3px;
      height: 7px;
      width: 48px;
      background-color: #E3E3EE;
      transition: background-color .15s ease-in-out;
    }

    @include rmin(1921) {
      left: 5px;
    }

    @include rwh(2400, 1280) {
      height: 10px;
      max-width: 13%;
      flex-basis: 13%
    }
  }

  &__right {
    height: 7px;
    flex-basis: 16%;
    max-width: 16%;
    position: relative;
    border-radius: 5px;

    &:after {
      content: '';
      border-radius: 5px;
      position: absolute;
      top: 0;
      right: 0;
      height: 7px;
      width: 44px;
      background-color: #E3E3EE;
      transition: background-color .15s ease-in-out;
    }

    @include r(1200) {
      flex-basis: 18%;
      max-width: 18%;
    }

    @include rwh(2400, 1280) {
      height: 10px;
    }
  }
}
</style>



