<template>
  <e-popup-info
    class="the-popup-error-order-balance"
    header="Create Order Error"
    @close="close"
  >
    <template slot="message">
      <div class="the-popup-error-order-balance__icon">
        <e-icon :src="iconEthLarge"/>
      </div>
      Insufficient funds
    </template>
    <template slot="details">
      <p class="the-popup-error-order-balance__p">Balance is too low to place order.</p>
      <p class="the-popup-error-order-balance__p">Please add funds to your Contract Balance.</p>
    </template>
  </e-popup-info>
</template>

<script>
import { mapMutations } from 'vuex';
import iconEthLarge from '@/icons/e-icon_eth-large.svg';
import { operationTypeAction } from '@/util/filters';

import EIcon from '@/components/EIcon';
import EModal from '@/components/EModal';
import EModalTrades from '@/components/EModalTrades';
import EBigNumber from '@/components/EBigNumber';
import EPopupInfo from '@/components/EPopupInfo';

import TransitionModal from '@/containers/transitions/TransitionModal';

export default {
  name: 'the-popup-error-order-balance',

  computed: {
    operationType: vm => operationTypeAction(vm.params.operationType),
  },

  created() {
    Object.assign(this, { iconEthLarge });
  },

  methods: {
    ...mapMutations({
      close: 'ui/HIDE_ERROR_ORDER_BALANCE',
    }),
  },

  components: { EPopupInfo, EBigNumber, TransitionModal, EModal, EModalTrades, EIcon },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-popup-error-order-balance {
  &__icon {
    width: 90px;
    height: 90px;
    margin: 0 auto 24px;
  }

  &__p {
    margin: 0;
    padding: 0;
  }

  &__min-balance {
    color: $color-blue;
    font-weight: 700;
  }
}
</style>



