<template>
  <div
    class="e-order-book"
    :class="{
      'e-order-book_increase': change === 'up',
      'e-order-book_decrease': change === 'down',
      'e-order-book_appear': isNew,
    }"
    @click="$emit('click')"
    data-test="orderBook"
  >
    <!--@mouseenter="$emit('mouseenter')"-->
    <!--@mouseleave="$emit('mouseleave')"-->

    <div class="e-order-book__row"
      :class="[
        type ? 'e-order-book__row_' + type : '',
        status ? 'e-order-book__row_status-' + status : '',
        /*isHighlighted ? 'is-active' : '',*/
        isMy ? 'is-my' : '',
      ]"
    >
      <div class="e-order-book__bg"></div>
      <div class="e-order-book__price">
        <e-big-number :value="price" :decimals="FUTURES_PRICE_DECIMALS" :decimal-places="decimals"/>
        <!--<span class="e-order-book__price-eth-icon"><e-icon :src="iconEarned"/></span>-->
      </div>
      <div class="e-order-book__token e-order-book__bignumber">
        <e-big-number :value="size" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="amountDec"/>
      </div>
      <div class="e-order-book__eth e-order-book__bignumber">
        <e-big-number :value="volumeSum" :decimals="FUTURES_AMOUNT_DECIMALS + FUTURES_PRICE_DECIMALS" :decimal-places="0"/>
      </div>

      <div class="e-order-book__volume-line" :style="{ 'width': volumeProportion }"></div>
    </div>
  </div>
</template>

<script>
import { APP_ENV } from '@/config';
import { BigNumber } from 'bignumber.js';

import iconLoss from '@/icons/e-icon_loss.svg';
import iconProfit from '@/icons/e-icon_profit.svg';
import iconEarned from '@/icons/e-icon_earned.svg';

import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber/';

export default {
  name: 'e-futures-order-book-item',

  data() {
    return {
      iconLoss,
      iconProfit,
      iconEarned,

      // Таймаут для удаления класса
      timerId: null,

      change: null,
      isNew: false,
    };
  },

  props: {
    type: {
      type: String,
      default: '',
    },

    isHighlighted: {
      type: Boolean,
      default: false,
    },

    price: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    size: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    volumeSum: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    max: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    proportion: {
      type: Number,
      default: () => 0,
    },

    isMy: {
      type: Boolean,
      default: false,
    },

    decimals: {
      type: Number,
      default: () => 2,
    },

    amountDec: {
      type: Number,
      default: () => 0,
    },

    status: {
      type: String,
      default: 'approved',
    },
  },

  computed: {
    volumeProportion() {
      // return `${(this.proportion / 2) * 100}%`;
      return `${this.volumeSum.div(this.max).toString() * 100 * (window.innerHeight < 1000 && APP_ENV === 'test' ? 3 : 1)}%`;
    },
  },

  watch: {
    size(nextAmount, prevAmount) {
      if (nextAmount.gt(prevAmount)) {
        this.change = 'up';
        this.timerId = setTimeout(() => {
          this.change = null;
        }, 100);
      } else if (nextAmount.lt(prevAmount)) {
        this.change = 'down';
        this.timerId = setTimeout(() => {
          this.change = null;
        }, 100);
      }
    },
  },

  mounted() {
    this.isNew = true;
    setTimeout(() => { this.isNew = false; }, 120);
    // if (this.type === 'sellToken') {
    //   console.log(`ASK Volume: ${this.volumeSum.shiftedBy(-18).toString()}`, `Max: ${this.max.shiftedBy(-18).toString()}`);
    // } else {
    //   console.log(`BID Volume: ${this.volumeSum.shiftedBy(-18).toString()}`, `Max: ${this.max.shiftedBy(-18).toString()}`);
    // }
  },

  beforeDestroy() {
    clearTimeout(this.timerId);
    this.timerId = null;
  },

  components: {
    EIcon,
    EBigNumber,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-order-book {
  $this: &;
  user-select: none;
  margin-bottom: 1px;
  white-space: nowrap;
  position: relative;

  &__bg {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 2px;
    /*transform: scaleX(0);*/
  }

  &__token {
    transform: translate(4px, 0);
    padding-right: 4px;
  }

  &_increase &__token {
    background-color: rgba($color-green-bright, 0.25);
    animation-name: eorderbooksizeanimation;
    animation-duration: .05s;
  }

  &_decrease &__token {
    background-color: rgba($color-red, .25);
    animation-name: eorderbooksizeanimation;
    animation-duration: .05s;
  }

  /*&_appear &__bg {
    top: 0;
    left: 0;
    background-color: rgba($color-yellow-dark, .2);
    transition: background-color .07s ease-in-out;
  }*/

  /*&_increase &__bg {
    top: 0;
    left: 28px;
    background-color: $color-green;
    animation-name: eorderbookanimation;
    animation-duration: 1s;
    transform-origin: center left;
  }

  &_decrease &__bg {
    top: 0;
    right: 0;
    background-color: $color-red;
    animation-name: eorderbookanimation;
    animation-duration: 1s;
    transform-origin: center right;
  }*/

  &__row {
    display: flex;
    justify-content: space-between;
    background-color: #fff;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.08);
    border-radius: 1px;
    padding-top: 2px;
    transition: $transition-time;
    cursor: pointer;
    min-height: 28px;
    box-sizing: border-box;
    padding-left: 12px;
    position: relative;

    /* pseudo element to catch empty space clicks */
    &:after {
      content: '';
      position: absolute;
      display: block;
      background-color: transparent;
      bottom: -1px;
      left: 0;
      width: 100%;
      height: 1px;
    }

    &:hover {
      background-color: #F3F6FF;
    }

    &.is-active {
      background-color: #F3F6FF;
    }

    &.is-my {
      &::before {
        content: '';
        display: block;
        width: 0;
        height: 0;
        border-top: 4px solid transparent;
        border-left: 4px solid #da7632;
        border-bottom: 4px solid transparent;
        position: absolute;
        background-color: transparent;
        left: 3px;
        top: calc(50% - 3px);
      }
    }

    /*@include rwh(2400, 1280) {
      padding: 10px 0;
    }*/

    @include r(1200) {
      min-height: 22px;
      padding-top: 0;
    }

    @include h(750) {
      min-height: 22px;
      padding-top: 0;
    }
  }

  &__volume-line {
    max-width: calc(100% - 28px);
    height: 100%;
    opacity: .23;
    background: #000;
    position: absolute;
    z-index: 0;
    right: 0;
    bottom: 0;
  }

  &__row_status-pending {
    opacity: .3;
    pointer-events: none;
  }

  &__row_last-order {
    background-color: transparent;
    box-shadow: none;
    min-height: 48px;
    display: flex;
    align-items: center;
    margin-right: 9px;
    margin-left: 4px;
    cursor: default;

    &:hover {
      background-color: transparent;
    }
  }

  &__row_buyToken {
    #{$this}__price {
      color: $color-green-bright;
    }

    #{$this}__price-eth-icon {
      .e-icon svg {
        fill: #0D105B;
      }
    }

    #{$this}__volume-line {
      background: #1FCE43;
    }
  }

  &__row_sellToken {
    #{$this}__price {
      color: $color-red;
    }

    #{$this}__price-eth-icon {
      .e-icon svg {
        fill: #0D105B;
      }
    }

    #{$this}__volume-line {
      background: #DA4830;
    }
  }

  &__market {
    flex-basis: 62px;
    padding-left: 22px;
    box-sizing: border-box;

    @include rwh(2400, 1280) {
      padding-left: 42px;
    }
  }

  &__market-name {
    display: inline-block;
    font-size: 11px;
    text-transform: uppercase;
    font-weight: 800;
    color: #0D105B;
    border: 1px solid #d9d9e2;
    border-radius: 4px;
    padding: 0 2px;
    line-height: 15px;
  }

  &__price {
    font-size: 12px;
    font-weight: 400;
    letter-spacing: .8px;
    flex-basis: 30%;
    max-width: 30%;
    padding-top: 3px;
    padding-bottom: 1px;
    transition: .15s;
    position: relative;
    z-index: 2;

    .e-big-number {
      .e-big-number__fraction {
        opacity: 0.6;
      }
    }
  }

  &__price-icon {
    width: 12px;
    height: 11px;
    display: inline-block;
    position: absolute;
    top: 7px;
    left: 6px;

    @include rwh(2400, 1280) {
      width: 18px;
      height: 16px;
      left: 19px;
      top: 2px;
    }

    @include r(1200) {
      top: 6px;
    }

    @include h(750) {
      top: 6px;
    }
  }

  &__price-eth-icon {
    height: 10px;
    width: 10px;
    display: inline-block;
    position: relative;
    left: -3px;
    top: 1px;
  }

  &__token {
    color: $color-dark-blue;
    font-size: 11px;
    font-weight: 400;
    padding-top: 4px;
    padding-bottom: 1px;
    text-align: right;
    flex-basis: 27%;
    max-width: 27%;
    transition: $transition-time;
    position: relative;
    z-index: 2;

    @include r(1200) {
      flex-basis: 27%;
      max-width: 27%;
      padding-top: 4px;
    }

    @include h(750) {
      flex-basis: 27%;
      max-width: 27%;
      padding-top: 4px;
    }

    @include rwh(2400, 1280) {
      font-size: 14px;
      padding-top: 0;
    }
  }

  &__eth {
    color: $color-dark-blue;
    font-size: 11px;
    padding-top: 4px;
    font-weight: 400;
    letter-spacing: .93px;
    flex: 1;
    text-align: right;
    transition: $transition-time;
    position: relative;
    z-index: 2;
    padding-right: 11px;

    @include r(1200) {
      letter-spacing: .73px;
      padding-right: 11px;
      padding-top: 4px;
    }

    @include h(750) {
      letter-spacing: .73px;
      padding-right: 11px;
      padding-top: 4px;
    }

    @include rwh(2400, 1280) {
      font-size: 14px;
      padding-top: 0;
      padding-right: 11px;
    }
  }

  &__eth-icon {
    display: inline-block;
    height: 10px;
    width: 10px;
    position: absolute;
    top: 6px;
    right: 7px;

    .e-icon {
      fill: $color-dark-blue;
    }

    @include rwh(2400, 1280) {
      width: 16px;
      height: 16px;
      top: 0px;
      right: 13px;
    }
  }

  &__bignumber {
    .e-big-number {
      &__leading-zeros,
      &__trailing-zeros {
        opacity: 0.5;
      }

      &__fraction {
        letter-spacing: .73px;
        margin-left: 1px;
        @include rwh(1921, 1280) {
          letter-spacing: .93px;
        }
      }

      &__separator {
        opacity: .5;
        margin-left: 1px;
      }

      &__leading-zeros + .e-big-number__trailing-zeros {
        opacity: 1;
      }

      &__main + .e-big-number__trailing-zeros {
        opacity: 1;
      }
    }
  }
}

@keyframes eorderbooksizeanimation {
    0%   {
      opacity: 0.5;
    }
    75% {
      opacity: 0.5;
    }
}

@keyframes eorderbookanimation {
    0%   {
      transform: scaleX(0);
      opacity: 0.5;
    }
    75% {
      transform: scaleX(1);
      opacity: 0.5;
    }
    100%  {
      // opacity: 0;
      transform: scaleX(1);
    }
}
</style>




