<template>
  <div
    class="e-receipt-item"
    :class="[
      bold ? 'e-receipt-item_font-bold' : '',
      buy ? 'e-receipt-item_buy'
          : sell ? 'e-receipt-item_sell' : 'e-receipt-item_num',
    ]"
  >
    <div class="e-receipt-item__title">
      <slot name="title"></slot>
    </div>
      <div class="e-receipt-item__value">
        <div v-if="!noIcon">
          <span v-if="buy && !icon">+</span>
          <span v-if="sell && !icon">-</span>
          <div
            v-if="icon"
            class="e-receipt-item__icon"
          >
            <e-icon :src="sell ? iconLoss : iconProfit" />
          </div>
          <slot
            v-else
            name="value"
          />
        </div>
        <div class="e-receipt-item__token-name" :class="{'e-receipt-item__token-name-operation' : noIcon, 'buy': buy, 'sell': sell  }" v-if="tokenName.length !== 0">
          {{tokenName}}
        </div>
      </div>
  </div>
</template>

<script>
import iconLoss from '@/icons/e-icon_loss.svg';
import iconProfit from '@/icons/e-icon_profit.svg';

import EIcon from '@/components/EIcon';

export default {
  name: 'e-receipt-item',

  created() {
    Object.assign(this, {
      iconLoss,
      iconProfit,
    });
  },

  props: {
    buy: {
      type: Boolean,
      default: false,
    },
    sell: {
      type: Boolean,
      default: false,
    },
    bold: {
      type: Boolean,
      default: false,
    },
    tokenName: {
      type: String,
      default: '',
    },
    icon: {
      type: Boolean,
      default: false,
    },
    noIcon: {
      type: Boolean,
      default: false,
    },
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-receipt-item {
  $this: &;

  display: flex;
  flex-direction: row;
  padding: 9px 0;

  &__value {
    position: relative;
    text-align: right;
    max-width: 50%;
    width: 50%;
  }

  &__title {
    max-width: 50%;
    width: 50%;
    color: $color-grey;
    font-size: 13px;
    font-weight: 500;
    padding-right: 10px;
    text-align: left;

    @include r(640) {
      font-size: 11px;
    }
  }

  &__value {
    font-size: 13px;
    font-weight: 500;
    align-items: center;
    display: flex;
    padding-left: 10px;
    justify-content: flex-end;

    @include r(640) {
      font-size: 11px;
    }
  }

  &_font-bold {
    font-weight: 800;
    font-size: 13px;

    #{$this}__title {
      color: #46484C;
      font-weight: 800;
    }
  }

  &_buy {
    color: $color-green-bright;

    .e-big-number {
      color: $color-green-bright;
    }

    #{$this}__token-name {
      color: $color-green-bright;
    }
  }

  &_sell {
    color: $color-red;

    .e-big-number {
      color: $color-red;
    }
  }

  &_num {
    .e-big-number {
      color: $color-dark-blue;
      font-weight: 500;
    }

    #{$this}__token-name {
      color: $color-dark-blue;
    }
  }

  &__token-name {
    margin-left: 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__icon {
    height: 11px;
    position: relative;
    margin-right: 8px;
    min-width: 12px;
    width: 12px;
  }

  &__token-name-operation {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 34px;
    height: 14px;
    font-size: 8px;
    text-transform: uppercase;
    border-radius: 3px;

    &.buy {
      color: #1FCE43;
      background: rgba(31,206,67,0.20);
    }

    &.sell {
      color: $color-red;
      background: rgba($color-red,0.20);
    }
  }
}
</style>



