import { BigNumber } from 'bignumber.js';
import get from 'lodash/get';
import backend from '@/api/backend/endpoint';

const history = {};

export default {
  history,

  getBars: async (resolution, from, to, symbol) => {
    const getCandles = filters => backend.http.get('/api/futures/asset-candles/history', { params: filters });

    const { data } = await getCandles({
      symbol: symbol.name,
      resolution,
      from,
      to,
    });

    const result = [];

    for (let i = 0; i < get(data, 'c', []).length; i += 1) {
      result.push({
        time: data.t[i] * 1000,
        low: BigNumber(data.l[i]).toFixed(),
        high: BigNumber(data.h[i]).toFixed(),
        open: BigNumber(data.o[i]).toFixed(),
        close: BigNumber(data.c[i]).toFixed(),
        volume: +BigNumber(data.v[i]).toFixed(),
      });
    }

    return result;
  },
};



