<template>
  <div
    class="the-assets"
    :class="{
      'the-assets__light': theme === 'light',
      'the-assets__dark': theme === 'dark'
     }"
  >
    <div v-if="demoStep === 4" class="the-assets__demo">
      <e-demo title="Assets">
        This is the list of all available assets with information about the last price, 24 hour volume and 24 hour price change.
      </e-demo>
    </div>

    <div class="the-assets__headline">
      <div class="the-assets__headline-item" :class="{ 'is-active': assetType === 'crypto' }" @click="setAssetType('crypto')">Crypto</div>
      <div v-if="APP_ENV === 'test'" class="the-assets__headline-item" :class="{ 'is-active': assetType === 'stock' }" @click="setAssetType('stock')">Stocks</div>
    </div>
    <the-assets-crypto :type="assetType" />
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

import TheAssetsCrypto from '@/containers/blocks/TheAssetsCrypto';
import EDemo from '@/components/EDemo';

/**
 * @property requestState
 */
export default {
  name: 'the-assets',

  computed: {
    ...mapGetters({
      assetType: 'ui/assetType',
      demoStep: 'ui/demoStep',
      theme: 'ui/getTheme',
    }),
  },

  methods: {
    setAssetType(assetType) {
      this.$store.commit('ui/SET_ASSET_TYPE', assetType);
    },
  },

  components: { TheAssetsCrypto, EDemo },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-assets {
  $this: &;

  position: relative;
  height: calc(100% - 62px);
  border-radius: 3px;
  border: 1px solid #DBDCDD;
  box-sizing: border-box;
  background: #fff;

  @include r(1279) {
    display: none;
  }

  &__demo {
    position: absolute;
    z-index: 20;
    top: 0;
    right: 0;
    transform: translate(calc(100% + 10px), 0);
  }

  &__headline {
    display: flex;
    align-items: center;
    padding: 8px;
    box-sizing: border-box;
    border-bottom: 1px solid #DBDCDD;
  }

  &__headline-item {
    color: $color-semi-blue;
    font-size: 10px;
    font-weight: 700;
    text-transform: uppercase;
    transition: color .15s ease-in-out;
    cursor: pointer;
    position: relative;
    padding-right: 10px;
    margin-right: 8px;

    &:after {
      content: '/';
      color: $color-semi-blue;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translate(0, -50%);
    }

    &:last-child {
      &:after {
        display: none;
      }
    }

    /*&:hover {
      color: #0123E7;
    }*/

    &.is-active {
      color: #0123e7;
    }
  }

  &__dark {
    background-color: $dark-background;
    border-color: #282C38;

    #{$this}__headline {
      border-color: #1e202b;
    }

    #{$this}__headline-item {
      color: $color-dark-grey;

      &.is-active {
        color: $color-green-dark;
      }

      &:after {
        color: $color-dark-grey;
      }
    }
  }
}
</style>



