<!--
  TODO: Нужно найти способ отслеживания окончания такогопреехода.
  Все события ниже эмиться после анимации каждого из элементов списка,
  по этому пересчет слайдера происходит много раз, желательно эту функцию вызывать, когда уже анимация закончит работу целиком.
-->
<template>
  <transition-group
    name="transition-list"
    tag="div"
    class="transition-list-container"
    v-on="$listeners"
  >
    <slot></slot>
  </transition-group>
</template>

<script>
export default {
  name: 'transition-list',
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.transition-list-container {
  position: relative;
  padding-bottom: 24px;

  > div {
    transition: 0.5s all;
    will-change: opacity, transform;
  }
}

.transition-list-enter, .transition-list-leave-to {
  opacity: 0;
  transform: translateZ(0) !important;
}

.transition-list-leave-active {
  position: absolute !important;
  width: 100%;
  box-sizing: border-box;
}
</style>



