import { BigNumber } from 'bignumber.js';

const GROUP_SIZE = 3;

/**
 * Formats number in format:
 * 12000 => 12k
 * 12000000 => 12kk
 *
 * @param {string} num
 * @returns {string}
 */
export default (arg) => {
  const num = BigNumber(arg);
  if (!num.isNaN()) {
    const intPart = num.dp(0);
    const intDigitCount = intPart.sd(true);
    if (intDigitCount > 4) {
      // at least 1 number must precede first k letter
      const kCount = ((intDigitCount - 1) - ((intDigitCount - 1) % GROUP_SIZE)) / GROUP_SIZE;
      const firstPart = intPart.shiftedBy(-kCount * GROUP_SIZE).dp(0, BigNumber.ROUND_UP).toFixed();
      return `${firstPart}${'k'.repeat(kCount)}`;
    }
    return num.toFixed(4);
  }
  return '';
};



