<template>
  <transition-modal>
    <e-modal
      class="the-min-order-popup"
      :level="1"
      :secondaryDesign="true"
      :autoHeight="true"
      :btnClose="false"
      :darkBackground="true"
      @close="close"
    >
      <template slot="inner">
        <e-modal-trades
          headerTitle="Order Error"
          btnText="Ok"
          @close="close"

        >
          <template slot="body">
            <div class="the-min-order-popup__container">
              <div class="the-min-order-popup__icon">
                <e-icon :src="iconEthLarge"/>
              </div>
              <div class="the-min-order-popup__main">
                <div class="the-min-order-popup__heading">
                  Minimum order value <br>is
                  <span class="the-min-order-popup__min-value">
                    <e-big-number
                      :value="orderMinEthValue"
                      :decimals="ETH_DECIMALS"
                      :decimal-places="null"
                    />
                    ETH
                  </span>
                </div>
                <div class="the-min-order-popup__text">
                  Please increase your order value.
                </div>
              </div>
            </div>
          </template>
        </e-modal-trades>
      </template>
    </e-modal>
  </transition-modal>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';

import EIcon from '@/components/EIcon';
import EModal from '@/components/EModal';
import EModalTrades from '@/components/EModalTrades';
import EBigNumber from '@/components/EBigNumber';

import TransitionModal from '@/containers/transitions/TransitionModal';

import iconEthLarge from '@/icons/e-icon_eth-large.svg';

export default {
  name: 'the-min-order-popup',

  async created() {
    Object.assign(this, { iconEthLarge });
  },

  computed: {
    ...mapGetters({
      orderMinEthValue: 'activeOrder/orderMinEthValue',
    }),
  },

  methods: {
    ...mapMutations({ close: 'activeOrder/HIDE_MIN_ORDER_ERROR' }),
  },

  components: { EBigNumber, TransitionModal, EModal, EModalTrades, EIcon },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-min-order-popup {
  .e-modal-trades__header {
    font-weight: bold;
  }

  &__icon {
    width: 90px;
    height: 90px;
    margin: 0 auto 24px;
  }

  &__min-value {
    color: $color-blue;
    font-weight: 700;
  }
}
</style>



