<template>
  <div
    class="e-futures-order"
    :class="[
      orderType === BUY_TOKEN_ORDER ? 'e-futures-order_buy' : 'e-futures-order_sell',
      {
        'e-futures-order_canceled': isCanceled,
        'e-futures-order_filled': isFilled,
      },
      `e-futures-order__${theme}`
    ]"
  >
    <!-- TODO: Ruslan Нужно сделать отбражение элмента внизу по клику на e-futures-order__attention -->
    <div class="e-futures-order__table-col" @click="handleSelect()">
      <span class="e-futures-order__table-col-asset">
        {{futuresAssetLabel}}
      </span>
    </div>
    <div class="e-futures-order__table-col">
      <div
        class="e-futures-order__token-order"
        :class="[ orderType === BUY_TOKEN_ORDER ? 'e-futures-order__token-order-buy' : 'e-futures-order__token-order-sell']"
      >
        {{ orderType === BUY_TOKEN_ORDER ? 'Buy' : 'Sell'}}
      </div>
    </div>
    <div class="e-futures-order__table-col">
      {{createdDateString}}
    </div>
    <div class="e-futures-order__table-col e-futures-order__table-col-amount">
      <e-big-number :value="amount" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="3"/>

      <!--<span class="e-futures-order__short-name">-->
        <!--{{tokenShortName.replace('USD', '')}}-->
      <!--</span>-->
    </div>
    <div class="e-futures-order__table-col e-futures-order__table-col-price">
      <e-big-number :value="unitPrice" :decimals="FUTURES_PRICE_DECIMALS" :decimal-places="futuresAssetDecimals"/>
    </div>

    <div v-if="isActiveMode" class="e-futures-order__table-col">
      <e-big-number v-if="stopPrice" :value="stopPrice" :decimals="FUTURES_PRICE_DECIMALS" :decimal-places="futuresAssetDecimals" />
      <span v-else>N/A</span>
    </div>

    <div class="e-futures-order__table-col">
      <span v-if="collateral !== null" class="e-futures-order__collateral">
        <span class="e-futures-order__prices-item-num">
          <e-big-number :value="collateral" :sign="false" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="4"/>
        </span>
        {{ futuresAssetBaseTokenSymbol }}
      </span>
      <span v-else>--</span>
    </div>

    <div class="e-futures-order__table-col">
      <e-big-number :value="amountFilled" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="3"/>
      <!--<span class="e-futures-order__short-name">{{tokenShortName.replace('USD', '')}}</span>-->
      <!--<div
        v-if="!allowedDelete"
        class="e-futures-order__condition"
      >
        <div class="e-futures-order__condition-hint">
          <template v-if="isCanceled">
            canceled order @ {{canceledDateString}}
          </template>
          <template v-else>
            finished order @ {{filledDateString}}
          </template>
        </div>
      </div>-->
    </div>
    <div v-if="isActiveMode" class="e-futures-order__table-col">
      <div
        v-if="allowedDelete"
        @click="handleCancel({ orderHash, orderType })"
        class="e-futures-order__delete-button"
        :class="[isTooSmall && isActive ? 'e-futures-order__delete-button_small-order' : '']"
      >
        cancel
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapState } from 'vuex';
import { BigNumber } from 'bignumber.js';
import { dateToString } from '@/util/dates';
import { ETH_DECIMALS, FUTURES_PRICE_DECIMALS, FUTURES_AMOUNT_DECIMALS, ETH_BASE_ADDRESS } from '@/util/constants';
import match from '@/util/match';
import futuresCalc from '@/util/futuresCalc';

// SVG icons
import iconEarned from '@/icons/e-icon_earned.svg';
import iconFlag from '@/icons/e-icon_flag.svg';
import iconLoss from '@/icons/e-icon_loss.svg';
import iconProfit from '@/icons/e-icon_profit.svg';
import iconTrash from '@/icons/e-icon_trash.svg';

// Components
import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber';
import ETokenIcon from '@/components/ETokenIcon';
import EOrderAttentionHint from '@/components/EOrderAttentionHint';

export default {
  name: 'the-futures-order-row',

  props: {
    // Тип орадера (покупка или продажа)
    orderType: {
      type: String,
    },

    ethUsdPrice: {
      type: BigNumber,
    },

    futuresAssetLabel: {
      type: String,
      required: true,
    },

    futuresAssetDecimals: {
      type: Number,
      required: true,
    },

    futuresAssetBaseTokenAddress: {
      type: String,
      required: true,
    },

    futuresAssetBaseTokenSymbol: {
      type: String,
      required: true,
    },

    orderHash: {
      type: String,
      required: true,
    },

    // Объем ордера в токенах
    amount: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // Объм, на который заполнен ордер (в токенах)
    amountFilled: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // Подтвержденный объем, на который заполнен ордер (в токенах)
    amountApproved: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // Объем, на который заполнен ордер ожидающий подтверждения (в токенах)
    amountPanding: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // Дата создания ордера
    createdAt: {
      type: Date,
      default: null,
    },

    // Дата отмены ордера
    canceledAt: {
      type: Date,
      default: null,
    },

    // Дата когда ордер был полностью заполнен
    fulfilledAt: {
      type: Date,
      default: null,
    },

    isActive: {
      type: Boolean,
      default: false,
    },

    isStopped: {
      type: Boolean,
      default: false,
    },

    // Является ли ордер отмененным
    isCanceled: {
      type: Boolean,
      default: false,
    },

    // Цена ордера
    unitPrice: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // Стоп цена
    stopPrice: {
      type: BigNumber,
      default: () => null,
    },

    // Количество знаков после запятой у токена ордера
    decimals: {
      type: Number,
      default: 18,
    },

    // Короткое название токена ордера
    tokenShortName: {
      type: String,
      default: '',
    },

    // URL изображения токена
    tokenImage: {
      type: String,
      default: '',
    },

    futuresContractHash: {
      type: String,
      default: '',
    },

    // Можно ли удалять данный ордер
    allowedDelete: {
      type: Boolean,
      default: true,
    },

    // Можно ли удалять данный ордер
    isActiveMode: {
      type: Boolean,
      default: true,
    },

    leverage: {
      type: BigNumber,
    },
  },

  computed: {
    ...mapGetters({
      baseToken: 'balance/baseToken',
      gasPrice: 'blockchain/backendGasPrice',
      takerGasLimit: 'blockchain/tradeTakerGasLimit',
      theme: 'ui/getTheme',
    }),

    ...mapState({
      activeFuturesSmartContract: state => state.contracts.activeFuturesContract,
    }),

    isETH() {
      return this.baseToken === ETH_BASE_ADDRESS;
    },

    decimalBalance() {
      return this.isETH ? 6 : 2;
    },

    collateral() {
      if (!this.amount || !this.unitPrice || !this.ethUsdPrice) {
        return null;
      }

      const limitPrice = BigNumber(this.unitPrice).shiftedBy(-FUTURES_PRICE_DECIMALS);
      const amount = BigNumber(this.amount).shiftedBy(FUTURES_AMOUNT_DECIMALS);
      const multiplier = futuresCalc.calculateMultiplier(this.futuresAssetBaseTokenAddress, this.ethUsdPrice);

      // Use same collateral function, because leverage has not 8 decimals
      const calcCollFn = match(this.activeFuturesSmartContract.version, [
        [v => v <= 7, futuresCalc.calculateCollateralV7],
      ], futuresCalc.calculateCollateralV7);

      return calcCollFn(
        amount,
        limitPrice,
        this.leverage,
        multiplier,
      );
    },

    // Является ли ордер заполненным (не отменен и уже не активен)
    isFilled() {
      return !this.isCanceled && !this.isActive;
    },

    amountLeft() {
      let res = BigNumber(0);

      if (this.amount && this.amountFilled) {
        res = this.amount.minus(this.amountFilled);
      }

      return res;
    },

    barApproved() {
      return this.amountApproved.div(this.amount.div(100)).toFixed(0);
    },

    barPanding() {
      return this.amountPanding.div(this.amount.div(100)).toFixed(0);
    },

    createdDateString() {
      return this.createdAt ? dateToString(this.createdAt) : null;
    },

    canceledDateString() {
      return this.canceledAt ? dateToString(this.canceledAt, '') : null;
    },

    filledDateString() {
      return this.fulfilledAt ? dateToString(this.fulfilledAt, '') : null;
    },

    /**
     * Размер ордера в эфире (объем в токене * цену)
     */
    ethAmount() {
      const { amount, unitPrice, decimals } = this;
      return amount.times(unitPrice).shiftedBy(ETH_DECIMALS - decimals);
    },

    // Ордер является слишком маленьким, что бы торговаться
    // true if amountLeft < 10 * gasFee
    isTooSmall() {
      const { decimals, amountLeft, unitPrice, gasPrice, takerGasLimit } = this;

      if (!gasPrice) {
        return true;
      }

      const avaliableAmountEth = amountLeft.times(unitPrice).shiftedBy(ETH_DECIMALS - decimals);
      const gasFeeAmountEth = gasPrice.times(takerGasLimit);
      return avaliableAmountEth.lt(gasFeeAmountEth.times(10));
    },
  },

  created() {
    Object.assign(this, {
      iconEarned,
      iconFlag,
      iconLoss,
      iconProfit,
      iconTrash,
    });
  },

  methods: {
    ...mapMutations({
      handleCancel: 'futuresOrders/CONFIRMATION_CANCEL_ORDER',
    }),

    handleSelect() {
      this.$store.commit('futuresPositions/SET_POSITION_TO_CLOSE', null);
      this.$store.commit('futuresPositions/SET_PREVIOUS_CONTRACT_HASH', null);
      this.$router.push({ name: 'future', params: { contract: this.futuresContractHash } });
    },
  },

  components: {
    EBigNumber,
    EIcon,
    EOrderAttentionHint,
    ETokenIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-futures-order {
  $this: &;

  height: 34px;
  font-size: 10px;
  font-weight: 500;
  color: #09168a;
  background: #fff;
  display: flex;
  align-items: center;
  position: relative;
  transition: background-color .15s ease-in-out;

  &:hover {
    z-index: 1;
  }

  &:last-child {
    margin-bottom: 0;
  }

  &__table-col {
    &:nth-child(1) {
      cursor: pointer;

      &:hover {
        background: #fafafd;
      }
    }

    .e-big-number {
      position: relative;
    }
  }

  &__token-order {
    width: 36px;
    height: 14px;
    box-sizing: border-box;
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    text-transform: uppercase;
    font-size: 9px;
    letter-spacing: 0;
    text-align: center;
  }

  &__token-order-buy {
    color: #1FCE43;
    background: rgba(31,206,67,0.10);
  }

  &__token-order-sell {
    color: #DC4727;
    background: rgba(220,71,39,0.10);
  }

  &__short-name {
    font-weight: 500;
    text-transform: uppercase;
    color: #09168a;
    text-align: right;
    padding-left: 2px;
  }

  &__icon-earned {
    width: 9px;
    height: 9px;
    margin-left: 1px;

    svg {
      fill: #09168a;
    }
  }

  &__delete-button {
    width: 45px;
    height: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #DC4727;
    color: #fff;
    font-size: 8px;
    text-transform: uppercase;
    border-radius: 3px;
    cursor: pointer;
    transition: background-color .15s ease-in-out;

    &:hover {
      background: rgba(#DC4727, .8);
    }
  }

  .e-big-number {
    font-size: 10px;
    font-weight: 500;
    color: #09168a;

    &__int {
      .e-big-number__main + .e-big-number__trailing-zeros {
        opacity: 1;
      }
    }
  }

  &_buy {
    #{$this}__amount-tokens {
      color: $color-green;
    }

    #{$this}__rail-fill {
      background-color: $color-green;
    }

    #{$this}__rail-fill_filled {
      opacity: 0.4;
    }

    #{$this}__hint {
      color: $color-green;
    }
  }

  &_sell {
    #{$this}__amount-tokens {
      color: $color-red;
    }

    #{$this}__rail-fill {
      background-color: $color-red;
    }

    #{$this}__rail-fill_filled {
      opacity: 0.4;
    }

    #{$this}__hint {
      color: $color-red;
    }
  }

  // Отмененные ордера
  &_canceled {
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
      background-image: repeating-linear-gradient(60deg, #fff, #fff 6px, #fef8f8 0, #fef8f8 8px)
    }

    #{$this}__condition {
      background-color: rgba(#DA4830, .38);
      border: 2px solid #DA4830;
    }
  }

  &_filled {
    #{$this}__condition {
      background-color: #baf5c6;
      border: 2px solid #1FCE43;
    }
  }

  &__condition {
    border-radius: 50%;
    height: 8px;
    margin-left: 7px;
    margin-top: 0;
    width: 8px;
    position: relative;
    z-index: 2;

    &:before {
      content: '';
      position: absolute;
      top: -5px;
      right: -5px;
      bottom: -5px;
      left: -5px;
      cursor: pointer;
    }

    &:hover {
      #{$this}__condition-hint {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__condition-hint {
    position: absolute;
    visibility: hidden;
    opacity: 0;
    transition: $transition-time;
    top: -3px;
    right: -6px;
    background-color: #fff;
    color: $color-dark-blue;
    font-size: 12px;
    font-weight: 400;
    line-height: 1;
    box-shadow: 0 3px 10px rgba(148, 165, 180, 0.14);
    border-radius: 3px;
    padding: 5px 10px 5px;
    white-space: nowrap;
  }

  &__collateral {
    display: flex;
    align-items: center;
    line-height: 18px;
  }

  &__prices-item-num {
    padding-right: 3px;
  }

  &__dark {
    background: #1C222D;
    color: rgba(#8D9098, .5);

    #{$this}__icon-status svg path:nth-child(2) {
      fill: #232323
    }

    #{$this}__table-col {
      &:nth-child(1) {
        &:hover {
          background: #1d222f;
        }
      }
    }

    #{$this}__price {
      color: #CACCD3;

      .e-big-number {
        color: #CACCD3;

        &__int,
        &__leading-zeros {
          color: #CACCD3;
        }
      }

      #{$this}_buy & {
        color: $color-dark-grey;
      }

      #{$this}_sell & {
        color: $color-dark-grey;
      }
    }

    #{$this}__price_total {
      color: #CACCD3;

      .e-big-number {
        color: #CACCD3;

        &__int,
        &__leading-zeros {
          color: #CACCD3;
        }
      }

      #{$this}_buy & {
        color: $color-dark-grey;
      }

      #{$this}_sell & {
        color: $color-dark-grey;
      }
    }

    #{$this}__price-icon .e-icon svg {
      fill: #8d9098;
    }

    #{$this}__top {
      border-bottom-color: #3a3b3c;
    }

    &#{$this}_filled {
      #{$this}__condition {
        background-color: #205d2c;
      }
    }

    #{$this}__condition-hint {
      background: #1a1a1a;
      color: $color-dark-grey;
      box-shadow: 0 3px 10px rgba(#000, 0.14);
    }

    #{$this}__filled,
    #{$this}__left {
      color: #fff;
    }

    #{$this}__timer,
    #{$this}__left #{$this}__hint {
      color: #8d9098;
    }

    #{$this}__delete .e-icon svg {
      fill: #8d9098;

      &:hover {
        fill: $color-blue-dark-mode;
      }
    }

    &#{$this}_canceled:before {
      background-image: repeating-linear-gradient(60deg, #1C222D, #1C222D 6px, #181d26 0, #181d26 8px);
    }

    #{$this}__rail {
      background: #505360;
    }

    #{$this}__short-name {
      color: #8D9098;
    }

    #{$this}__icon-earned {
      svg {
        fill: #8D9098;
      }
    }

    #{$this}__delete-button {
      background: rgba(92,96,98, .4);
      color: #8D9098;

      &:hover {
        background: rgba(92,96,98, .6);
      }
    }

    .e-big-number {
      color: #8D9098;
    }

    #{$this}__table-col-price,
    #{$this}__table-col-amount {
      .e-big-number {
        color: #CACCD3;
      }
    }

    #{$this}__token-order-buy {
      color: #00DB9C;
      background: rgba(31,206,67,0.10);
    }

    &_filled {
      #{$this}__condition {
        border: 2px solid #00DB9C;
      }
    }
  }
}

@keyframes blink {
  0%, 100% {
    opacity: 0.1;
  }
  50% {
    opacity: 0.8;
  }
}

</style>



