<template>
  <div class="the-order-form-stop-limit">
    <e-input-number-order
      :value="amount"
      :decimals="FUTURES_AMOUNT_DECIMALS"
      :invalid="$v.amount.$dirty && ($v.amount.$invalid || !enoughBalance)"
      label="Amount"
      :measurement-label="activeContractName.replace(/USDT|USD/g, '')"
      class="the-order-form-stop-limit__input the-order-form-stop-limit__item"
      labelAsPlaceholder
      squareStyle
      placeholder=""
      @input="onAmountInput"
      :min="0"
      type="arrow"
      :activeAssetAmountDec="activeAssetAmountDec"
      :priceStep="0"
    />

    <e-input-number-order
      :value="limitPrice"
      :decimals="FUTURES_PRICE_DECIMALS"
      :invalid="$v.limitPrice.$error"
      label="Limit Price"
      measurement-label="USD"
      class="the-order-form-stop-limit__input the-order-form-stop-limit__item"
      labelAsPlaceholder
      squareStyle
      placeholder=""
      @input="onLimitPriceInput"
      :min="0"
      :max="100"
      type="arrow"
      :priceStep="activeAssetNotional"
    />

    <e-input-number-order
      :value="stopPrice"
      :decimals="FUTURES_PRICE_DECIMALS"
      :invalid="$v.stopPrice.$error"
      label="Stop Price"
      measurement-label="USD"
      class="the-order-form-stop-limit__input the-order-form-stop-limit__item"
      labelAsPlaceholder
      squareStyle
      placeholder=""
      @input="onStopPriceInput"
      :min="0"
      type="arrow"
      :priceStep="activeAssetNotional"
    />

    <!--<div class="the-order-form-stop-limit__submit the-order-form-stop-limit__item">-->
      <!--<slot name="submit"/>-->
    <!--</div>-->
  </div>
</template>

<script>
import { BigNumber } from 'bignumber.js';
import { mapGetters, mapState } from 'vuex';
import { validationMixin } from 'vuelidate';
import { required, decimal } from 'vuelidate/lib/validators';
import get from 'lodash/get';

import { ETH_DECIMALS, BUY_TOKEN_ORDER, SELL_TOKEN_ORDER } from '@/util/constants';
import { maxDecimalPlaces, greaterThanZero } from '@/util/validators';

import EInputNumberOrder from '@/components/EInputNumberOrder';

export default {
  name: 'the-futures-order-form-stop-limit',
  inheritAttrs: false,

  props: {
    stopPrice: {
      type: String,
      default: '',
    },

    limitPrice: {
      type: String,
      default: '',
    },

    amount: {
      type: String,
      default: '',
    },

    buyOrSell: {
      type: String,
      required: true,
      validator: v => [BUY_TOKEN_ORDER, SELL_TOKEN_ORDER].includes(v),
    },

    anyDirty: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    total() {
      const limitPrice = BigNumber(this.limitPrice);
      const amount = BigNumber(this.amount);
      return limitPrice.isNaN() || amount.isNaN()
        ? ''
        : amount.times(limitPrice).toFixed();
    },

    invalid() {
      return this.$v.$invalid;
    },

    ...mapGetters({
      activeContractName: 'futuresActiveContract/contractName',
    }),

    ...mapState({
      activeAssetAmountDec: state => get(state, 'futuresActiveContract.activeContract.amountDec'),
      activeAssetNotional: state => get(state, 'futuresActiveContract.activeContract.notional'),
    }),
  },

  validations: {
    stopPrice: {
      required,
      decimal,
      greaterThanZero,
      maxDecimalPlaces: maxDecimalPlaces(ETH_DECIMALS),
    },

    limitPrice: {
      required,
      decimal,
      greaterThanZero,
      maxDecimalPlaces: maxDecimalPlaces(ETH_DECIMALS),
    },

    amount: {
      required,
      decimal,
      greaterThanZero,
      maxDecimalPlaces(value) {
        return maxDecimalPlaces(ETH_DECIMALS)(value);
      },
    },
  },

  watch: {
    invalid: {
      immediate: true,
      handler(invalid) {
        this.$emit('update:invalid', invalid);
      },
    },

    '$v.$anyDirty': function handler(anyDirty) {
      this.$emit('update:anyDirty', anyDirty);
    },

    anyDirty: {
      immediate: true,
      handler(anyDirty) {
        if (anyDirty) {
          this.$v.$reset();
        }
      },
    },
  },

  created() {
    this.onInput();
  },

  methods: {
    onInput({ stopPrice = this.stopPrice, limitPrice = this.limitPrice, amount = this.amount } = {}) {
      this.$emit('input', { stopPrice, limitPrice, amount });
    },

    onStopPriceInput(stopPrice) {
      this.$v.stopPrice.$touch();
      this.onInput({ stopPrice });
    },

    onLimitPriceInput(limitPrice) {
      this.$v.limitPrice.$touch();
      this.onInput({ limitPrice });
    },

    onAmountInput(amount) {
      this.$v.amount.$touch();
      this.onInput({ amount });
    },
  },

  components: {
    EInputNumberOrder,
  },

  mixins: [validationMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-order-form-stop-limit {
  display: flex;
  flex-flow: wrap;
  justify-content: space-between;

  &__item {
    height: 25px;
    margin-bottom: 8px;
  }

  &__input {
    width: 100%;
  }

  &__submit {
    width: 100%;
  }

  &__collateral {
    width: calc(50% - 2px);

    .the-order__prices {
      margin-top: 0;
    }

    .the-order__prices-item {
      flex: 0 0 100%;
    }
  }
}
</style>



