<script>
/**
 * Component: OrderBook
 *
 * @version 1.1.0
 */

import { BigNumber } from 'bignumber.js';
import { mapGetters, mapActions, mapState } from 'vuex';
import debounce from 'lodash/debounce';
import { BUY_TOKEN_ORDER, SELL_TOKEN_ORDER } from '@/util/constants';

import { onReconnect, removeReconnectListener } from '@/api/backend';
import { onResume, removeResumeListener } from '@/util/IdleEmitter';

import iconEye from '@/icons/e-icon_eye.svg';
import iconChips from '@/icons/e-icon_chips.svg';
import iconLoss from '@/icons/e-icon_loss.svg';
import iconProfit from '@/icons/e-icon_profit.svg';
import iconEarned from '@/icons/e-icon_earned.svg';
import iconError from '@/icons/e-icon_empty-order-books.svg';

import EBigNumber from '@/components/EBigNumber';
import EContainerScroll from '@/components/EContainerScroll';
import EFuturesOrderBookItem from '@/components/EFuturesOrderBookItem';
import EPanelError from '@/components/EPanelError';
import EIcon from '@/components/EIcon';
import EOrderBookPlaceholder from '@/components/EOrderBookPlaceholder';
import ETooltip from '@/components/ETooltip';

export default {
  name: 'the-futures-order-book',

  data() {
    return {
      minScrollbarLength: 20,
      iconEye,
      iconChips,
      iconLoss,
      iconProfit,
      iconEarned,
      iconError,

      hoverIndex: -1,
      hoverOrderType: '',
      orderMinCount: 10,
      maxBid: BigNumber(0),
      maxAsk: BigNumber(0),
      amountDec: 0,
      decimals: 0,
    };
  },

  props: {
    orderBookState: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    ...mapGetters({
      activeContractName: 'futuresActiveContract/contractName',
      activeAssetHash: 'futuresActiveContract/assetHash',
      previousContractHash: 'futuresPositions/previousContractHash',
      contractBaseToken: 'futuresActiveContract/contractBaseToken',
      asks: 'futuresOrderBook/getAsks',
      bids: 'futuresOrderBook/getBids',
      windowSize: 'ui/windowSize',
      positionToClose: 'futuresPositions/positionToClose',
      theme: 'ui/getTheme',
    }),

    ...mapState({
      activeAsset: state => state.futuresActiveContract.activeContract.asset,
      activeContract: state => state.futuresActiveContract.activeContract,
      orderBookVolume: state => state.futuresOrderBook.volume,
    }),

    sellPlaceholderCount() {
      const res = this.orderMinCount - this.asks.length;
      return res > 0 ? res : 0;
    },

    buyPlaceholderCount() {
      const res = this.orderMinCount - this.bids.length;
      return res > 0 ? res : 0;
    },

    // Определяет есть ли ордера
    isOrderBookHasOrders() {
      return this.asks.length + this.bids.length > 0;
    },
  },

  watch: {
    asks() {
      // this.calcHeight();
      this.maxAsk = BigNumber.max.apply(null, this.asks.map(item => item.volumeSum));
      this.decimals = this.activeContract.decimals;
      this.amountDec = this.activeContract.amountDec;
    },

    bids() {
      // this.calcHeight();
      this.maxBid = BigNumber.max.apply(null, this.bids.map(item => item.volumeSum));
      this.amountDec = this.activeContract.amountDec;
    },

    windowSize: {
      deep: true,
      handler() {
        this.onResize();
      },
    },

    activeAssetHash() {
      this.loadOrderBook();
    },
  },

  created() {
    this.onResize = debounce(this.onResize.bind(this), 200);
    this.loadOrderBook = this.loadOrderBook.bind(this);
    onReconnect(this.loadOrderBook);
    onResume(this.loadOrderBook);
    this.loadOrderBook();
    this.listenToOrderBookChanges();
  },

  mounted() {
    // this.calcHeight();
  },

  beforeDestroy() {
    removeResumeListener(this.loadOrderBook);
    removeReconnectListener(this.loadOrderBook);
    this.$store.dispatch('futuresOrderBook/stopListeningToOrderBookChanges');
  },

  methods: {
    ...mapActions({
      loadOrderBook: 'futuresOrderBook/loadOrderBook',
      listenToOrderBookChanges: 'futuresOrderBook/listenToOrderBookChanges',
    }),

    handleSelectOrder({ price, size, buyOrSellAsResponse, index }) {
      this.hoverIndex = index;
      this.hoverOrderType = buyOrSellAsResponse === BUY_TOKEN_ORDER ? SELL_TOKEN_ORDER : BUY_TOKEN_ORDER;

      this.$store.dispatch('futuresActiveOrder/fillOrder', {
        buyOrSell: buyOrSellAsResponse,
        limitPrice: price,
        amount: size,
      });
    },

    handleSelectBuyOrder({ price, size, index }) {
      if (!this.positionToClose) {
        this.handleSelectOrder({ price, size, index, buyOrSellAsResponse: SELL_TOKEN_ORDER });
      } else {
        this.handleSelectOrder({ price, size, index, buyOrSellAsResponse: this.positionToClose.side ? SELL_TOKEN_ORDER : BUY_TOKEN_ORDER });
      }
    },

    handleSelectSellOrder({ price, size, index }) {
      if (!this.positionToClose) {
        this.handleSelectOrder({ price, size, index, buyOrSellAsResponse: BUY_TOKEN_ORDER });
      } else {
        this.handleSelectOrder({ price, size, index, buyOrSellAsResponse: this.positionToClose.side ? SELL_TOKEN_ORDER : BUY_TOKEN_ORDER });
      }
    },

    handleHoverBuyOrder(index) {
      this.hoverIndex = index;
      this.hoverOrderType = BUY_TOKEN_ORDER;
    },

    handleHoverSellOrder(index) {
      this.hoverIndex = index;
      this.hoverOrderType = SELL_TOKEN_ORDER;
    },

    handleMouseleaveOrder() {
      this.hoverIndex = -1;
      this.hoverOrderType = '';
    },

    orderBookToggle() {
      this.$emit('orderBookToggle', this.toggler);
      // this.calcHeight();
    },

    calcHeight() {
      this.$nextTick(() => {
        const totalHeight = this.$refs.orderBookList
          ? this.$refs.orderBookList.offsetHeight
          : 0;
        const lastOrderHeight = this.$refs.lastOrder
          ? this.$refs.lastOrder.offsetHeight
          : 0;
        const order = this.$el.querySelector('.e-order-book');
        const orderHeight = order ? order.offsetHeight + 1 : 0;
        this.orderMinCount = Math.floor((totalHeight - lastOrderHeight) / (orderHeight * 2));
      });
    },

    onResize() {
      // this.calcHeight();
    },
  },

  components: {
    EFuturesOrderBookItem,
    EContainerScroll,
    EIcon,
    EBigNumber,
    EPanelError,
    EOrderBookPlaceholder,
    ETooltip,
  },
};
</script>

