    margin-left: -2px;
    transition: color .15s ease-in-out;

    &:not(:last-child) {
      padding-right: 3px;
      margin-right: -1px;

      &:before {
        content: '';
        position: absolute;
        right: 3px;
        top: 7px;
        transform: translateY(-50%);
        height: 2px;
        width: 2px;
        border-radius: 50%;
        background-color: $color-dark-blue;
      }
    }

    @include rwh(2400, 1280) {
      font-size: 13px;
    }
  }

  &__amount-item-token-name {
    font-weight: 800;
  }

  &__amount-item-icon {
    display: inline-block;
    height: 9px;
    width: 9px;
    margin-left: -3px;
    margin-right: 4px;
  }

  &__toggler {
    margin-right: 8px;
    border-radius: 5px;
    padding-top: 1px;
    margin-left: 4px;
    color: $color-blue;
    /*box-shadow: 0 1px 3px rgba(black, 0.08);*/
    font-size: 11px;
    font-weight: 800;
    text-transform: uppercase;
    position: relative;
    letter-spacing: 0.75px;
    cursor: pointer;
    /*background-color: #fff;*/
    display: flex;
    justify-content: center;
    align-items: center;
    height: 36px;

    &:hover {
      #{$this}__toggler-text {
        color: $color-red;
      }

      #{$this}__toggler-close {
        .e-icon svg {
          fill: $color-red;
        }
      }

      #{$this}__toggler-eye {
        .e-icon svg {
          fill: $color-red;
        }
      }
    }

    @include rwh(2400, 1280) {
      margin-top: 5px;
      letter-spacing: 1.3px;
      padding-left: 24px;
      font-size: 10px;
    }

    @include r(1279) {
      display: none;
    }
  }

  &__toggler-wrapper {
    position: relative;
  }

  &__toggler-text {
    transition: $transition-time;
    letter-spacing: 0.75px;
  }

  &__toggler-close {
    width: 12px;
    height: 12px;
    position: absolute;
    top: 0;
    right: -15px;

    .e-icon svg {
      transition: $transition-time;
      fill: $color-blue;
    }
  }

  &__toggler-eye {
    width: 19px;
    height: 10px;
    /*display: inline-block;*/
    display: none;
    position: relative;
    top: -4px;
    left: -3px;

    .e-icon svg {
      transition: $transition-time;
      fill: $color-blue;
    }
  }

  &__list {
    position: relative;
    height: calc(100% - 44px);

    /*@include rwh(2400, 1280) {
      height: calc(100% - 147px);
    }*/

    @include r(1279) {
      height: calc(100% - 28px);
    }
  }

  &__sell {
    height: calc(50% - 6px);
    margin-bottom: 12px;
    position: relative;
    overflow: hidden;
  }

  &__sell-wrapper {
    position: absolute;
    width: 100%;
    bottom: 0;
  }

  &__sell-title {
    overflow: hidden;
    position: absolute;
    z-index: 1;
    top: 2px;
    left: 4px;
    width: 28px;
    height: 100%;
    background: #f6f7f9;
    transition: background .15s ease-in-out;
  }

  &__sell-title-text {
    width: 100px;
    position: absolute;
    top: calc(50% - 65px);
    transform: rotate(-90deg) translate(-50%, -34px);
    text-transform: uppercase;
    font-size: 10px;
    font-weight: 800;
    color: #DA4830;
  }

  &__sell-icon {
    width: 12px;
    height: 11px;
    display: inline-block;
    position: absolute;
    margin-left: 5px;
    margin-top: 2px;
    transform: rotate(90deg);
  }

  &__buy {
    height: calc(50% - 6px);
    position: relative;
    overflow: hidden;
  }

  &__buy-wrapper {
    position: absolute;
    width: 100%;
  }

  &__buy-title {
    overflow: hidden;
    position: absolute;
    z-index: 1;
    top: 2px;
    left: 4px;
    width: 28px;
    height: 100%;
    background: #f6f7f9;
    transition: background .15s ease-in-out;
  }

  &__buy-title-text {
    width: 100px;
    position: absolute;
    top: calc(50% - 65px);
    transform: rotate(-90deg) translate(-50%, -34px);
    text-transform: uppercase;
    font-size: 10px;
    font-weight: 800;
    color: #1FCE43;
  }

  &__buy-icon {
    width: 12px;
    height: 11px;
    display: inline-block;
    position: absolute;
    margin-left: 5px;
    margin-top: 1px;
    transform: rotate(90deg);
  }

  &__error {
    height: calc(100% - 25px);
    width: 100%;
    box-sizing: border-box;
    margin-top: 8px;
  }

  .e-big-number {
    letter-spacing: 0.73px;
  }

  &__dark {
    background: $dark-background;

    .e-order-book-placeholder {
      background: $dark-background-lighter;
    }

    .e-order-book-placeholder__left:after,
    .e-order-book-placeholder__mid:after,
    .e-order-book-placeholder__right:after {
      background: #58595d;
    }

    #{$this}__sell-title,
    #{$this}__buy-title {
      background: #111;
    }

    #{$this}__head-col,
    #{$this}__amount-title,
    #{$this}__amount-item,
    #{$this}__last-eth-price-title,
    #{$this}__last-eth {
      color: $color-dark-grey;
      opacity: 1;
    }

    #{$this}__amount-item-icon,
    #{$this}__last-eth-icon {
      svg {
        fill: $color-dark-grey;
      }
    }

    #{$this}__last-eth-price-change {
      color: #1FCE43;
    }

    #{$this}__buy-title-text {
      color: #1FCE43;

      .e-icon_profit-fill {
        fill: #1FCE43;
      }

      svg path:nth-child(2) {
        fill: #232323;
      }
    }

    #{$this}__sell-title-text {
      svg path:nth-child(2) {
        fill: #232323;
      }
    }

    #{$this}__toggler-text {
      color: $color-blue-dark-mode;
    }

    .e-order-book__row {
      background: $dark-background-lighter;

      &:hover {
        background: #121620;
      }
    }

    .e-order-book__row.is-active {
      background: #121620;
    }

    .e-order-book__token,
    .e-order-book__eth {
      color: #fff;
    }

    .e-order-book__price-eth-icon svg,
    .e-order-book__eth-icon svg {
      fill: $color-dark-grey !important;
    }

    .e-order-book__row:before,
    .e-order-book:before {
      background: #111;
    }

    .e-order-book__row_buyToken {
      .e-order-book__price {
        color: #00DB9C;
      }

      .e-order-book__volume-line {
        background: #00DB9C;
      }
    }
  }
}
</style>



