<template>
  <div class="the-futures-orders-table" :class="[ `the-futures-orders-table__${theme}`]">
    <div v-if="demoStep === 10 && windowSize.width > 1279" class="the-futures-orders-table__demo">
      <e-demo title="Positions" arrowPosition="bottom" :isPrevDisabled="true" :nextAction="scrollToPositionClose">
        Congratulations, you have opened your first position! To understand what each number means, simply hover your mouse over the column title to see an explanation.
      </e-demo>
    </div>

    <div
      class="the-futures-orders-table__tabs"
    >
      <e-tabs
        :tabs="tabs"
        v-model="activeTab"
      />
    </div>

    <the-futures-my-orders v-if="[NEWS_AND_ORDERS_TAB_ACTIVE, NEWS_AND_ORDERS_TAB_HISTORY].includes(activeTab)"/>

    <the-futures-positions v-if="activeTab === NEWS_AND_ORDERS_TAB_POSITIONS"/>
    <the-futures-positions-closed v-if="activeTab === NEWS_AND_ORDERS_TAB_CLOSED_POSITION"/>
    <the-history-tab v-if="activeTab === HISTORY_TAB"/>
  </div>
</template>

<script>
/**
 * Компонент для отображения новостей и моих ордеров
 *
 * @version 1.0.5
 */

import { mapGetters } from 'vuex';

import {
  NEWS_AND_ORDERS_TAB_ACTIVE,
  NEWS_AND_ORDERS_TAB_HISTORY,
  MY_ORDERS_MODE_ACTIVE,
  MY_ORDERS_MODE_HISTORY,
} from '@/store/constants';

// Сomponents
import TheNews from '@/containers/blocks/TheNews';
import TheFuturesPositions from '@/containers/blocks/TheFuturesPositions';
import TheFuturesPositionsClosed from '@/containers/blocks/TheFuturesPositionsClosed';
import TheFuturesMyOrders from '@/containers/blocks/TheFuturesMyOrders';
import TheFuturesPositionsAndMyOrders from '@/containers/blocks/TheFuturesPositionsAndMyOrders';
import TheHistoryTab from '@/containers/blocks/TheHistoryTab';
import ETabs from '@/components/ETabs';
import EDemo from '@/components/EDemo';


const TAB_ACTIVE_ORDERS = {
  id: 1,
  title: 'Active orders',
};

const TAB_HISTORY = {
  id: 3,
  title: 'Order history',
};

const TAB_POSITIONS = {
  id: 0,
  title: 'Positions',
};

const TAB_CLOSED_POSITIONS = {
  id: 2,
  title: 'Closed positions',
};

const TAB_TRANSACTION = {
  id: 5,
  title: 'Trades',
};

let TABS_FOR_USER_WITH_WALLET = [
  TAB_POSITIONS,
  TAB_ACTIVE_ORDERS,
  TAB_HISTORY,
  TAB_CLOSED_POSITIONS,
  TAB_TRANSACTION,
];

const TABS_FOR_USER_WITHOUT_WALLET = TABS_FOR_USER_WITH_WALLET; /* [TAB_ACTIVE_ORDERS]; */

export default {
  name: 'the-futures-positions-and-my-orders',

  computed: {
    /**
     * Номер выбранной по умолчанию вкладки
     */
    activeTab: {
      get() {
        return this.$store.state.ui.newsAndMyOrdersTab;
      },

      set(activeTab) {
        this.$store.commit('ui/SET_NEWS_AND_ORDERS_TAB', activeTab);

        // TODO: infer my orders mode from newsAndOrders mode
        if (activeTab === NEWS_AND_ORDERS_TAB_ACTIVE) {
          this.$store.commit('futuresOrders/SET_MY_ORDERS_MODE', MY_ORDERS_MODE_ACTIVE);
        } else if (activeTab === NEWS_AND_ORDERS_TAB_HISTORY) {
          this.$store.commit('futuresOrders/SET_MY_ORDERS_MODE', MY_ORDERS_MODE_HISTORY);
        }
        // else if (activeTab === NEWS_AND_ORDERS_TAB_CLOSED_POSITION) {
        //   console.log('NEWS_AND_ORDERS_TAB_CLOSED_POSITION opened');
        // }
      },
    },

    /**
     * Массив со вкладками
     * В зависимости от того, залогинен или нет пользователь показываются либо только Новости, либо Позиции и Ордера
     */
    tabs() {
      if (this.windowSize.width > 1279) {
        TABS_FOR_USER_WITH_WALLET = [
          TAB_POSITIONS,
          TAB_ACTIVE_ORDERS,
          TAB_HISTORY,
          TAB_CLOSED_POSITIONS,
          TAB_TRANSACTION,
        ];
      } else {
        TABS_FOR_USER_WITH_WALLET = [
          TAB_ACTIVE_ORDERS,
          TAB_HISTORY,
          TAB_TRANSACTION,
        ];

        this.$store.commit('ui/SET_NEWS_AND_ORDERS_TAB', NEWS_AND_ORDERS_TAB_ACTIVE);
        this.$store.commit('futuresOrders/SET_MY_ORDERS_MODE', MY_ORDERS_MODE_ACTIVE);
      }

      return this.isLogin ? TABS_FOR_USER_WITH_WALLET : TABS_FOR_USER_WITHOUT_WALLET;
    },

    ...mapGetters({
      demoStep: 'ui/demoStep',
      isLogin: 'wallets/isLogin',
      theme: 'ui/getTheme',
      windowSize: 'ui/windowSize',
    }),
  },

  components: {
    TheNews,
    TheFuturesPositions,
    TheFuturesPositionsClosed,
    TheFuturesMyOrders,
    TheFuturesPositionsAndMyOrders,
    TheHistoryTab,
    ETabs,
    EDemo,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-futures-orders-table {
  $this: &;

  position: relative;
  height: 100%;
  background: #fff;
  border: 1px solid #DBDCDD;
  border-radius: 3px;
  /*overflow: hidden;*/

  @include r(1279) {
    height: auto;
  }

  &__tabs {
    margin-bottom: 0;
    padding: 8px 10px;
    background: #fff;
    border-bottom: 1px solid #DBDCDD;
  }

  .e-container-scroll {
    overflow-y: scroll;
    height: calc(100% - 34px);

    &::-webkit-scrollbar-track {
      background-color: #fff;
    }
  }

  &__demo {
    position: absolute;
    z-index: 36;
    top: 0;
    left: 0;
    transform: translate(calc(-50% + 35px), calc(-100% - 10px));

    @include r(1279) {
      left: 0;
      transform: translate(0, calc(-100% - 10px));
    }
  }

  .e-demo {
    width: 290px;
  }

  &__dark {
    background: #121722;
    border-color: #282C38;

    #{$this}__tabs {
      background: #121722;
      border-color: #1e202b;
    }

    .e-container-scroll {
      &::-webkit-scrollbar-track {
        background-color: #121722;
      }
    }
  }
}
</style>



