<template>
  <div>
    <div class="page-charts" :class="{ 'page-charts__test': isNotification || getStorageValue('isNewTestETHNotification') }">
      <!-- <div class="page-charts__notification-header" :class="{ 'dark': theme === 'dark' }">
        <a href="https://docs.dmex.app/changelog" target="_blank">
           <span>13th Feb 2020 | Contracts Updated!</span>
          <span class="highlight">Changelog<span class="symbol"></span></span>
        </a>
        <a href="https://docs.dmex.app/withdrawing-from-old-contracts" target="_blank">
          <span class="highlight">Withdraw funds from Old Contract<span class="symbol"></span></span>
        </a> -->
      <!-- <div class="page-charts__notification-header" :class="{ 'dark': theme === 'dark' }">
        <a href="https://twitter.com/DMEX_APP/status/1232917470207447040" target="_blank">
           <span>If the website freezes, please clear your browser cache including "Cookies and other site data"!</span>
          <span class="highlight">Learn More<span class="symbol"></span></span>
        </a> -->
        <!-- <span class="page-charts__notification-close" @click.stop="isNotification = false; setStorageValue('isNewTestETHNotification', false)">
          <e-icon :src="$options.icons.iconClose" />
        </span> -->
      <!-- </div> -->

      <div
        class="page-charts__wrapp"
      >
        <!-- header menu -->
        <the-header-inner/>
        <!-- header menu -->

        <div class="page-charts__wrapp-content">
          <div class="page-charts__body">
            <div class="page-charts__main" :class="{ 'page-charts__main-dark': theme === 'dark' }">
              <div
                class="page-charts__assets"
              >
                <the-assets />

                <a href="https://t.me/dmex_app" target="_blank" class="page-charts__telegram-link">
                  <div class="page-charts__telegram-link-icon">
                    <e-icon :src="iconTelegram" />
                  </div>
                  <div class="page-charts__telegram-link-text">
                    <b>Join our Telegram group</b>
                    <div>to get reall-time support</div>
                  </div>
                </a>
              </div>
              <div
                class="page-charts__content"
                :class="{ 'page-charts__content-light': theme === 'light', 'page-charts__content-dark': theme === 'dark' }"
              >
                <div class="page-charts__graph">
                  <div class="page-charts__graph-wrapper">
                    <the-futures-tradingview-staging v-if="assetType === 'crypto' && isTradingViewStaging" />
                    <the-futures-tradingview v-if="assetType === 'stock'" />

                    <div class='the-trading-view' :class="{ 'show': assetType === 'stock'}">
                      <!-- TradingView Widget BEGIN -->
                      <div class="tradingview-widget-container the-trading-view__chart">
                        <div id="tradingview_684bd"></div>
                      </div>
                      <!-- TradingView Widget END -->
                    </div>

                    <div class='the-trading-view staging' :class="{ [`the-trading-view__${theme}`]: true, 'show': assetType === 'crypto' }">
                      <div id='tv_chart_container' class='the-trading-view__chart'></div>
                    </div>

                    <!--<the-futures-tradingview-staging v-if="APP_ENV === 'test'" />-->
                    <!--<the-futures-tradingview v-else />-->
                  </div>

                  <div class="page-charts__graph-trades">
                    <the-futures-trades />
                  </div>
                </div>

                <div class="page-charts__panels">
                  <div class="page-charts__col page-charts__trades">
                    <the-futures-my-orders-table />
                    <the-futures-positions-and-my-orders v-if="windowSize.width < 1280" />
                  </div>
                </div>
              </div>

              <div class="page-charts__right">
                <the-futures-panel-right :show-tokens="!showTokensBig"/>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <the-futures-order-popup v-if="orderConfirmationModal"/>

    <the-min-order-popup v-if="minOrderErrorModal"/>

    <the-futures-delete-order-popup v-if="cancelOrderModal"/>

    <the-popup-error-order-balance v-if="errorOrderBalance"/>

    <the-popup-error-order-balance-reserved v-if="errorOrderBalanceReserved"/>

    <!-- popup and menu -->
    <the-popup v-if="isPopupShowing"/>
    <the-menu-sidebar v-if="isPopupShowing"/>
    <!-- popup and menu -->
  </div>
</template>

<script>
import debounce from 'lodash/debounce';
import { setStorageValue, getStorageValue } from '@/util/storage';
import { isValidHex } from '@/util/crypto';
import { NOT_FOUND_PAGE } from '@/util/constants';
import { mapState, mapGetters } from 'vuex';
// import { ASYNC_STATES, NOT_FOUND_PAGE } from '@/util/constants';

// Components
import TheHeaderInner from '@/containers/blocks/TheHeaderInner';
import TheFuturesTradingview from '@/containers/blocks/TheFuturesTradingview';
import TheFuturesTradingviewStaging from '@/containers/blocks/TheFuturesTradingviewStaging';
import TheFuturesPositionsAndMyOrders from '@/containers/blocks/TheFuturesPositionsAndMyOrders';
import TheFuturesMyOrdersTable from '@/containers/blocks/TheFuturesMyOrdersTable';
import TheFuturesTrades from '@/containers/blocks/TheFuturesTrades';
import TheFuturesPanelRight from '@/containers/blocks/TheFuturesPanelRight';
import TheFuturesOrderPopup from '@/containers/blocks/TheFuturesOrderPopup';
import TheFuturesTradeDetailsPopup from '@/containers/blocks/TheFuturesTradeDetailsPopup';
import TheMinOrderPopup from '@/containers/blocks/TheMinOrderPopup';
import TheFuturesDeleteOrderPopup from '@/containers/blocks/TheFuturesDeleteOrderPopup';
import ThePopupErrorOrderBalance from '@/containers/blocks/ThePopupErrorOrderBalance';
import ThePopupErrorOrderBalanceReserved from '@/containers/blocks/ThePopupErrorOrderBalanceReserved';
import ThePopup from '@/containers/blocks/ThePopup';
import TheMenuSidebar from '@/containers/blocks/TheMenuSidebar';
import TheAssets from '@/containers/blocks/TheAssets';
import EIcon from '@/components/EIcon';

import { PAGE_TITLE } from '@/config';

import iconClose from '@/icons/e-icon_close.svg';
import iconTelegram from '@/icons/e-icon__f-telegram.svg';

export default {
  name: 'page-futures',

  data() {
    return {
      tradingviewInterval: null,
      isTradingViewStaging: true,
      isNotification: true,
      assetType: null,
      setStorageValue,
      getStorageValue,
      showTokensBig: false,
      scrollSettings: {
        maxScrollbarLength: 348,
        minScrollbarLength: 121,
        wheelSpeed: 1,
        wheelPropagation: false,
        swipeEasing: false,
        suppressScrollY: true,
      },
      currentContractAsset: null,
      iconTelegram,
    };
  },

  icons: {
    iconClose,
  },

  computed: {
    ...mapState({
      errorOrderBalance: state => state.ui.errorOrderBalance,
      errorOrderBalanceReserved: state => state.ui.errorOrderBalanceReserved,
      activeContract: state => state.futuresActiveContract.activeContract,
    }),

    ...mapGetters({
      isPopupShowing: 'ui/isPopupShowing',
      orderConfirmationModal: 'futuresActiveOrder/orderConfirmation',
      minOrderErrorModal: 'activeOrder/minOrderError',
      cancelOrderModal: 'futuresOrders/cancelledOrder',
      activeContractTitle: 'futuresActiveContract/contractTitle',
      windowSize: 'ui/windowSize',
      theme: 'ui/getTheme',
    }),
  },

  watch: {
    windowSize: {
      deep: true,
      handler() {
        this.onResize();
      },
    },

    activeContract(newVal) {
      if (this.currentContractAsset !== newVal.asset) {
        this.isTradingViewStaging = false;

        setTimeout(() => {
          this.isTradingViewStaging = true;
        }, 100);
      }

      this.currentContractAsset = newVal.asset;

      const isStock = ['NASDAQ', 'NYSE'].includes(newVal.chartSymbol.split(':')[0]);

      if (isStock) {
        this.$store.commit('ui/SET_ASSET_TYPE', 'stock');
        this.assetType = 'stock';
      } else {
        this.$store.commit('ui/SET_ASSET_TYPE', 'crypto');
        this.assetType = 'crypto';
      }
    },
  },

  created() {
    this.onResize = debounce(this.onResize.bind(this), 200);
    this.onResize();
  },

  mounted() {
    this.onResize();
    this.$ga.page(this.$router);

    if (getStorageValue('isFundingRateReducedNotification') !== false) {
      setStorageValue('isFundingRateReducedNotification', true);
    }

    // this.tradingviewInterval = setInterval(() => {
    //   this.isTradingViewStaging = false;

    //   setTimeout(() => {
    //     this.isTradingViewStaging = true;
    //   }, 100);
    // }, 10 * 60 * 1000);

    // this.$store.dispatch('tokens/listenToTokensChanges');
    // this.$store.dispatch('trades/listenToTradesChanges');
    // this.$store.dispatch('balance/listenToContractBalanceChanges');
    this.$store.dispatch('deposit/listenToBalanceOperations');
    // this.$store.dispatch('tokens/loadTokens');
  },

  async beforeRouteUpdate(to, from, next) {
    const { contract: paramContract } = to.params;
    const prevParam = from.params.token;

    // const activeContract = this.$store.state.activeContract.activeContract;
    const paramChanged = paramContract !== prevParam;
    // const paramIsActiveAddress = activeContract.futuresContractHash === paramContractHex;

    if (!paramChanged) {
      next();
      return;
    }

    if (isValidHex(paramContract)) {
      if (await this.$store.dispatch('futuresActiveContract/fetchContract', paramContract)) {
        await Promise.all([
          this.$store.dispatch('futuresActiveContract/subscribeToSmartContract'),
          this.$store.dispatch('futuresActiveContract/subscribeToFuturesContract'),
          this.$store.dispatch('futuresActiveContract/listenToContractChanges'),
        ]);
        next();
      } else {
        next({ name: NOT_FOUND_PAGE });
      }
    } else {
      next({ name: NOT_FOUND_PAGE });
    }
  },

  beforeDestroy() {
    if (this.tradingviewInterval) {
      clearInterval(this.tradingviewInterval);
    }
    // this.$store.dispatch('trades/stopListeningToTradesChanges');
    // this.$store.dispatch('tokens/stopListeningToTokensChanges');
    // this.$store.dispatch('balance/stopListeningToContractBalanceChanges');
    // this.$store.dispatch('deposit/stopListeningToBalanceOperations');
  },

  methods: {
    onResize() {
      this.$nextTick(() => {
        this.showTokensBig = this.windowSize.width > 1921;
      });
    },
  },

  metaInfo() {
    return {
      title: `${this.activeContractTitle} ${PAGE_TITLE}`,
    };
  },

  components: {
    ThePopupErrorOrderBalanceReserved,
    ThePopupErrorOrderBalance,
    ThePopup,
    TheMenuSidebar,
    TheHeaderInner,
    TheFuturesTradingview,
    TheFuturesTradingviewStaging,
    TheFuturesPositionsAndMyOrders,
    TheFuturesMyOrdersTable,
    TheFuturesTrades,
    TheFuturesPanelRight,
    TheFuturesDeleteOrderPopup,
    TheFuturesOrderPopup,
    TheFuturesTradeDetailsPopup,
    TheMinOrderPopup,
    TheAssets,
    EIcon,
  },
};
</script>


<style lang="scss">
