  $this: &;

  background-color: #f4f5f9;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  overflow-x: auto;

  @include r(1279) {
    min-width: 375px;
    overflow-y: visible;
    height: auto;
  }

  &.page-charts__test {
    .page-charts__main {
      height: 100%;

      @include r(1279) {
        flex-wrap: wrap;
      }
    }
  }

  &__test-header,
  &__notification-header {
    background-color: #F3862D;
    color: #5E2F09;
    width: 100%;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 13px;
    font-weight: 500;
    padding: 0 10px;
    box-sizing: border-box;
    position: relative;
    z-index: 101;

    @include r(1279) {
      font-size: 11px;
      line-height: 12px;
      height: 30px;
    }
  }

  &__notification-header {
    background-color: #0097FE;
    font-size: 12px;
    font-weight: 500;
    text-decoration: none;
    transition: background-color .15s ease-in-out;

    a {
      display: flex;
      align-items: center;
      text-decoration: none;
      color: #fff;

      &:hover {
        .symbol {
          transform: translate(3px, 0);
        }
      }
    }

    .highlight {
      display: flex;
      align-items: center;
      padding: 0 5px;
      color: #00DB9C;

      .symbol {
        margin-left: 3px;
        display: block;
        width: 0;
        height: 0;
        border-top: 3px solid transparent;
        border-left: 5px solid #00DB9C;
        border-bottom: 3px solid transparent;
        transition: transform .15s ease-in-out;
      }
    }

    &.dark {
      background-color: #353e4f;

      a {
        color: #fff;
      }

      #{$this}__notification-close {
        opacity: .2;

        &:hover {
