        }

        svg {
          fill: #fff;
        }
      }
    }
  }

  &__notification-close {
    position: absolute;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    opacity: .8;
    cursor: pointer;
    transition: opacity .15s ease-in-out;
    right: 0;
    top: 0;

    &:hover {
      opacity: 1;
    }

    .e-icon {
      width: 8px !important;
      height: 8px !important;
    }

    svg {
      fill: #fff;
    }
  }

  &__test-header-link {
    display: inline-flex;
    align-items: center;
    font-size: 13px;
    color: #F3862D;
    background-color: #fff;
    border-radius: 4px;
    margin-left: 5px;
    height: 18px;
    text-decoration: none;
