    transition: all .15s ease-in-out;

    &:hover {
      background-color: rgba(255,255,255,.85);
      color: #000;
    }

    @include r(1279) {
      font-size: 12px;
      flex: 0 0 56px;
    }
  }

  &__wrapp {
    overflow: hidden;
    min-width: 1280px;

    @include r(1279) {
      overflow-y: visible;
      min-width: 0;
    }
  }

  &__aside {
    flex-basis: 328px;
    max-width: 328px;
    padding: 0 9px 0 10px;
    box-sizing: border-box;

    @include rmin(1921) {
      padding: 0 8px 0 10px;
    }
  }

  &__body {
    display: flex;
    flex-wrap: wrap;
    min-width: 1024px;
    height: calc(100vh - 50px);

    @include r(1279) {
      overflow-y: visible;
      height: auto;
      min-width: 0;
    }
  }

  &__assets {
    flex-basis: 14.87%;
    min-width: 268px;
    position: relative;
    height: calc(100% - 24px);
    /*border: 1px solid #DBDCDD;*/
    /*box-sizing: border-box;*/
    border-radius: 3px;
    margin: 12px 0 12px 12px;

    @include r(1279) {
      display: none;
      flex-basis: calc(100% - 24px);
      width: calc(100% - 24px);
      height: auto;
      max-width: none;
      order: 3;
    }
  }

  &__content {
    background-color: #F8F9FC;
    box-shadow: 2px 3px 3px rgba(0, 0, 0, 0.03);
    flex: 1;
    position: relative;
    height: 100%;
    z-index: 1;
    transition: background-color .15s ease-in-out;

    @include r(1560) {
      max-width: calc(100% - 601px);
    }

    @include r(1279) {
      max-width: none;
      height: auto;
      flex: 0 0 100%;
      order: 2;
    }
  }

  &__main {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    /*flex-wrap: wrap;*/
    position: relative;
    height: 100%;

    @include r(1279) {
      height: auto;
    }

    @include r(1200) {
