      max-width: 100%;
    }

    // @include rmin(1921) {
    //  flex-basis: calc(100% - 328px);
    //  max-width: calc(100% - 328px);
    // }
  }

  &__wrapp-content {
    box-sizing: border-box;
    position: relative;
    background-color: #f4f5f9;
    transform: translateZ(0);
  }

  &__panels {
    display: flex;
    flex-wrap: wrap;
    height: calc(38.2% - 15px);
    box-sizing: border-box;
    padding: 8px 0 0 10px;

    @include r(1279) {
      height: auto;
    }
  }

  &__graph {
    display: flex;
    flex-wrap: wrap;
    padding: 0 0 15px 10px;
    height: calc(61.8% - 15px);

    @include r(1279) {
      height: 632px;
    }
  }

  &__graph-wrapper {
    flex: 1;
    padding-top: 12px;

    @include r(1279) {
      flex: 0 0 100%;
      width: 100%;
      height: 340px;
    }
  }

  &__graph-trades {
    flex: 0 0 265px;
    height: 100%;
    padding-top: 12px;
    margin-left: 12px;

    @include r(1279) {
      flex: 100%;
      height: 291px;
      margin-left: 0;

      .e-container-scroll {
