      }
    }
  }

  &__right {
    flex-basis: 20%;
    min-width: 321px;
    position: relative;
    height: 100%;
    /*overflow: hidden;*/
    display: flex;
    flex-flow: column;

    @include r(1279) {
      flex: 0 0 100%;
      order: 1;
      max-width: none;
      height: auto;
    }
  }

  &__active-order {
    padding-bottom: 14px;
    border-bottom: 1px solid #e3e5f2;

    @include rmin(1921) {
      padding-bottom: 17px;
    }
  }

  &__order-book {
    min-height: calc(100vh - 81px - 418px);

    @include r(1279) {
      min-height: 0;
    }
  }

  &__col {
    box-sizing: border-box;
  }

  &__trades {
    flex-wrap: wrap;
    flex: 1;
    height: 100%;
    width: 100%;

    @include r(1279) {
      flex: 1 1 100%;
      height: auto;
      padding: 0 !important;
      width: calc(100vw - 20px);
      order: 2;
      /*overflow-y: auto;*/
      margin-top: 12px;
      max-width: none;
    }
  }

  &__orders {
    flex-basis: 405px;
    height: 100%;

    @include r(1540) {
      flex-basis: 310px;
    }

    @include r(1279) {
      flex: 1 1 100%;
      width: 100%;
      padding: 0 !important;
      order: 1;
      max-height: 408px;
      overflow: hidden;
      border-bottom: 1px solid #DBDCDD;
    }
  }

  .the-trading-view {
    position: relative;
    height: 0;
    opacity: 0;

    &.show {
      height: 100%;
      opacity: 1;
    }

    &.staging {
      border: 1px solid #DBDCDD;
