      }
    }

    #{$this}__telegram-link-icon {
      background: #343E4E;

      svg path {
        fill: #fff;
      }
    }

    #{$this}__telegram-link-text {
      color: #fff;

      div {
        color: #8D9098;
      }
    }
  }

  &__content-dark {
    background-color: $dark-background;

    #{$this}__orders {
      @include r(1279) {
        border-color: #282C38;
      }
    }
  }
}
</style>



