<template>
  <a :href="linkAddress" :class="['e-news', isPinned && '_isPinned', `e-news__${theme}`]" target="_blank">
    <div class="e-news__top">
      <div class="e-news__top-pin" v-if="isPinned">
        <e-icon :src="iconPin" />
      </div>
      <div class="e-news__top-time">
        {{time}}
      </div>
      <a :href="linkAddress" class="e-news__top-link" target="_blank">
        <div class="e-news__top-link-icon">
          <e-icon :src="iconNewsLink"/>
        </div>
        {{linkText}}
      </a>
    </div>

    <div class="e-news__body">
      <div class="e-news__body-text">
        {{bodyTextParsed}}
      </div>
    </div>

    <div class="e-news__rates">
      <div class="e-news__rates-item e-news__rates-item-arrow-up" v-if="positive !== 0">
        <div class="e-news__rates-item-icon">
          <e-icon :src="iconArrowUp"/>
        </div>
        {{positive}}
      </div>

      <div class="e-news__rates-item e-news__rates-item-arrow-down" v-if="negative !== 0">
        <div class="e-news__rates-item-icon">
          <e-icon :src="iconArrowDown"/>
        </div>
        {{negative}}
      </div>

      <div class="e-news__rates-item e-news__rates-item-alert" v-if="important !== 0">
        <div class="e-news__rates-item-icon">
          <e-icon :src="iconAlert"/>
        </div>
        {{important}}
      </div>

      <div class="e-news__rates-item e-news__rates-item-thumb-up" v-if="liked !== 0">
        <div class="e-news__rates-item-icon">
          <e-icon :src="iconThumbUp"/>
        </div>
        {{liked}}
      </div>

      <div class="e-news__rates-item e-news__rates-item-thumb-down" v-if="disliked !== 0">
        <div class="e-news__rates-item-icon">
          <e-icon :src="iconThumbDown"/>
        </div>
        {{disliked}}
      </div>

      <div class="e-news__rates-item e-news__rates-item-smile" v-if="lol !== 0">
        <div class="e-news__rates-item-icon">
          <e-icon :src="iconSmile"/>
        </div>
        {{lol}}
      </div>

      <div class="e-news__rates-item e-news__rates-item-star" v-if="saved !== 0">
        <div class="e-news__rates-item-icon">
          <e-icon :src="iconNewsStar"/>
        </div>
        {{saved}}
      </div>
    </div>
  </a>
</template>

<script>
import { mapGetters } from 'vuex';
import unescape from 'lodash/unescape';
/**
 * Component: Отображает одну новость
 */
import iconNewsLink from '@/icons/e-icon_news-link.svg';
import iconPin from '@/icons/e-icon_pin.svg';
import iconAlert from '@/icons/e-icon_alert.svg';
import iconArrowDown from '@/icons/e-icon_arrow-down.svg';
import iconArrowUp from '@/icons/e-icon_arrow-up.svg';
import iconNewsStar from '@/icons/e-icon_news-star.svg';
import iconSmile from '@/icons/e-icon_smile.svg';
import iconThumbDown from '@/icons/e-icon_thumb-down.svg';
import iconThumbUp from '@/icons/e-icon_thumb-up.svg';

import EIcon from '@/components/EIcon';

export default {
  name: 'e-news',

  props: {
    isPinned: {
      type: Boolean,
      default: false,
    },

    time: {
      type: String,
    },

    linkAddress: {
      type: String,
    },

    linkText: {
      type: String,
    },

    bodyText: {
      type: String,
    },

    positive: {
      type: Number,
      default: 0,
    },

    negative: {
      type: Number,
      default: 0,
    },

    important: {
      type: Number,
      default: 0,
    },

    liked: {
      type: Number,
      default: 0,
    },

    lol: {
      type: Number,
      default: 0,
    },

    saved: {
      type: Number,
      default: 0,
    },

    disliked: {
      type: Number,
      default: 0,
    },
  },

  data() {
    return {
      iconNewsLink,
      iconPin,
      iconAlert,
      iconArrowDown,
      iconArrowUp,
      iconNewsStar,
      iconSmile,
      iconThumbDown,
      iconThumbUp,
    };
  },

  computed: {
    bodyTextParsed() {
      return unescape(this.bodyText);
    },

    ...mapGetters({
      theme: 'ui/getTheme',
    }),
  },

  components: {
    iconNewsLink,
    iconPin,
    iconAlert,
    iconArrowDown,
    iconArrowUp,
    iconNewsStar,
    iconSmile,
    iconThumbDown,
    iconThumbUp,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-news {
  $this: &;

  display: block;
  background: #fff;
  padding: 10px 15px 5px;
  box-sizing: border-box;
  border: 1px solid #F3F4F5;
  margin-bottom: -1px;
  position: relative;
  text-decoration: none;
  transition: all .15s ease-in-out;

  &._isPinned {
    background: #F4FAFF;
    border-color: #BFD5EA;
    z-index: 1;
  }

  &:hover {
    border-color: rgba(13, 16, 91, .38);
    z-index: 2;
  }

  &__top {
    display: flex;
    align-items: center;
  }

  &__top-pin {
    width: 18px;
    height: 18px;
    margin-right: 6px;
    margin-top: -6px;
  }

  &__top-time {
    font-size: 11px;
    color: #8384AB;
    margin-right: 6px;
    transition: color .15s ease-in-out;
  }

  &__top-link {
    font-size: 12px;
    color: #0123E7;
    text-decoration: none;
    position: relative;
    padding-left: 13px;
    margin-left: 10px;
    transition: color .15s ease-in-out;

    &:before {
      content: '·';
      position: absolute;
      left: -10px;
      top: 8px;
      color: #8384AB;
      font-size: 10px;
      line-height: 0;
    }
  }

  &__top-link-icon {
    position: absolute;
    left: -4px;
    top: 3px;
    width: 12px;
    height: 12px;
  }

  &__body {
    padding: 3px 0 6px;
  }

  &__body-text {
    font-size: 13px;
    line-height: 18px;
    color: #09168A;
    transition: color .15s ease-in-out;
  }

  &__rates {
    display: flex;
    align-items: center;
  }

  &__rates-item {
    font-size: 10px;
    display: flex;
    align-items: center;
    margin-right: 20px;

    .e-icon {
      margin-right: 1px;

      svg {
        position: relative;
      }
    }
  }

  &__rates-item-icon {
    display: flex;
    align-items: center;
    margin-right: 2px;
  }

  &__rates-item-arrow-up {
    color: #1FCE43;

    svg {
      width: 7px;
      height: 9px;
    }
  }

  &__rates-item-arrow-down {
    color: #DA4830;

    svg {
      width: 7px;
      height: 9px;
    }
  }

  &__rates-item-alert {
    color: #09168A;

    svg {
      width: 11px;
      height: 9px;
    }
  }

  &__rates-item-thumb-up {
    color: #09168A;

    svg {
      width: 10px;
      height: 9px;
    }
  }

  &__rates-item-thumb-down {
    color: #09168A;

    svg {
      width: 10px;
      height: 9px;
    }
  }

  &__rates-item-smile {
    color: #09168A;

    svg {
      width: 10px;
      height: 10px;
    }
  }

  &__rates-item-star {
    color: #09168A;

    svg {
      width: 10px;
      height: 10px;
    }
  }

  &__dark {
    background: #1C222D;
    border-color: #282C38;

    &._isPinned {
      background: #1e2531;
      border-color: #282C38;
    }

    &:hover {
      border-color: #8D9098;
    }

    #{$this}__top-time,
    #{$this}__body-text {
      color: #8D9098;
    }

    #{$this}__top-link {
      color: $color-blue-dark-mode;
    }
  }
}

</style>



