<template>
  <div class="the-order-form-market">

    <div v-if="demoStep === 7" class="the-order-form-market__demo">
      <e-demo title="Amount" arrowPosition="right" :isNextDisabled="!amount" arrowPositionMobile="top">
        This is the quantity of the asset you wish to long/short. For ease of use, click on the '25%' to use a quarter of the balance as margin. The amount will be automatically calculated based on the current market price.
      </e-demo>
    </div>

    <e-input-number-order
      :value="amount"
      :decimals="FUTURES_AMOUNT_DECIMALS"
      label="Amount"
      :measurement-label="activeContractName.replace(/USDT|USD/g, '')"
      :invalid="$v.amount.$dirty && ($v.amount.$invalid || !enoughBalance)"
      class="the-order-form-market__input the-order-form-market__item"
      labelAsPlaceholder
      squareStyle
      placeholder=""
      @input="onAmountInput"
      :min="0"
      type="arrow"
      :activeAssetAmountDec="activeAssetAmountDec"
      :priceStep="0"
    />

    <e-input-number-order
      value="MARKET PRICE"
      disabled
      measurement-label="USD"
      class="the-order-form-market__input the-order-form-market__item the-order-form-market__item-market-price"
      labelAsPlaceholder
      squareStyle
      placeholder=""
    />

    <span style="display: none;">{{total}}</span>
  </div>
</template>

<script>
import { BigNumber } from 'bignumber.js';
import { mapGetters, mapState } from 'vuex';
import { validationMixin } from 'vuelidate';
import { required, decimal } from 'vuelidate/lib/validators';
import get from 'lodash/get';
import { ZERO } from '@/util/numbers';
import { ETH_DECIMALS, FUTURES_AMOUNT_DECIMALS, BUY_TOKEN_ORDER, SELL_TOKEN_ORDER } from '@/util/constants';
import { maxDecimalPlaces, greaterThanZero } from '@/util/validators';
import getOrderTotalRoundingMode from '@/util/getOrderTotalRoundingMode';

import EDemo from '@/components/EDemo';
import EInputNumberOrder from '@/components/EInputNumberOrder';

export default {
  name: 'the-futures-order-form-market',
  inheritAttrs: false,

  props: {
    amount: {
      type: String,
      default: '',
    },

    buyOrSell: {
      type: String,
      required: true,
      validator: v => [BUY_TOKEN_ORDER, SELL_TOKEN_ORDER].includes(v),
    },

    anyDirty: {
      type: Boolean,
      default: false,
    },

    setAmountPercent: {
      type: Function,
    },
  },

  computed: {
    /**
     * Флаг показывающий крректно ли заполнена форма
     */
    invalid() {
      return this.$v.$invalid;
    },

    /**
     * Общий объем ордербука
     */
    orderBookTotal() {
      return this.orders.reduce((acc, order) => acc.plus(order.amount), ZERO);
    },

    /**
     * Тотал, который высчитаывается и выводится
     *
     * Если по каким-то причинам тотал не может быть высчитан вывдится символ '—'
     */
    total() {
      if (this.invalid) return '—'; // invalid form

      const orders = this.orders;
      const amount = BigNumber(this.amount);
      let total = BigNumber(0);
      let remains = amount;
      let i = 0;

      while (!remains.isZero() && i < orders.length) {
        const curOrderAmount = orders[i].size;
        if (remains.gt(curOrderAmount)) {
          remains = remains.minus(curOrderAmount);
          total = orders[i].volumeSum;
        } else {
          const lastOrderValue = remains.times(orders[i].price).shiftedBy(ETH_DECIMALS);
          total = total.plus(lastOrderValue);
          remains = BigNumber(0);

          this.$emit('input', { limitPrice: (orders[i].price.shiftedBy(-FUTURES_AMOUNT_DECIMALS).toFixed() * (this.buyOrSell === BUY_TOKEN_ORDER ? 1.002 : 0.998)).toString(), amount: this.amount });
        }
        i += 1;
      }

      if (!remains.isZero()) return '—'; // not enough orders in order book
      return (total.shiftedBy(-ETH_DECIMALS - FUTURES_AMOUNT_DECIMALS).dp(ETH_DECIMALS, getOrderTotalRoundingMode(this.buyOrSell)).toFixed() * (this.buyOrSell === BUY_TOKEN_ORDER ? 1.005 : 0.995)).toFixed(2);
    },

    /**
     * Массив предложений в ордербуке
     */
    orders() {
      return this.buyOrSell === BUY_TOKEN_ORDER ? [...this.asks].reverse() : this.bids;
    },

    ...mapGetters({
      demoStep: 'ui/demoStep',
      activeContractName: 'futuresActiveContract/contractName',
      asks: 'futuresOrderBook/getAsks',
      bids: 'futuresOrderBook/getBids',
    }),

    ...mapState({
      activeAssetAmountDec: state => get(state, 'futuresActiveContract.activeContract.amountDec'),
    }),
  },

  validations: {
    amount: {
      required,
      decimal,
      greaterThanZero,
      maxDecimalPlaces(value) {
        return maxDecimalPlaces(ETH_DECIMALS)(value);
      },
    },
  },

  watch: {
    demoStep() {
      if (this.demoStep === 7) {
        this.setAmountPercent(0.25);
      }
    },

    invalid: {
      immediate: true,
      handler(invalid) {
        this.$emit('update:invalid', invalid);
      },
    },

    '$v.$anyDirty': function handler(anyDirty) {
      this.$emit('update:anyDirty', anyDirty);
    },

    anyDirty: {
      immediate: true,
      handler(anyDirty) {
        if (anyDirty) {
          this.$v.$reset();
        }
      },
    },
  },

  created() {
    this.onInput();
  },

  methods: {
    onInput({ amount = this.amount } = {}) {
      this.$emit('input', { amount });
    },

    onAmountInput(amount) {
      this.$v.amount.$touch();
      this.$emit('input', { amount });
    },
  },

  components: {
    EInputNumberOrder,
    EDemo,
  },

  mixins: [validationMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-order-form-market {
  display: flex;
  flex-flow: wrap;
  justify-content: space-between;
  position: relative;

  &__demo {
    position: absolute;
    z-index: 20;
    top: 0;
    left: 0;
    transform: translate(calc(-100% - 10px), -6px);

    @include r(1279) {
      top: auto;
      bottom: 0;
      left: 0;
      right: auto;
      transform: translate(10px, calc(100% - 34px));
    }
  }

  &__item {
    height: 25px;
    margin-bottom: 8px;
  }

  &__input {
    width: 100%;
  }

  &__submit {
    width: 100%;
  }

  &__submit {
    width: calc(50% - 5px);
  }

  &__item-market-price {
    input {
      padding: 0 58px 0 44px !important;
      text-align: left !important;
    }
  }
}
</style>



