<template>
  <div class="e-market-name">
    <v-popover trigger="hover" :delay="{ show: 150 }">
      <div class="e-market-name__exchange tooltip-target b3 popover-btn">{{exchange || 'ETM'}}</div>

      <div class="e-market-name__hint" slot="popover">
        <div class="e-market-name__hint-exchange">
          Exchange
          <span class="e-market-name__hint-exchange-market">
            {{exchangeFullName}}
          </span>
        </div>
        <div class="e-market-name__hint-image" v-if="exchangeIcon">
          <e-icon :src="exchangeIcon"/>
        </div>
      </div>
    </v-popover>
  </div>
</template>

<script>
import { VPopover } from 'v-tooltip';
import { EXCHANGE_ETHERMIUM, EXCHANGE_BINANCE, EXCHANGE_HUOBI, EXCHANGE_IDEX, EXCHANGE_OKEX, EXCHANGE_FORKDELTA } from '@/util/constants';

// Components
import EIcon from '@/components/EIcon';

// Icons
import iconBinance from '@/icons/e-icon_binance.svg';
import iconEthermium from '@/icons/e-icon_ethermium.svg';
import iconHuobi from '@/icons/e-icon_huobi.svg';
import iconIDEX from '@/icons/e-icon_IDEX.svg';
import iconOkex from '@/icons/e-icon_okex.svg';

export default {
  name: 'e-market-name',

  data() {
    return {
      iconBinance,
      iconEthermium,
      iconHuobi,
      iconIDEX,
      iconOkex,
    };
  },

  props: {
    exchange: {
      type: String,
      default: 'ETM',
    },
  },

  computed: {
    exchangeIcon() {
      switch (this.exchange) {
        case EXCHANGE_BINANCE: return iconBinance;
        case EXCHANGE_ETHERMIUM: return iconEthermium;
        case EXCHANGE_HUOBI: return iconHuobi;
        case EXCHANGE_IDEX: return iconIDEX;
        case EXCHANGE_OKEX: return iconOkex;
        case EXCHANGE_FORKDELTA: return iconEthermium;
        default: return null;
      }
    },
    exchangeFullName() {
      switch (this.exchange) {
        case EXCHANGE_BINANCE: return 'Binance';
        case EXCHANGE_ETHERMIUM: return 'Ethermium';
        case EXCHANGE_HUOBI: return 'Huobi';
        case EXCHANGE_IDEX: return 'IDEX';
        case EXCHANGE_OKEX: return 'OKex';
        case EXCHANGE_FORKDELTA: return 'Forkdelta';
        default: return null;
      }
    },
  },

  components: {
    EIcon,
    VPopover,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-market-name {
  $this: &;

  display: inline-block;

  .trigger {
    outline: none;
  }

  &__exchange {
    cursor: pointer;
    display: inline-block;
    font-size: 11px;
    text-transform: uppercase;
    font-weight: 800;
    color: #0d105b;
    border: 1px solid #d9d9e2;
    border-radius: 4px;
    padding: 0 2px;
    line-height: 15px;
    margin-right: 2px;
    transition: border-color 0.15s ease-in-out, color 0.15s ease-in-out;
    position: relative;
    min-width: 31px;
    box-sizing: border-box;
    text-align: center;

    @include rmin(1921) {
      margin-right: 5px;
    }

    &:hover {
      border-color: #0e30e2;
      color: #0e30e2;

      #{$this}__hint {
        opacity: 1;
      }
    }
  }

  &__hint {
    width: 116px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 11px;
    line-height: 12px;
    opacity: 1;
    position: absolute;
    left: 0;
    top: 0;
    // transform: translate(-8px, calc(-100% - 7px));
    margin-top: -65px;
    margin-left: -22px;
    background: rgba(18, 51, 226, .9);
    border-radius: 4px;
    color: #fff;
    transition: opacity .15s ease-in-out;
    padding: 6px 6px 6px 8px;
    box-sizing: border-box;

    &:after {
      content: '';
      display: block;
      position: absolute;
      left: 9px;
      bottom: -4px;
      width: 0;
      height: 0;
      border-left: 4px solid transparent;
      border-right: 4px solid transparent;
      border-top: 4px solid rgba(18, 51, 226, .9);
    }
  }

  &__hint-exchange-market {
    display: block;
    font-size: 12px;
    font-weight: 900;
  }

  &__hint-image {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    background: #fff;
    border-radius: 50%;
    overflow: hidden;
  }
}
</style>



