<template>
  <div
    class="the-panel-right"
    :class="[ `the-panel-right__${theme}` ]"
  >
    <transition-dropdown>
      <div class="the-panel-right__body transition-dropdown-absolute transition-dropdown-up">
        <transition-list class="the-panel-right__body-list">
          <the-futures-order
            v-show="!orderBookToggler"
            :key="1"
            class="the-panel-right__order"
          />

          <div
            :key="2"
            class="the-panel-right__order-book"
          >
            <the-futures-order-book
              :orderBookState="orderBookToggler"
              @orderBookToggle="toggleOrderBookMode"
            />
          </div>
        </transition-list>
      </div>
    </transition-dropdown>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

import TheFuturesOrder from '@/containers/blocks/TheFuturesOrder';
import TheFuturesOrderBook from '@/containers/blocks/TheFuturesOrderBook';
import TheActiveToken from '@/containers/blocks/TheActiveToken';

import TransitionDropdown from '@/containers/transitions/TransitionDropdown';
import TransitionList from '@/containers/transitions/TransitionList';

export default {
  name: 'the-futures-panel-right',

  data() {
    return {
      tokensModeSelected: false,
      orderBookToggler: false,
    };
  },

  computed: {
    ...mapGetters({
      theme: 'ui/getTheme',
      baseToken: 'balance/baseToken',
    }),
  },

  methods: {
    toggleOrderBookMode() {
      this.orderBookToggler = !this.orderBookToggler;
    },
  },

  components: {
    TheFuturesOrder,
    TheFuturesOrderBook,
    TransitionDropdown,
    TransitionList,
    TheActiveToken,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-panel-right {
  $this: &;

  position: relative;
  height: 100%;
  transition: background-color .15s ease-in-out;

  @include r(1279) {
    height: auto;
  }

  &__body {
    position: relative;
    height: 100%;

    @include r(1279) {
      height: auto;
    }
  }

  &__body-list {
    display: flex;
    flex-flow: column;
    height: 100%;

    @include r(1279) {
      height: auto;
    }
  }

  &__order {
    z-index: 1;
  }

  &__order-book {
    flex: 1;
    overflow: hidden;
    background-color: #f4f5f9;
    transition: background-color .15s ease-in-out !important;

    @include r(1279) {
      height: 410px;
    }
  }

  &__tokens {
    padding: 0 18px 0 16px;
    position: relative;
    height: calc(100% - 88px);

    @include rhw(750, 1921) {
      height: calc(100% - 60px);
      padding-bottom: 20px;
    }

    @include r(1200) {
      height: calc(100% - 60px);
      padding-right: 8px;
      padding-bottom: 20px;
    }
  }

  &__dark {
    background: $dark-background;

    #{$this}__order-book {
      background: #1a1a1a;
    }
  }
}
</style>



