<template>
  <div class="the-news">
    <e-container-scroll
      v-if="!isLoading"
      ref="scroll"
      class="the-news__wrapper"
    >
      <e-news
        key="ledger nano s"
        is-pinned
        :time="dateToString(new Date('2018-12-09T10:57:00'))"
        link-address="https://twitter.com/ethermium/status/1071720403922489345"
        link-text="twitter.com"
        body-text="You can trade ANY ERC20 Token on EtherMium. No listing required."
      />
      <!-- TODO: Когда Марчел реализует выбранную новость, нужно будет доабвить :isPinned="i === 0" -->
      <e-news
        v-for="item in newsPage"
        :key="item.id"
        :time="item.publishedAt"
        :linkAddress="item.url"
        :linkText="item.source.domain"
        :bodyText="item.title"
        :positive="item.votes.positive"
        :negative="item.votes.negative"
        :important="item.votes.important"
        :liked="item.votes.liked"
        :lol="item.votes.lol"
        :saved="item.votes.saved"
        :disliked="item.votes.disliked"
      />
    </e-container-scroll>

    <div
      class="the-news__error"
      v-if="isLoading"
    >
      <e-panel-error :icon="errorIcon">
        <template slot="title">
          News
        </template>
        <template slot="descr">
          <template>
            No news about selected token
          </template>
        </template>
      </e-panel-error>
    </div>
  </div>
</template>

<script>
/**
 * Component: Компонент отображения новостей
 */

import { mapGetters } from 'vuex';

import { dateToString } from '@/util/dates';
import log from '@/util/logger';
import { getNews, removeNewsListener, onNewsChange, onReconnect, removeReconnectListener } from '@/api/backend';
import { onResume, removeResumeListener } from '@/util/IdleEmitter';
import { NEWS_PAGE_SIZE } from '@/config';

// Components
import EContainerScroll from '@/components/EContainerScroll';
import EPanelError from '@/components/EPanelError';
import ENews from '@/components/ENews';

import errorIcon from '@/icons/e-icon_empty-trades.svg';

export default {
  name: 'the-news',

  data() {
    return {
      /**
       * Флаг, устанавливается в true когда загружаются данные
       */
      isLoading: true,

      /**
       * Страница с новостями
       */
      newsPage: [],

      /**
       * Буфер для новостей
       */
      newsBuffer: [],
      errorIcon,
    };
  },

  computed: {
    ...mapGetters({
      activeTokenContract: 'tokens/activeTokenContract',
      isConnected: 'isConnected',
    }),
  },

  watch: {
    /**
     * Загрузка новостей из буфера при окончании загрузки страницы новостей
     */
    isLoading(val) {
      if (!val) {
        this.newsBuffer.forEach((news) => {
          this.pushNews(news);
        });

        // Обнуление буфер, после того как все новости добавлены
        this.newsBuffer = [];
      }
    },

    /**
     * Отслеживание смены активного токена
     */
    activeTokenContract(newVal, oldVAl) {
      if (newVal !== oldVAl) {
        this.loadNewsPage();
      }
    },
  },

  async mounted() {
    // Проверка наличия подписки на токен
    await Promise.all([
      this.$store.dispatch('tokens/subscribeToToken'),
    ]);
    this.loadNewsPage = this.loadNewsPage.bind(this);
    this.onNewsUpdate();
    this.loadNewsPage();
    onReconnect(this.loadNewsPage);
    onResume(this.loadNewsPage);
  },

  beforeDestroy() {
    removeResumeListener(this.loadNewsPage);
    removeReconnectListener(this.loadNewsPage);
    this.unubscribe();
  },

  methods: {
    dateToString,
    /**
     * Загрузка страницы с новостями
     */
    async loadNewsPage() {
      this.isLoading = true;
      try {
        this.newsPage = await getNews({
          tokenAddress: this.activeTokenContract,
          limit: NEWS_PAGE_SIZE - 1, // minus pinned hardcoded news
          offset: 0,
        });
      } catch (error) {
        if (this.isConnected) {
          log.error(`backend news unavailable: ${error}`, {
            module: 'news',
            layer: 'backend',
          });
        }
      }
      this.isLoading = false;
    },

    /**
     * Подписка на обновления новостей
     */
    onNewsUpdate() {
      try {
        onNewsChange((news) => {
          this.pushNews(news);
        });
      } catch (error) {
        if (this.isConnected) {
          log.error(`backend news updates unavailable: ${error}`, {
            module: 'news',
            layer: 'backend',
          });
        }
      }
    },

    /**
     * Отписка от обновлений новостей
     */
    unubscribe() {
      removeNewsListener();
    },

    /**
     * Добавление новости
     */
    pushNews(news) {
      if (this.activeTokenContract in news.tokens) {
        if (this.isLoading) {
          this.newsBuffer.push(news);
        } else {
          const index = this.newsPage.findIndex(item => item.id === news.id);
          if (index !== -1) {
            this.newsPage[index] = news;
          } else {
            if (this.newsPage && this.newsPage.langth === NEWS_PAGE_SIZE) {
              // Случай когда у нас уже полная старница новостей. Нужно удалить последнюю новость и добавть новость в начало
              this.newsPage.splice(-1, 1);
            }
            this.newsPage.unshift(news);
          }
        }
      }
    },
  },

  components: {
    EContainerScroll,
    EPanelError,
    ENews,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-news {
  position: relative;
  height: calc(100% - 27px);

  &__wrapper {
    margin-top: 4px;
    padding: 0 3px 4px 4px;
  }

  &__error {
    height: 100%;
    width: 100%;
    padding-right: 10px;
    box-sizing: border-box;
  }

  .e-container-scroll {
    height: calc(100% - 14px);
  }
}
</style>



