/**
 * Хелпер: Сокращает строку с адресом добавляя троеточие
 *
 * @param {string} adress - Строка для сокращения
 * @param {number} [left=10] - Количество символов, которые нужно оставить слева
 * @param {number} [right=10] - Количество символов, которые нужно оставить справа
 * @returns {string}
 *
 * @version 1.0.0
 */
export default (address, left = 10, right = 10) => (address
  ? `${address.slice(0, left)}...${address.slice(address.length - right, address.length)}`
  : '');



