<template>
  <div></div>
</template>

<script>
/* eslint-disable */
import { BigNumber } from 'bignumber.js';
import { mapGetters, mapState } from 'vuex';
import { FUTURES_PRICE_DECIMALS, FUTURES_AMOUNT_DECIMALS } from '@/util/constants';

import historyProvider from '@/api/datafeed/historyProvider';
import { onTradeChange, removeTradeChangeHandler } from '@/api/backend/futures/trades';
import { onResume, removeResumeListener } from '@/util/IdleEmitter';
import { onReconnect, removeReconnectListener } from '@/api/backend';

const timezonesOffsets = {
  '300:240': 'America/New_York',
  '480:420': 'America/Los_Angeles',
  '420:420': 'America/Phoenix',
  '360:300': 'America/Chicago',
  '180:180': 'America/Argentina/Buenos_Aires',
  '360:360': 'America/El_Salvador',
  '180:120': 'America/Sao_Paulo',
  '300:300': 'America/Bogota',
  '240:240': 'America/Caracas',
  '600:600': 'Pacific/Honolulu',
  '420:360': 'US/Mountain',
  '-180:-240': 'Europe/Moscow',
  '-120:-180': 'Europe/Athens',
  '-60:-120': 'Europe/Berlin',
  '0:-60': 'Europe/London',
  '-660:-600': 'Australia/Sydney',
  '-600:-600': 'Australia/Brisbane',
  '-630:-570': 'Australia/Adelaide',
  '-360:-360': 'Asia/Almaty',
  '-300:-300': 'Asia/Ashkhabad',
  '-540:-540': 'Asia/Tokyo',
  '-480:-480': 'Asia/Taipei',
  '-210:-270': 'Asia/Tehran',
  '-240:-240': 'Asia/Dubai',
  '-330:-330': 'Asia/Kolkata',
  '-420:-420': 'Asia/Bangkok',
  '-780:-720': 'Pacific/Auckland',
  '-765:-825': 'Pacific/Chatham',
  '-780:-780': 'Pacific/Fakaofo',
  '-120:-120': 'Africa/Johannesburg',
  '-345:-345': 'Asia/Kathmandu',
};

const overridesLight = {
  'paneProperties.background': '#f8f9fc',
  'paneProperties.crossHairProperties.color': '#989898',
  'mainSeriesProperties.candleStyle.upColor': '#6FB36F',
  'mainSeriesProperties.candleStyle.borderUpColor': '#278947',
  'mainSeriesProperties.candleStyle.downColor': '#EB6265',
  'mainSeriesProperties.candleStyle.borderDownColor': '#CA4144',
  'scalesProperties.lineColor': '#E9ECF2',
  'mainSeriesProperties.areaStyle.color1': '#92AEC8',
  'mainSeriesProperties.areaStyle.color2': '#f8f9fc',
  'mainSeriesProperties.areaStyle.linewidth': 2,
  'mainSeriesProperties.areaStyle.linecolor': '#4A6E8E',
  'mainSeriesProperties.lineStyle.color': '#4A6E8E',
  'mainSeriesProperties.lineStyle.linewidth': 2,
  'mainSeriesProperties.baselineStyle.topLineWidth': 2,
  'mainSeriesProperties.baselineStyle.bottomLineWidth': 2,
  'mainSeriesProperties.baselineStyle.topFillColor2': 'rgba(111, 179, 111, .1)',
  'mainSeriesProperties.baselineStyle.bottomFillColor1': 'rgba(111, 179, 111, .1)',
  'mainSeriesProperties.baselineStyle.bottomFillColor2': '#CA4144',
  'mainSeriesProperties.baselineStyle.topLineColor': '#278947',
  'mainSeriesProperties.baselineStyle.bottomLineColor': '#CA4144',
  'paneProperties.vertGridProperties.color': '#e4eaee',
  'paneProperties.horzGridProperties.color': '#e4eaee',
  'mainSeriesProperties.candleStyle.wickUpColor': '#278947',
  'mainSeriesProperties.candleStyle.wickDownColor': '#CA4144',
  'mainSeriesProperties.hollowCandleStyle.wickUpColor': '#278947',
  'mainSeriesProperties.hollowCandleStyle.wickDownColor': '#CA4144',
  'scalesProperties.backgroundColor': '#CA4144',
  'scalesProperties.textColor': '#5c5c5c',
};

const overridesDark = {
  'paneProperties.background': '#121722',
  'paneProperties.crossHairProperties.color': '#DC4727',
  'mainSeriesProperties.candleStyle.upColor': '#00D89A',
  'mainSeriesProperties.candleStyle.borderUpColor': '#00D89A',
  'mainSeriesProperties.candleStyle.downColor': '#DC4727',
  'mainSeriesProperties.candleStyle.wickUpColor': '#00D89A',
  'mainSeriesProperties.candleStyle.wickDownColor': '#DC4727',
  'mainSeriesProperties.candleStyle.borderDownColor': '#DC4727',
  'mainSeriesProperties.hollowCandleStyle.wickUpColor': '#00D89A',
  'mainSeriesProperties.hollowCandleStyle.wickDownColor': '#DC4727',
  'mainSeriesProperties.areaStyle.color1': '#92AEC8',
  'mainSeriesProperties.areaStyle.color2': '#f8f9fc',
  'mainSeriesProperties.areaStyle.linewidth': 2,
  'mainSeriesProperties.areaStyle.linecolor': '#6191bb',
  'mainSeriesProperties.lineStyle.color': '#6191bb',
  'mainSeriesProperties.lineStyle.linewidth': 2,
  'mainSeriesProperties.baselineStyle.topLineWidth': 2,
  'mainSeriesProperties.baselineStyle.bottomLineWidth': 2,
  'mainSeriesProperties.baselineStyle.topFillColor2': 'rgba(111, 179, 111, .1)',
  'mainSeriesProperties.baselineStyle.bottomFillColor1': 'rgba(111, 179, 111, .1)',
  'mainSeriesProperties.baselineStyle.bottomFillColor2': '#DC4727',
  'mainSeriesProperties.baselineStyle.topLineColor': '#00D89A',
  'mainSeriesProperties.baselineStyle.bottomLineColor': '#DC4727',
  'paneProperties.vertGridProperties.color': '#282C38',
  'paneProperties.horzGridProperties.color': '#282C38',
  'scalesProperties.backgroundColor': '#DC4727',
  'scalesProperties.lineColor': '#121722',
  'scalesProperties.textColor': '#d0d0d0',
};

const studiesOverridesLight = {
  'volume.volume.color.0': '#DC4727',
  'volume.volume.color.1': '#00D89A',
};

const supportedResolutions = ['1', '5', '15', '30', '60'];

const config = {
  supported_resolutions: supportedResolutions,
};

export default {
  name: 'the-futures-tradingview-staging',

  data() {
    return {
      //  Bars = 0, Candles = 1, Line = 2, Area = 3, Heiken Ashi = 8, Hollow Candles = 9, Renko = 4, Kagi = 5
      // Point&Figure = 6, Line Break = 7, Baseline = 10
      graphicType: 1,
      handleNewTrade: null,
      onRealtimeCallback: null,
      lastBar: null,
      resolution: null,
      positionsMap: [],
    };
  },

  computed: {
    lastTrade() {
      return this.$store.state.futuresTrades.latestTrades[0];
    },

    ...mapGetters({
      activeTokenContract: 'tokens/activeTokenContract',
      activeContractName: 'futuresActiveContract/contractName',
      theme: 'ui/getTheme',
    }),

    ...mapState({
      activeContract: state => state.futuresActiveContract.activeContract,
      positions: state => state.futuresPositions.positions || [],
    }),
  },

  watch: {
    activeContract() {
      if (this.tvWidget) {
        this.tvWidget.setSymbol(this.activeContract.asset, this.tvWidget.symbolInterval().interval);
      }

      this.lastBar = null;
    },
    theme() {
      if (this.theme === 'light') {
        this.tvWidget.changeTheme('light');
        this.tvWidget.applyOverrides(overridesLight);
        this.tvWidget.applyStudiesOverrides(studiesOverridesLight);
      }

      if (this.theme === 'dark') {
        this.tvWidget.changeTheme('dark');
        this.tvWidget.applyOverrides(overridesDark);
      }
    },

    positions() {
      if (window.tvWidget) {
        //this.setPositions();
      };
    },
  },

  beforeDestroy() {
    if (window.tvWidget) {
      window.tvWidget.remove();
    }
    window.tvWidget = undefined;
    removeTradeChangeHandler(this.handleNewTrade);
    this.handleNewTrade = null;
    this.onRealtimeCallback = null;
  },

  mounted() {
    if (!window.TradingViewLib) {
      const chartingLibrary = document.createElement('script');
      const chartingPolyfills = document.createElement('script');
      const chartingBundle = document.createElement('script');

      chartingLibrary.setAttribute('src', '/static/charting_library/charting_library.min.js');
      chartingPolyfills.setAttribute('src', '/static/datafeeds/udf/dist/polyfills.js');
      chartingBundle.setAttribute('src', '/static/datafeeds/udf/dist/bundle.js');

      document.head.appendChild(chartingLibrary);
      document.head.appendChild(chartingPolyfills);
      document.head.appendChild(chartingBundle);
    }


    const tradingViewInitInterval = setInterval(() => {
      if (window.TradingViewLib && window.Datafeeds) {
        clearInterval(tradingViewInitInterval);
        this.tradingViewInit();
      }
    }, 100);


    onReconnect(this.tradingViewInit);
    onResume(this.tradingViewInit);

    this.handleNewTrade = (tradeEvt) => {
      if (this.onRealtimeCallback && tradeEvt.data.futures_asset_name === this.activeContract.asset) {
        const lastPrice = BigNumber(tradeEvt.data.price).shiftedBy(-FUTURES_PRICE_DECIMALS);
        const lastBar = { ...this.lastBar };


        const lastTradeVolume = tradeEvt.data.price/1e8*tradeEvt.data.amount_filled/1e8;

        const segment = BigNumber(parseInt(this.resolution)*60*1e3);
        const newBarTime = new BigNumber(new Date()/1).idiv(segment).times(segment);

        // console.log('newBarTime', newBarTime.toFixed(0));
        // console.log('segment', segment.toString());
        // console.log('time', new Date()/1);
        // console.log('lastTrade', tradeEvt.data);
        // console.log('lastTradeVolume', lastTradeVolume);


        if (BigNumber(lastBar.time).lt(newBarTime))
        {

          const newBar = {
            time: newBarTime.toFixed(0),
            open: lastPrice.toFixed(),
            high: lastPrice.toFixed(),
            low: lastPrice.toFixed(),
            close: lastPrice.toFixed(),
            volume: lastTradeVolume
          }

          this.lastBar = newBar;
          this.onRealtimeCallback({ ...newBar });
        }
        else
        {

          lastBar.close = lastPrice.toFixed();

          if (lastPrice.gt(new BigNumber(lastBar.high))) {
            lastBar.high = lastPrice.toFixed();
          }
          if (lastPrice.lt(new BigNumber(lastBar.low))) {
            lastBar.low = lastPrice.toFixed();
          }

          lastBar.volume += lastTradeVolume;

          this.lastBar = lastBar;
          this.onRealtimeCallback({ ...lastBar });
        }


      }
    };

    onTradeChange(this.handleNewTrade);
  },

  methods: {
    getTimezone() {
      const winterOffset = new Date('2010-01-01T00:00:00').getTimezoneOffset();
      const summerOffset = new Date('2010-06-01T00:00:00').getTimezoneOffset();
      return timezonesOffsets[`${winterOffset}:${summerOffset}`];
    },

    setPositions() {
      window.tvWidget.chart().removeAllShapes();
      this.positionsMap.forEach(position => {
        if (position) {
          position.remove();
        }
      });

      this.positionsMap = this.positions.map(position => {
        if (position.symbol === this.activeContractName.replace(/USD|USDT/g, '')) {
          return window.tvWidget ? window.tvWidget.chart().createPositionLine()
              .setText(position.side ? `LONG` : `SHORT`)
              .setQuantity(position.size.shiftedBy(-FUTURES_AMOUNT_DECIMALS).toString())
              .setPrice(+position.price.shiftedBy(-FUTURES_PRICE_DECIMALS).toString())
              .setExtendLeft(true)
              .setLineStyle(0)
              .setLineColor(position.side ? '#0097FE' : '#0097FE')
              .setBodyBackgroundColor(position.side ? '#0097FE' : '#0097FE')
              .setBodyBorderColor(position.side ? '#0097FE' : '#0097FE')
              .setQuantityBorderColor(position.side ? '#0097FE' : '#0097FE')
              .setQuantityBackgroundColor(position.side ? '#0097FE' : '#0097FE')
              .setQuantityFont('"DIN Pro", "Arial", sans-serif')
              .setBodyFont('"DIN Pro", "Arial", sans-serif')
              .setBodyTextColor('#000')
            : null;
        }
      });
    },

    tradingViewInit() {
      const activeContract = this.activeContract;
      const futuresAssetName = this.activeContract.asset;
      const overrides = this.theme === 'light' ? overridesLight : overridesDark;
      const studiesOverrides = studiesOverridesLight;

      const dataFeedOpt = {
        onReady: (cb) => {
          setTimeout(() => cb(config), 0);
        },

        searchSymbols: () => {},

        resolveSymbol: (symbolName, onSymbolResolvedCallback) => {
          const symbolStub = {
            name: symbolName,
            description: '',
            type: 'crypto',
            session: '24x7',
            timezone: 'Etc/UTC',
            ticker: symbolName,
            exchange: 'ETM',
            minmov: 1,
            pricescale: 10 ** activeContract.decimals,
            has_intraday: true,
            intraday_multipliers: ['1', '5', '15', '30', '60'],
            supported_resolution: supportedResolutions,
            volume_precision: 8,
            data_status: 'streaming',
          };

          setTimeout(() => { onSymbolResolvedCallback(symbolStub); }, 0);
        },

        getBars: (symbolInfo, resolution, from, to, onHistoryCallback, onErrorCallback) => {
          historyProvider.getBars(resolution, from, to, symbolInfo)
            .then((bars) => {
              for (let i = 0; i < bars.length; i++) {
                 bars[i].low = parseFloat(bars[i].low);
                 bars[i].high = parseFloat(bars[i].high);
                 bars[i].open = parseFloat(bars[i].open);
                 bars[i].close = parseFloat(bars[i].close);
              }

              this.resolution = resolution;
              window.localStorage.setItem('CHART_RESOLUTION', resolution);

              setTimeout(() => {
                if (
                  this.lastTrade &&
                  symbolInfo.name === this.lastTrade.futuresAssetName &&
                  bars.length &&
                  (!this.lastBar || (this.lastBar && this.lastBar.time < bars[bars.length - 1].time))
                ) {
                  this.lastBar = { ...bars[bars.length - 1] };
                }

                const barsList = [...bars];

                if (barsList.length && (!this.lastBar || (this.lastBar && this.lastBar.time <= bars[bars.length - 1].time))) {
                  const lastTradePrice = this.lastTrade.price.shiftedBy(-FUTURES_PRICE_DECIMALS).toFixed();

                  barsList[barsList.length - 1].close = lastTradePrice;
                  if (lastTradePrice > barsList[barsList.length - 1].high) {
                    barsList[barsList.length - 1].high = lastTradePrice;
                  }
                  if (lastTradePrice < barsList[barsList.length - 1].low) {
                    barsList[barsList.length - 1].low = lastTradePrice;
                  }
                }


                onHistoryCallback(barsList, { noData: barsList.length === 0 });
              }, 0);
            }).catch((err) => {
              onErrorCallback(err);
            });
        },

        subscribeBars: (symbolInfo, resolution, onRealtimeCallback) => {
          this.onRealtimeCallback = onRealtimeCallback;
        },

        unsubscribeBars: () => {},

        // eslint-disable-next-line arrow-body-style
        calculateHistoryDepth: (resolution) => {
          // while optional, this makes sure we request 24 hours of minute data at a time
          // CryptoCompare's minute data endpoint will throw an error if we request data beyond 7 days in the past, and return no data
          return resolution < 60 ? { resolutionBack: 'D', intervalBack: '1' } : undefined;
        },
      };

      try {
        this.graphicType = this.tvWidget.chart().chartType();
      } catch (e) {
        this.graphicType = 1;
      }

      const graphicType = this.graphicType;

      const intialResolution =  window.localStorage.getItem('CHART_RESOLUTION') ? window.localStorage.getItem('CHART_RESOLUTION') : '60';

      const defaultProps = {
        symbol: futuresAssetName,
        interval: intialResolution,
        containerId: 'tv_chart_container',
        libraryPath: '/static/charting_library/',
        fullscreen: false,
        autosize: true,
        clientId: 'tradingview.com',
        userId: 'public_user_id',
        studiesOverrides: {},
      };

      const widgetOptions = {
        width: '100%',
        height: '100%',
        symbol: futuresAssetName,
        datafeed: dataFeedOpt,
        debug: false,
        editorFontsList: ['Arial', 'sans-serif'],
        interval: defaultProps.interval,
        container_id: defaultProps.containerId,
        library_path: defaultProps.libraryPath,
        //timeframe: '5D',
        timezone: this.getTimezone(),
        locale: 'en',
        disabled_features: [
          'use_localstorage_for_settings',
          'header_symbol_search',
          'header_undo_redo',
          'header_compare',
          'header_saveload',
          'header_settings',
          'study_buttons_in_legend',
          'format_button_in_legend',
          'show_hide_button_in_legend',
          'delete_button_in_legend',
          'control_bar',
          'side_toolbar_in_fullscreen_mode'
        ],
        user_id: defaultProps.userId,
        fullscreen: defaultProps.fullscreen,
        autosize: defaultProps.autosize,
        loading_screen: { backgroundColor: "#121722" },
        overrides: { 'mainSeriesProperties.style': graphicType, ...overrides },
        studies_overrides: { ...studiesOverrides },
      };

      // eslint-disable-next-line new-cap,no-unused-vars
      this.tvWidget = new window.TradingViewLib.widget(widgetOptions);

      // eslint-disable-next-line
      window.tvWidget = this.tvWidget;

      // this.tvWidget.onChartReady(() => {
      //   this.setPositions();
      // });
    },
  },
};
</script>

<style lang='scss'>
@import '~@/scss/styles';

.the-trading-view {
  position: relative;
  height: 100%;

  &.staging {
    border: 1px solid #DBDCDD;
    border-radius: 3px;
    box-sizing: border-box;
  }

  &__chart {
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 21;
  }

  &.the-trading-view__dark {
    &.staging {
      border: 1px solid #292d3d;
    }
  }
}
</style>




