<script>
// import { BigNumber } from 'bignumber.js';

import { mapGetters, mapState } from 'vuex';
import moment from 'moment';
import { ASYNC_STATES, ETH_ADDRESS } from '@/util/constants';

import { dateToString } from '@/util/dates';
import { onReconnect, removeReconnectListener } from '@/api/backend';
import { setTourStep } from '@/api/backend/statistics';
import { onResume, removeResumeListener } from '@/util/IdleEmitter';

// Components
import TheSendETHPlaceholder from '@/containers/blocks/TheSendETHPlaceholder';
import EBigNumber from '@/components/EBigNumber';
import EContainerScroll from '@/components/EContainerScroll';
import EPanelError from '@/components/EPanelError';
import EPreloader from '@/components/EPreloader';
import EIcon from '@/components/EIcon/EIcon';
import ETooltip from '@/components/ETooltip';
import EDemo from '@/components/EDemo';

import errorIcon from '@/icons/e-icon_empty-trades.svg';
import iconClose from '@/icons/e-icon_close.svg';
import iconEdit from '@/icons/e-icon__edit.svg';

export default {
  name: 'the-futures-positions',

  data() {
    return {
      errorIcon,
      iconClose,
      iconEdit,
      reconnectHandler: null,
      fetchIntervalId: null,
    };
  },

  computed: {
    ...mapGetters({
      activeWallet: 'wallets/activeWallet',
      demoStep: 'ui/demoStep',
      activeContractName: 'futuresActiveContract/contractName',
      contractBaseToken: 'futuresActiveContract/contractBaseToken',
      ethPrice: 'tokens/ethPrice',
      isConnected: 'isConnected',
      theme: 'ui/getTheme',
      windowSize: 'ui/windowSize',
      baseToken: 'balance/baseToken',
    }),

    ...mapState({
      activeContract: state => state.futuresActiveContract.activeContract,
      fetchPositionsState: state => state.futuresPositions.fetchPositionsState,
      positions: state => state.futuresPositions.positions,
    }),

    isBaseTokenETH() {
      return this.baseToken === ETH_ADDRESS;
    },

    decimalBalance() {
      return this.isBaseTokenETH ? 6 : 2;
    },

    isLoading() {
      return this.fetchPositionsState === ASYNC_STATES.PENDING;
    },
  },

  watch: {
    /**
     * Отслеживание смены активного токена
     */
    activeContract(newVal, oldVAl) {
      if (newVal !== oldVAl) {
        this.$store.dispatch('futuresPositions/fetchPositions');
      }
    },
  },

  mounted() {
    this.$store.dispatch('futuresPositions/fetchPositions');
    this.startReconnectListener();

    this.fetchIntervalId = setInterval(() => this.$store.dispatch('futuresPositions/fetchPositions'), 5e3);
  },

  beforeDestroy() {
    this.stopReconnectListener();
    this.$store.commit('futuresPositions/SET_POSITION_TO_CLOSE', null);
    this.$store.commit('futuresPositions/SET_PREVIOUS_CONTRACT_HASH', null);

    clearInterval(this.fetchIntervalId);
  },

  methods: {
    dateToString,

    setMarginPosition(position) {
      this.$store.commit('futuresPositions/SET_MARGIN_POSITION', position);
    },

    settleClickHandler(futuresContractHash, position, activeWallet) {
      if (this.demoStep) {
        setTourStep({ walletAddress: activeWallet.address, step: 11 });
        this.$store.commit('ui/SET_DEMO_STEP', 12);
      }

      // window.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
      this.$scrollTo('body');

      if (futuresContractHash !== this.activeContract.futuresContractHash) {
        this.$store.commit('futuresPositions/SET_PREVIOUS_CONTRACT_HASH', this.activeContract.futuresContractHash);
      }

      this.$store.commit('futuresPositions/SET_POSITION_TO_CLOSE', position);
      this.$router.push({ name: 'future', params: { contract: futuresContractHash } });
    },

    startReconnectListener() {
      this.reconnectHandler = () => {
        this.$store.dispatch('futuresPositions/fetchPositions');
      };
      onReconnect(this.reconnectHandler);
      onResume(this.reconnectHandler);
    },

    stopReconnectListener() {
      if (!this.reconnectHandler) {
        return;
      }
      removeReconnectListener(this.reconnectHandler);
      removeResumeListener(this.reconnectHandler);
    },

    handleSelect(futuresContractHash) {
      this.$store.commit('futuresPositions/SET_POSITION_TO_CLOSE', null);
      this.$store.commit('futuresPositions/SET_PREVIOUS_CONTRACT_HASH', null);
      this.$router.push({ name: 'future', params: { contract: futuresContractHash } });
    },

    formatExpiresToTime(seconds) {
      return moment().add(seconds, 'seconds').format('MMM DD @ HH:mm');
    },
  },

  components: {
    EIcon,
    EBigNumber,
    EContainerScroll,
    EPanelError,
    EPreloader,
    ETooltip,
    EDemo,
    TheSendETHPlaceholder,
  },
};
</script>

