  &__light {
    &::-webkit-scrollbar-track {
      background-color: $color-violet;
    }

    &::-webkit-scrollbar-thumb {
      background-color: $color-blue;
    }

    #{$this}__collateral:hover {
      svg .fill {
        fill: $color-blue-dark-mode;
      }
    }

    #{$this}__collateral-edit {
      svg .fill {
        fill: $color-violet-bright;
      }
    }

    #{$this}__table {
      color: $color-light-grey;
    }

    #{$this}__table-head {
      background: #FBFBFD;
      border-color: #F0F6F9;

      #{$this}__table-col {
        border-color: #F0F6F9;
      }
    }

    #{$this}__table-body-item {
      color: #09168a;
      background: #fff;
      border-color: #F0F6F9;
    }

    #{$this}__table-col {
      border-color: #F0F6F9;
    }

    #{$this}__symbol {
      color: #0D105B;
      border-color: rgba(92,96,98,.3);
    }

    #{$this}__base {
      // background: rgba(92,96,98,.1);
      color: #8D9098;
    }

    #{$this}__table-col-asset {
      color: #09168a;

      &:hover {
        background: #fafafd;
      }
    }

    #{$this}__price,
    #{$this}__collateral {
      color: #09168a;
    }

    #{$this}__price .e-big-number,
    #{$this}__collateral .e-big-number {
      color: #09168a;
    }

    #{$this}__label {
      color: #fff;
    }

    #{$this}__date {
      color: #8384AB;
    }

    #{$this}__date-expires-expired {
      background: rgba(220,71,39,.1);
    }

    #{$this}__size-settle,
    #{$this}__profit {
      color: #1FCE43;
    }
  }
}
</style>



