<template>
  <div
    class="the-active-token"
    :class="[!isLogin ? 'is-preview' : '', `the-active-token__${theme}`]"
  >
    <div
      class="the-active-token__balance"
      @click="handleMyWallet"
    >
      <div class="the-active-token__balance-item">
        <div class="the-active-token__balance-amount">
          <div class="the-active-token__balance-value">
            <e-icon
              v-if="isPrivateBalanceNotReady"
              :src="iconDottedAnimation"
              class="the-active-token__dotted-animation"
            />

            <template v-else>
              <e-big-number
                :value="tokenPrivateBalance"
                :decimals="activeToken.decimals"
                :decimal-places="activeToken.amount_dec"
              />
              <span class="the-active-token__balance-token">{{ activeToken.shortName }}</span>
            </template>
          </div>
          <div class="the-active-token__balance-label">
            {{ walletBalanceName }} balance
          </div>
        </div>
      </div>
      <div class="the-active-token__switcher">
        <div class="the-wallet-new__switcher-wrapper">
          <div class="the-active-token__switcher-button left" @click.stop="startWithdrawal">
            <e-icon :src="$options.icons.iconArrow" class="the-active-token__switcher-icon left" />
            <div class="the-active-token__switcher-tooltip left">
              Withdrawal to {{ walletBalanceName }}
            </div>
          </div>

          <div class="the-active-token__switcher-button right" @click.stop="startDeposit">
            <e-icon :src="$options.icons.iconArrow" class="the-active-token__switcher-icon right" />
            <div class="the-active-token__switcher-tooltip right">Deposit to Contract</div>
          </div>
        </div>

        <div class="the-active-token__switcher-loading">
          <e-icon
            v-if="anyBalanceNotReady"
            :src="$options.icons.iconLoading"
            class="the-active-token__switcher-icon-loading"
          />
          <span
            v-else
            @click.stop="refreshBalance"
            class="the-active-token__switcher-title"
          >REFRESH</span>
        </div>
      </div>

      <div class="the-active-token__balance-item contract-balance" @click="setAmount">
        <div class="the-active-token__balance-amount right">
          <div class="the-active-token__balance-value">
            <e-icon
              v-if="isContractBalanceNotReady"
              :src="iconDottedAnimation"
              class="the-active-token__dotted-animation"
            />

            <template v-else>
              <e-big-number
                :value="tokenContractBalance"
                :decimals="activeToken.decimals"
                :decimal-places="activeToken.amount_dec"
              />
              <span class="the-active-token__balance-token">{{ activeToken.shortName }}</span>
            </template>
          </div>
          <div class="the-active-token__balance-label">Contract balance</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import balanceMixin from '@/util/mixins/balanceMixin';
import { routesMixin } from '@/util/mixins';
import { ASYNC_STATES } from '@/util/constants';
import log from '@/util/logger';

// Components
import EIcon from '@/components/EIcon';
import ETokenIcon from '@/components/ETokenIcon';
import EBigNumber from '@/components/EBigNumber';

// Icons
import iconDottedAnimation from '@/icons/e-icon_dotted-animation.svg';
import iconArrowDowner from '@/icons/e-icon__arrow-thin.svg';
import iconWallet from '@/icons/e-icon__wallet-thin.svg';
import iconWalletDefault from '@/icons/e-icon__wallet.svg';
import iconToken from '@/icons/e-icon__token-thin.svg';
import iconArrow from '@/icons/e-icon_arrow_right.svg';
import iconLoading from '@/icons/e-icon__loading.svg';

export default {
  name: 'the-active-token',

  data() {
    return {
      privateBalanceRefreshState: ASYNC_STATES.SUCCESS,
      contractBalanceRefreshState: ASYNC_STATES.SUCCESS,
    };
  },

  computed: {
    anyBalanceNotReady() {
      return this.isPrivateBalanceNotReady || this.isContractBalanceNotReady;
    },

    isPrivateBalanceNotReady() {
      return this.$store.state.balance.tokenInitStatus === ASYNC_STATES.PENDING || this.privateBalanceRefreshState === ASYNC_STATES.PENDING;
    },

    isContractBalanceNotReady() {
      return this.$store.state.balance.tokenInitStatus === ASYNC_STATES.PENDING || this.contractBalanceRefreshState === ASYNC_STATES.PENDING;
    },

    buyOrSell() {
      return this.$store.state.activeOrder.buyOrSell;
    },

    ...mapGetters({
      walletBalanceName: 'wallets/walletBalanceName',
      baseToken: 'balance/baseToken',
      activeToken: 'tokens/activeToken',
      tokenPrivateBalance: 'balance/activeTokenPrivateBalance',
      tokenContractBalance: 'balance/activeTokenContractBalance',
      isLogin: 'wallets/isLogin',
      theme: 'ui/getTheme',
    }),
  },

  watch: {
    tokenContractBalance() {
      this.refreshContractBalance();
    },
  },

  created() {
    Object.assign(this, {
      iconArrowDowner,
      iconWallet,
      iconToken,
      iconDottedAnimation,
    });
    this.startListeningToTokenBalance();
  },

  beforeDestroy() {
    this.stopListeningToTokenBalance();
  },

  icons: {
    iconWalletDefault,
    iconArrow,
    iconLoading,
  },

  methods: {
    startDeposit() {
      if (this.isLogin) {
        this.$store.dispatch('deposit/startDeposit', this.activeToken.contract);
      } else {
        this.$router.push({ name: 'signup' });
      }
    },

    startWithdrawal() {
      if (this.isLogin) {
        this.$store.dispatch('deposit/startWithdrawal', this.activeToken.contract);
      } else {
        this.$router.push({ name: 'signup' });
      }
    },

    refreshBalance() {
      if (this.isLogin) {
        this.refreshPrivateBalance();
        this.refreshContractBalance();
      } else {
        this.$router.push({ name: 'signup' });
      }
    },

    async refreshPrivateBalance() {
      const balanceApi = this.$store.getters['blockchain/balanceApi'];
      this.privateBalanceRefreshState = ASYNC_STATES.PENDING;

      let balance;
      try {
        balance = await balanceApi.getPrivateToken({ tokenAddress: this.activeToken.contract });
      } catch (e) {
        const tokenName = this.activeToken.shortName;
        // TODO: add error indicator
        log.warn(`Error @ Refresh ${tokenName} private balance button: ${e.message}`);
      }

      if (balance) {
        this.$store.commit('balance/UPDATE_PRIVATE_BALANCE', { tokens: { [this.activeToken.contract]: balance } });
      }
      this.privateBalanceRefreshState = ASYNC_STATES.SUCCESS;
    },

    async refreshContractBalance() {
      const balanceApi = this.$store.getters['blockchain/balanceApi'];
      this.contractBalanceRefreshState = ASYNC_STATES.PENDING;

      let balance;
      try {
        balance = await balanceApi.getTradableToken({ tokenAddress: this.activeToken.contract });
      } catch (e) {
        const tokenName = this.activeToken.shortName;
        // this.$store.commit('ui/ADD_NOTIFICATION', {
        //   status: 'warning',
        //   headline: 'Refresh failed',
        //   message: `${tokenName} Contract balance refresh failed`,
        // }, { root: true });
        log.warn(`Error @ Refresh ${tokenName} contract balance Button: ${e.message}`);
      }

      if (balance) {
        this.$store.commit('balance/UPDATE_TRADABLE_BALANCE', { balance: { [this.activeToken.contract]: balance } });
      }
      this.contractBalanceRefreshState = ASYNC_STATES.SUCCESS;
    },

    handleMyWallet() {
      if (!this.isLogin) {
        this.$router.push({ name: 'signup' });
      }
    },

    setAmount() {
      if (this.isLogin && this.tokenContractBalance && this.buyOrSell === 'sellToken') {
        this.$store.commit('activeOrder/SET_CREATE_ORDER_NUMBERS', {
          amount: this.tokenContractBalance.shiftedBy(-this.activeToken.decimals).toFixed(9, 1),
        });
      }
    },
  },

  components: {
    EBigNumber,
    EIcon,
    ETokenIcon,
  },

  mixins: [routesMixin, balanceMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-active-token {
  $this: &;

  background-color: #f9f9fb;
  box-sizing: border-box;
  height: 56px;
  box-shadow: inset 0 -1px 0 0 rgba(#DBE1E5, 1);
  display: flex;
  justify-content: space-between;
  transition: all .15s ease-in-out;

  &.is-preview {
    justify-content: space-between;
  }

  @include r(1200) {
    height: 60px;
    /*padding: 15px 21px 5px 16px;*/
  }

  @include rhw(750, 1921) {
    height: 60px;
    /*padding: 15px 21px 5px 16px;*/
  }

  @include rmin(1921) {
    // box-shadow: none;
    background-color: transparent;
    justify-content: space-between;
    /*padding: 31px 11px 5px 15px;*/
    height: 86px;
  }

  &__balance {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 16px;
    box-sizing: border-box;
  }

  &__balance-item {
    min-width: 110px;
    display: flex;
    align-items: center;

    &.contract-balance {
      cursor: pointer;
    }
  }

  &__balance-icon {
    width: 29px;
    height: 26px;

    &.left {
      margin-right: 10px;
    }

    &.right {
      margin-left: 10px;
    }

    .e-icon__wallet-colored {
      fill: #275379;
    }

    &.metamask {
      .e-icon__wallet-colored {
        fill: #F4852D;
      }
    }
  }

  &__balance-amount {
    line-height: 15px;

    &.right {
      text-align: right;

      #{$this}__balance-value {
        justify-content: flex-end;
      }
    }
  }

  &__balance-value {
    display: flex;
    color: #0D105C;
    font-size: 12px;
    letter-spacing: 0;
    font-weight: 600;
    min-width: 108px;

    .e-big-number {
      padding-right: 4px;
    }
  }

  &__balance-label {
    color: #0D105C;
    opacity: .4;
    font-weight: 500;
    font-size: 9px;
    text-transform: uppercase;
    transition: all .15s ease-in-out;
  }

  &__switcher {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__switcher-wrapper {
    display: flex;
    margin: 0 20px;
  }

  &__switcher-loading {
    cursor: pointer;
    font-size: 8px;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    color: #1F2EE9;
    padding-top: 4px;

    .e-icon {
      margin-right: 3px;
    }

    &:hover {
      #{$this}__switcher-title {
        opacity: .6;
      }
    }
  }

  &__switcher-title {
    transition: opacity .2s ease-in-out;
  }

  &__switcher-icon-loading {
    width: 12px;
    height: 12px;
  }

  &__switcher-button {
    width: 27px;
    height: 22px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    border: 2px solid #a5abf6;
    transition: all .2s ease-in-out;
    position: relative;
    cursor: pointer;

    &.left {
      border-radius: 40px 0 0 40px;
      border-right: 1px solid #a5abf6;
    }

    &.right {
      border-radius: 0 40px 40px 0;
      border-left: 1px solid #a5abf6;
      margin-left: -1px;
    }

    &:hover {
      z-index: 1;
      background: #1F2EE9;
      border-color: #1F2EE9;

      #{$this}__switcher-icon {
        svg {
          fill: #fff;
        }
      }

      #{$this}__switcher-tooltip {
        opacity: 1;
        top: -2px;

        &.left {
          transform: translate(calc(-100% - 9px), 0);
        }
        &.right {
          transform: translate(calc(100% + 9px), 0);
        }
      }
    }

    &:active {
      background: #161E8A;
      border-color: #161E8A;

      #{$this}__switcher-icon {
        top: calc(50% + 1px);

        svg {
          fill: #fff;
        }
      }
    }
  }

  &__switcher-icon {
    position: absolute;
    left: 50%;
    top: 50%;

    &.left {
      transform: translate(-50%, -50%) rotate(-180deg);
      width: 15px;
      height: 15px;
    }

    &.right {
      transform: translate(-50%, -50%);
      width: 15px;
      height: 15px;
    }

    svg {
      fill: #1F2EE9;
      transition: fill .2s ease-in-out;
    }
  }

  &__switcher-tooltip {
    display: flex;
    align-items: center;
    padding: 0 8px;
    box-sizing: border-box;
    height: 22px;
    opacity: 0;
    top: -9999px;
    position: absolute;
    font-size: 10px;
    font-weight: 500;
    color: #fff;
    background: #2D2F72;
    white-space: nowrap;
    border-radius: 2px;
    transition: opacity .2s ease-in-out, transform .2s ease-in-out;

    &:before {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      top: calc(50% - 5px);
      border-top: 5px solid transparent;
      border-bottom: 5px solid transparent;
    }

    &.left {
      left: 0;
      transform: translate(calc(-100% - 5px), 0);

      &:before {
        border-left: 6px solid #2D2F72;
        right: -5px;
      }
    }

    &.right {
      right: 0;
      transform: translate(calc(100% + 5px), 0);

      &:before {
        border-right: 6px solid #2D2F72;
        left: -5px;
      }
    }
  }

  &__dotted-animation {
    width: 48px;
    transform: translateY(30%);
    height: 14px;
    margin-left: 2px;
  }

  &__dark {
    background: $dark-background;
    box-shadow: inset 0 -1px 0 0 rgba(#DBE1E5, .05);

    #{$this}__balance-value {
      color: #fff;

      .e-big-number__fraction {
        color: $color-dark-grey;
      }
    }

    #{$this}__balance-token,
    #{$this}__balance-label,
    #{$this}__switcher-title {
      color: $color-dark-grey;
      opacity: 1;

      &:hover {
        opacity: 1;
      }
    }

    #{$this}__switcher-tooltip {
      background: $color-blue-dark-mode;

      &.left {
        &::before {
          border-left-color: $color-blue-dark-mode;
        }
      }

      &.right {
        &::before {
          border-right-color: $color-blue-dark-mode;
        }
      }
    }

    #{$this}__switcher-title:hover {
      color: #83898c;
    }

    #{$this}__balance-icon .e-icon__wallet-colored {
      fill: $color-dark-grey;
    }

    #{$this}__switcher-button {
      border-color: #313b3c;

      &:hover {
        background: $color-blue-dark-mode;
        border-color: $color-blue-dark-mode;
      }
    }

    #{$this}__switcher-icon svg {
      fill: #8D9098;
    }

    #{$this}__dotted-animation svg g > g {
      fill: #fff;
    }

    #{$this}__switcher-icon-loading svg circle {
      fill: #8D9098;
    }
  }
}
</style>



