<template>
  <div class="the-positions-closed" :class="[ `the-positions-closed__${theme}`]">
    <!--<e-preloader v-if="isLoading"/>-->

    <div class="the-positions-closed__table">
      <div class="the-positions-closed__table-head">
        <div class="the-positions-closed__table-col">
          <e-tooltip position="right">
            <template slot="label">
              Closed time
            </template>
            <template slot="message">
              Position close time.
            </template>
          </e-tooltip>
        </div>
        <div class="the-positions-closed__table-col">
          <e-tooltip position="right">
            <template slot="label">
              Asset
            </template>
            <template slot="message">
              The traded pair for the position.
            </template>
          </e-tooltip>
        </div>
        <!--<div class="the-positions-closed__table-col">-->
          <!--<e-tooltip position="left">-->
            <!--<template slot="label">-->
              <!--Size-->
            <!--</template>-->
            <!--<template slot="message">-->
              <!--The size of the open position in the underlying asset.-->
            <!--</template>-->
          <!--</e-tooltip>-->
        <!--</div>-->
        <!--<div class="the-positions-closed__table-col">-->
          <!--<e-tooltip position="left">-->
            <!--<template slot="label">-->
              <!--Entry Price (USD)-->
            <!--</template>-->
            <!--<template slot="message">-->
              <!--The entry price for the position. Used for profit and loss calculation.-->
            <!--</template>-->
          <!--</e-tooltip>-->
        <!--</div>-->
        <div class="the-positions-closed__table-col">
          <e-tooltip position="left">
            <template slot="label">
              Gross PnL
            </template>
            <template slot="message">
              The gross profit of your position excluding trading fees and funding cost.
            </template>
          </e-tooltip>
        </div>
        <div class="the-positions-closed__table-col">
          <e-tooltip position="left">
            <template slot="label">
              Fee
            </template>
            <template slot="message">
              Total fees paid on all trades to open and close the position
            </template>
          </e-tooltip>
        </div>
        <div class="the-positions-closed__table-col">
          <e-tooltip position="left">
            <template slot="label">
              Funding Cost
            </template>
            <template slot="message">
              Funding cost paid for the duration of the position
            </template>
          </e-tooltip>
        </div>
        <div class="the-positions-closed__table-col">
          <e-tooltip position="left">
            <template slot="label">
              Net PnL
            </template>
            <template slot="message">
              Net PnL = Gross PnL - Fees - Funding Cost
            </template>
          </e-tooltip>
        </div>
      </div>

      <e-container-scroll
        ref="scroll"
        class="the-positions-closed__wrapper"
      >
        <div class="the-positions-closed__table-body">
          <div
            class="the-positions-closed__table-body-item"
            v-for="pos in positions"
            :key="pos.positionHash"
          >
            <div class="the-positions-closed__table-col">
              <span class="the-positions-closed__price" v-if="pos.price">
               {{pos.closedAt ? dateToString(pos.closedAt) : '-'}}
              </span>
            </div>

            <div class="the-positions-closed__table-col">
              <div class="the-positions-closed__table-col-asset" @click="handleSelect(pos.futuresContractHash)">{{ pos.symbol }}</div>
            </div>

            <!--<div class="the-positions-closed__table-col">-->
              <!--<span-->
                <!--class="the-positions-closed__size"-->
                <!--:class="[pos.side ? 'the-positions-closed__size-settle' : 'the-positions-closed__size-close']"-->
              <!--&gt;-->
                <!--<e-big-number :value="pos.size" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="8"/>-->
              <!--</span>-->
            <!--</div>-->

            <div class="the-positions-closed__table-col">
              <span class="the-positions-closed__change the-positions-closed__profit" v-if="pos.unrealizedPL.isGreaterThanOrEqualTo(0)">
                <e-big-number :value="pos.unrealizedPL" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="8"/>
              </span>
              <span class="the-positions-closed__change the-positions-closed__loss" v-else>
                -<e-big-number :value="pos.unrealizedPL.abs()" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="8"/>
              </span>
            </div>

            <div class="the-positions-closed__table-col">
               <span :class="{'the-positions-closed__change': true, 'the-positions-closed__loss': pos.fee.gt(0)}">
                 <e-big-number :value="pos.fee" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="8"/>
               </span>
            </div>

            <div class="the-positions-closed__table-col">
                <span :class="{'the-positions-closed__change': true, 'the-positions-closed__loss': pos.fundingCost.gt(0)}">
                 <e-big-number :value="pos.fundingCost" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="8"/>
               </span>
            </div>

            <div class="the-positions-closed__table-col">
               <span class="the-positions-closed__change the-positions-closed__profit" v-if="pos.netPL.isGreaterThanOrEqualTo(0)">
                <e-big-number :value="pos.netPL" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="8"/>
              </span>
              <span class="the-positions-closed__change the-positions-closed__loss" v-else>
                -<e-big-number :value="pos.netPL.abs()" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="8"/>
              </span>
            </div>
          </div>
        </div>
      </e-container-scroll>
    </div>
  </div>
</template>

<script>
// import { BigNumber } from 'bignumber.js';

import { mapGetters, mapState } from 'vuex';
import { ASYNC_STATES } from '@/util/constants';

import { dateToString } from '@/util/dates';
import { onReconnect, removeReconnectListener } from '@/api/backend';
import { onResume, removeResumeListener } from '@/util/IdleEmitter';

// Components
import EBigNumber from '@/components/EBigNumber';
import EContainerScroll from '@/components/EContainerScroll';
import EPanelError from '@/components/EPanelError';
import EPreloader from '@/components/EPreloader';
import EIcon from '@/components/EIcon/EIcon';
import ETooltip from '@/components/ETooltip';

import errorIcon from '@/icons/e-icon_empty-trades.svg';
import iconClose from '@/icons/e-icon_close.svg';

export default {
  name: 'the-futures-positions-closed',

  data() {
    return {
      errorIcon,
      iconClose,
      reconnectHandler: null,
      fetchIntervalId: null,
    };
  },

  computed: {
    ...mapGetters({
      activeContractName: 'futuresActiveContract/contractName',
      contractBaseToken: 'futuresActiveContract/contractBaseToken',
      isConnected: 'isConnected',
      theme: 'ui/getTheme',
    }),

    ...mapState({
      activeContract: state => state.futuresActiveContract.activeContract,
      fetchPositionsState: state => state.futuresPositions.fetchClosedPositionsState,
      positions: state => state.futuresPositions.closedPositions,
    }),

    isLoading() {
      return this.fetchPositionsState === ASYNC_STATES.PENDING;
    },
  },

  watch: {
    /**
     * Отслеживание смены активного токена
     */
    activeContract(newVal, oldVAl) {
      if (newVal !== oldVAl) {
        this.$store.dispatch('futuresPositions/fetchClosedPositions');
      }
    },
  },

  mounted() {
    this.$store.dispatch('futuresPositions/fetchClosedPositions');
    this.startReconnectListener();

    this.fetchIntervalId = setInterval(() => this.$store.dispatch('futuresPositions/fetchClosedPositions'), 5e3);
  },

  beforeDestroy() {
    this.stopReconnectListener();
    clearInterval(this.fetchIntervalId);
  },

  methods: {
    dateToString,

    startReconnectListener() {
      this.reconnectHandler = () => {
        this.$store.dispatch('futuresPositions/fetchClosedPositions');
      };
      onReconnect(this.reconnectHandler);
      onResume(this.reconnectHandler);
    },

    stopReconnectListener() {
      if (!this.reconnectHandler) {
        return;
      }
      removeReconnectListener(this.reconnectHandler);
      removeResumeListener(this.reconnectHandler);
    },

    handleSelect(futuresContractHash) {
      this.$store.commit('futuresPositions/SET_POSITION_TO_CLOSE', null);
      this.$store.commit('futuresPositions/SET_PREVIOUS_CONTRACT_HASH', null);
      this.$router.push({ name: 'future', params: { contract: futuresContractHash } });
    },
  },

  components: {
    EIcon,
    EBigNumber,
    EContainerScroll,
    EPanelError,
    EPreloader,
    ETooltip,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-positions-closed {
  $this: &;

  position: relative;
  height: calc(100% - 24px);
  overflow-x: scroll;

  &__wrapper {
    padding-left: 0;
  }

  &__error {
    height: 100%;
    width: 100%;
    box-sizing: border-box;
  }

  &__table {
    width: 100%;
    height: 100%;
    color: $color-dark-grey;
    min-width: 540px;

    @include r(1279) {
      min-width: 550px;
    }
  }

  &__table-head,
  &__table-body-item {
    display: flex;
    align-items: center;
  }

  &__table-head {
    font-size: 9px;
    /*padding-right: 2px;*/
    text-transform: uppercase;
    letter-spacing: .5px;
    background: #1C222D;
    border-bottom: 1px solid #282C38;

    #{$this}__table-col {
      border-right: 1px solid #282C38;
    }

    .e-tooltip__icon {
      position: relative;
      top: 1px;
      left: 1px;
    }

    .e-tooltip__content {
      margin-top: -5px;
    }
  }

  &__table-body-item {
    height: 34px;
    background: #121722;
    border-bottom: 1px solid #282C38;

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__table-col {
    display: flex;
    align-items: center;
    align-self: stretch;
    flex: 1 1 20%;
    padding: 8px 6px;
    box-sizing: border-box;
    border-right: 1px solid #1e202b;
    position: relative;

    /*&:nth-child(1) {
      flex: 0 0 90px;
    }*/

    /*&:nth-child(2) {
      @include r(1600) {
        flex: 1 1 10%;
      }
    }*/

    &:nth-child(1) {
      flex: 0 0 150px;
    }

    &:nth-child(2) {
      flex: 0 0 100px;
    }

    &:nth-child(3),
    &:nth-child(4),
    &:nth-child(5),
    &:nth-child(6),
    &:nth-child(7) {
      justify-content: flex-end;
      text-align: right;
    }

    &:nth-child(7) {
      display: flex;
      justify-content: flex-end;
      flex: 0 0 70px;
      padding: 8px 5px;
    }

    &:last-child {
      border: 0;
    }

    .e-big-number {
      position: relative;
    }

    @include r(1439) {
      padding: 8px 5px;
    }
  }

  &__table-body {
    #{$this}__table-col {
      height: 34px;

      &:nth-child(7) {
        font-size: 11px;
        font-weight: 400;
      }
    }
  }

  &__symbol {
    // display: inline-flex;
    // align-items: center;
    // justify-content: center;
    // height: 17px;
    // padding: 0 2px;
    // color: #8D9098;
    // font-size: 9px;
    // letter-spacing: -.25px;
    // text-transform: uppercase;
    // font-weight: 600;
    // border-radius: 4px;
    // border: 1px solid rgba(92,96,98,0.40);
  }

  &__table-col-asset {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding-left: 8px;
    box-sizing: border-box;
    cursor: pointer;
    color: #fff;
    font-size: 11px;
    font-weight: 400;
    transition: background .15s ease-in-out;

    &:hover {
      background: #1d222f;
    }
  }

  &__size,
  &__change,
  &__price,
  &__collateral {
    font-size: 11px;
    font-weight: 400;

    .e-big-number {
      font-size: 11px;
      font-weight: 400;

      &__int {
        .e-big-number__main + .e-big-number__trailing-zeros {
          opacity: 1;
        }
      }

      @include r(1439) {
        font-size: 10px;
      }
    }
  }

  &__change,
  &__collateral {
    font-size: 11px;
    font-weight: 400;

    .e-big-number {
      font-size: 11px;
      font-weight: 400;

      &__leading-zeros,
      &__trailing-zeros {
        opacity: 1;
      }

      &__int {
        .e-big-number__main + .e-big-number__trailing-zeros {
          opacity: 1;
        }
      }

      @include r(1439) {
        font-size: 10px;
        font-weight: 400;
      }
    }
  }

  &__price,
  &__collateral {
    color: #8D9098;

    .e-big-number {
      color: #8D9098;
    }
  }

  &__size-settle,
  &__profit {
    color: #00DB9C;
  }

  &__size-close,
  &__loss {
    color: #DA4830;
  }

  &__date {
    font-size: 9px;
    font-weight: 400;
    color: #8D9098;
    opacity: .5;
  }

  &__date-expires {
    color: #8D9098;
    font-size: 9px;
    font-weight: 400;
  }

  &__date-expires-expired {
    width: 100%;
    text-align: center;
    display: inline-block;
    font-size: 8px;
    font-weight: 400;
    text-transform: uppercase;
    padding: 1px 4px;
    border-radius: 3px;
    background: rgba(220,71,39,.2);
    color: #DC4727;
  }

  &__label {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 14px;
    text-align: center;
    border-radius: 2px;
    font-size: 8px;
    font-weight: 400;
    color: #fff;
    cursor: pointer;
    transition: opacity .15s ease-in-out;

    &:hover {
      opacity: .85;
    }
  }

  &__label-close {
    background: #DA4830;
  }

  &__label-settle {
    font-size: 10px;
    color: $color-light-grey;
  }

  &__base {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 27px;
    height: 14px;
    border-radius: 2px;
    // background: rgba(92,96,98,0.40);
    color: #8D9098;
    font-size: 8px;
    text-transform: uppercase;
    font-weight: 400;
  }

  &__close {
    width: 20px;
    height: 35px;
    margin-right: -8px;
    margin-left: 1px;
    cursor: pointer;
    opacity: .7;
    transition: opacity .15s ease-in-out;

    svg {
      width: 8px;
      height: 8px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      fill: rgb(218, 72, 48);
    }

    &:hover {
      opacity: 1;
    }
  }

  &__light {
    #{$this}__table {
      color: $color-light-grey;
    }

    #{$this}__table-col-asset {
      color: #09168a;

      &:hover {
        background: #fafafd;
      }
    }

    #{$this}__table-head {
      background: #FBFBFD;
      border-color: #F0F6F9;

      #{$this}__table-col {
        border-color: #F0F6F9;
      }
    }

    #{$this}__table-body-item {
      background: #fff;
      border-color: #F0F6F9;
    }

    #{$this}__table-col {
      border-color: #F0F6F9;
    }

    #{$this}__symbol {
      color: #0D105B;
      border-color: rgba(92,96,98,.3);
    }

    #{$this}__base {
      // background: rgba(92,96,98,.1);
      color: #8D9098;
    }

    #{$this}__price,
    #{$this}__collateral {
      color: #09168a;
    }

    #{$this}__price .e-big-number,
    #{$this}__collateral .e-big-number {
      color: #09168a;
    }

    #{$this}__label {
      color: #fff;
    }

    #{$this}__date {
      color: #8384AB;
    }

    #{$this}__date-expires-expired {
      background: rgba(220,71,39,.1);
    }

    #{$this}__size-settle,
    #{$this}__profit {
      color: #1FCE43;
    }
  }
}
</style>



