<template>
  <div class="e-tabs" :class="[ `e-tabs__${theme}` ]">
    <button
      v-for="item in tabs"
      :key="item.title"
      class="e-tabs__tab"
      :class="{ 'is-active': item.id === value }"
      @click="handleClick(item.id)"
    >{{item.title}}</button>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'e-tabs',

  props: {
    tabs: {
      type: Array,
      default: () => [],
    },
    value: {
      type: Number,
      default: 0,
    },
  },

  computed: {
    ...mapGetters({
      theme: 'ui/getTheme',
    }),
  },

  methods: {
    handleClick(nextValue) {
      if (nextValue !== this.value) {
        this.$emit('input', nextValue);
      }
    },
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-tabs {
  $this: &;

  display: flex;

  &__tab {
    position: relative;
    margin-left: 39px;
    color: $color-semi-blue;
    font-size: 10px;
    font-weight: 700;
    text-transform: uppercase;
    transition: color .15s ease-in-out;

    &:hover {
      color: #0123E7;
    }

    &:before {
      content: '/';
      font-size: 16px;
      border-radius: 50%;
      font-weight: 700;
      position: absolute;
      top: -5px;
      left: -21px;
      color: #CBD0E4;
      transition: color .15s ease-in-out;

      @include r(1200) {
        left: -9px;
        top: -5px;
      }

      @include rmin(1921) {
        top: -2px;
        left: -21px;
      }
    }

    &:first-child {
      margin-left: 0;

      &::before {
        display: none;
      }
    }

    @include r(1200) {
      margin-left: 15px;
    }

    @include rmin(1921) {
      font-size: 14px;
      margin-left: 33px;
    }

    &.is-active {
      color: #0123e7;

      &::after {
        opacity: 1;
      }
    }
  }

  &__dark {
    #{$this}__tab {
      color: $color-dark-grey;

      &.is-active {
        color: $color-green-dark;
      }

      &::before {
        color: $color-dark-grey;
      }
    }
  }
}
</style>




