<template>
  <div
    class="e-input-number-order"
    :class="{
      focused: focused || thumbDragged,
      invalid: invalid && !disabled && !readonly,
      [`e-input-number-order_${slider}`]: slider,
      'e-input-number-order_wide': wide,
      'e-input-number-order_non-editable': readonly || disabled,
      'e-input-number-order__label-placeholder': labelAsPlaceholder,
      'e-input-number-order_square-style': squareStyle,
      'e-input-number-order__dark': theme === 'dark',
    }"
    @click="setFocus"
  >
    <div class="e-input-number-order__wrap">
      <div
        v-if="label != null"
        class="e-input-number-order__label"
      >
        {{ label }}
      </div>

      <div class="e-input-number-order__box">
        <div
          v-if="measurementLabel"
          class="e-input-number-order__unit-left"
          :class="{ 'e-input-number-order__unit_mod': measurementUnitMod, 'e-input-number-order__unit_mod-filled': focused || stringValue.length > 0 }"
        >
          <div v-if="measurementLabelTooltip" class="e-input-number-order__measurement-label-tooltip-position">
            <e-tooltip :position="measurementLabelTooltipPosition">
              <template slot="label">
                {{ measurementLabel }}
              </template>
              <template slot="message">
                {{measurementLabelTooltip}}
              </template>
            </e-tooltip>
          </div>

          <div v-else>{{ measurementLabel }}</div>
        </div>

        <div
          v-if="measurementUnit != null && type !== 'arrow'"
          class="e-input-number-order__unit"
          :class="{ 'e-input-number-order__unit_mod': measurementUnitMod, 'e-input-number-order__unit_mod-filled': focused || stringValue.length > 0 }"
        >
          {{ measurementUnit }}
          <div
            v-if="$slots.hint"
            class="e-input-number-order__hint"
          >
            <slot name="hint"/>
          </div>
        </div>

        <input
          ref="inputField"
          v-bind="$attrs"
          :value="stringValue"
          :placeholder="focused ? null : placeholder"
          :class="{
            'e-input-number-order__input_has-hint' : $slots.hint,
            'e-input-number-order__input_padd-right-sm' : fontSize === 10
          }"
          :style="{ 'font-size': fontSize && `${fontSize}px` }"
          :disabled="disabled"
          :readonly="readonly"
          class="e-input-number-order__input"
          @keydown="handleKeyDown"
          @input="handleInput"
          @focus="handleFocus"
          @blur="handleBlur"
        />

        <div
          v-if="type === 'arrow'"
          class="e-input-number-order__arrows"
        >
          <div class="e-input-number-order__arrow-down" @mousedown="handleArrowDown">
            –
          </div>
          <div class="e-input-number-order__arrow-up" @mousedown="handleArrowUp">
            +
          </div>

        </div>

        <e-range-slider
          v-if="slider && !disabled && !readonly"
          v-model="sliderValue"
          v-bind="sliderOptions"
          @change="emitInput"
          @thumbDragStart="thumbDragged = true"
          @thumbDragEnd="thumbDragged = false"
        >
          <div
            slot="knob"
            class="e-input-number-order__thumb"
          >
            <e-icon :src="iconRange"/>
          </div>
        </e-range-slider>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable no-restricted-syntax */
import { BigNumber } from 'bignumber.js';
import { mapGetters } from 'vuex';
import ERangeSlider from '@/components/ERangeSlider';
import ETooltip from '@/components/ETooltip';

import EIcon from '@/components/EIcon';
import iconRange from '@/icons/e-icon_range.svg';
import iconArrowDowner from '@/icons/e-icon_arrow_downer.svg';

const DECIMAL_SEPARATOR = '.';
const DIGITS = Array.from(Array(10), (_, i) => `${i}`);
const ALLOWED_KEYS = ['Backspace', 'Delete', 'Tab', 'Escape', 'Enter', 'ArrowLeft', 'ArrowRight', 'Home', 'End', ...DIGITS];
const SLIDER_INTERVAL_MAX_COUNT = 100;

export default {
  name: 'e-input-number-order',
  inheritAttrs: false,

  props: {
    value: {
      type: [Number, String, BigNumber],
      default: null,
    },

    min: [Number, String, BigNumber],

    max: [Number, String, BigNumber],

    disabled: {
      type: Boolean,
      default: false,
    },

    // for validation

    decimals: {
      type: Number,
      default: 0,
    },

    slider: {
      type: String,
      default: null,
      validator: v => ['default', 'primary', 'secondary', null].includes(v),
    },

    type: {
      type: String,
      default: null,
      validator: v => ['arrow', null].includes(v),
    },

    label: String,
    measurementLabelTooltip: String,
    measurementLabelTooltipPosition: String,

    measurementUnit: String,

    measurementLabel: String,

    readonly: {
      type: Boolean,
      default: false,
    },

    placeholder: {
      type: String,
      default: '0',
    },

    invalid: {
      type: Boolean,
      default: false,
    },

    // style modifiers

    wide: {
      type: Boolean,
      default: false,
    },

    measurementUnitMod: {
      type: Boolean,
      default: false,
    },

    labelAsPlaceholder: {
      type: Boolean,
      default: false,
    },

    squareStyle: {
      type: Boolean,
      default: false,
    },

    priceStep: {
      type: Number,
      default: 1,
    },

    activeAssetAmountDec: {
      type: Number,
    },
  },

  icons: {
    iconArrowDowner,
  },

  data() {
    return {
      stringValue: this.getStringValueFromProps(),
      focused: false,
      thumbDragged: false,
    };
  },

  computed: {
    fontSize() {
      if (this.stringValue.length > 10) {
        return 10;
      }
      return null;
    },

    numberValue() {
      return BigNumber(this.stringValue);
    },

    isValidNumber() {
      return !this.numberValue.isNaN();
    },

    canHaveSeparator() {
      return this.decimals !== 0;
    },

    // Slider

    sliderValue: {
      get() {
        if (!this.slider) return null;
        return this.isValidNumber ? this.numberValue.toNumber() : this.sliderOptions.min;
      },

      set(value) {
        let roundedValue;
        if (this.isValidNumber && this.nearlyMax.lt(value)) {
          roundedValue = this.max;
        } else {
          roundedValue = value.toPrecision(15);
        }
        this.stringValue = BigNumber(roundedValue).toFixed();
      },
    },

    maxNum() {
      return BigNumber(this.max);
    },

    minNum() {
      return BigNumber(this.min);
    },

    range() {
      const { slider, maxNum: max, minNum: min } = this;
      if (!slider) return null;
      return max.minus(min);
    },

    nearlyMax() {
      const { slider, range, minNum: min } = this;
      if (!slider) return null;
      return min.plus(range.times(0.98));
    },

    step() {
      const { slider, range } = this;
      if (!slider) return null;
      const intervalCount = BigNumber.min(SLIDER_INTERVAL_MAX_COUNT, range.shiftedBy(this.decimals));
      return range.div(intervalCount).dp(this.decimals);
    },

    sliderOptions() {
      const { slider, minNum: min, maxNum: max, step } = this;
      if (!slider) return null;
      return {
        max: max.toNumber(),
        min: min.toNumber(),
        step: step.toNumber(),
      };
    },

    ...mapGetters({
      theme: 'ui/getTheme',
    }),
  },

  watch: {
    value(value) {
      const stringValue = typeof value === 'string'
        ? value
        : BigNumber(value).toFixed();
      this.stringValue = stringValue;
    },

    min() {
      if (this.isValidNumber && this.numberValue.lt(this.minNum)) {
        this.stringValue = this.minNum.toFixed();
        this.emitInput();
      }
    },

    max() {
      if (this.isValidNumber && this.numberValue.gt(this.maxNum)) {
        this.stringValue = this.maxNum.toFixed();
        this.emitInput();
      }
    },
  },

  created() {
    Object.assign(this, { iconRange });
    if (this.value == null) {
      this.emitInput();
    }
  },

  methods: {
    // инициализируем value используя props
    getStringValueFromProps() {
      const { value } = this;
      if (value == null) {
        return '';
      } else if (typeof value === 'string') {
        return value;
      }
      return BigNumber(value).toFixed();
    },

    emitInput() {
      this.$emit('input', this.stringValue);
    },

    setFocus() {
      if (!this.disabled && !this.focused) {
        this.$refs.inputField.focus();
      }
    },

    handleInput(event) {
      const nextVal = event.target.value;
      if (this.stringValue === '' && nextVal === '0') {
        this.stringValue = `${nextVal}${DECIMAL_SEPARATOR}`;
      } else {
        let nextValFiltered = '';
        let dotAppended = false;
        for (const c of nextVal) {
          if (DIGITS.includes(c)) {
            nextValFiltered += c;
          } else if (c === DECIMAL_SEPARATOR && this.canHaveSeparator) {
            if (nextValFiltered === '') {
              nextValFiltered += `0${DECIMAL_SEPARATOR}`;
            } else if (!dotAppended) {
              nextValFiltered += DECIMAL_SEPARATOR;
            }
            dotAppended = true;
          }
        }
        this.stringValue = nextValFiltered;
      }
      this.$forceUpdate();
      this.emitInput();
    },

    handleFocus() {
      this.focused = true;
    },

    handleBlur() {
      this.focused = false;
      if (this.isValidNumber) {
        const normalizedNumber = this.numberValue.toFixed();
        if (this.stringValue !== normalizedNumber) {
          this.stringValue = normalizedNumber;
          this.emitInput();
        }
      }
    },

    handleKeyDown(e) {
      const isSpecialKeyPressed = e.ctrlKey || e.metaKey || e.altKey;
      const allowInput = (
        isSpecialKeyPressed ||
        ALLOWED_KEYS.includes(e.key) ||
        (
          e.key === DECIMAL_SEPARATOR &&
          this.canHaveSeparator && // dot only if decimals allowed
          !this.stringValue.includes(DECIMAL_SEPARATOR) // only 1 dot
        )
      );
      if (!allowInput) {
        e.preventDefault();
      }
    },

    handleArrowUp() {
      if (this.activeAssetAmountDec || this.activeAssetAmountDec === 0) {
        let changeVal = +this.stringValue * 0.01;
        if (+changeVal.toFixed(this.activeAssetAmountDec) === 0) {
          /* eslint-disable */
          changeVal = this.activeAssetAmountDec === 0 ? 1 : this.activeAssetAmountDec === 1 ? 0.1 : this.activeAssetAmountDec === 2 ? 0.01 : this.activeAssetAmountDec === 3 ? 0.001 : 1;
        }
        this.stringValue = BigNumber(+this.stringValue + changeVal).toFixed(this.activeAssetAmountDec);
      } else {
        const firstStepOccurency = this.priceStep.toString().split('5')[0].length - 1;
        this.stringValue = BigNumber(+this.stringValue + +this.priceStep).toFixed(firstStepOccurency);
      }
      this.$forceUpdate();
      this.emitInput();
    },

    handleArrowDown() {
      if (+this.stringValue > 0) {
        if (this.activeAssetAmountDec || this.activeAssetAmountDec === 0) {
          let changeVal = +this.stringValue * 0.01;
          if (+changeVal.toFixed(this.activeAssetAmountDec) === 0) {
            /* eslint-disable */
            changeVal = this.activeAssetAmountDec === 0 ? 1 : this.activeAssetAmountDec === 1 ? 0.1 : this.activeAssetAmountDec === 2 ? 0.01 : this.activeAssetAmountDec === 3 ? 0.001 : 1;
          }

          this.stringValue = BigNumber(+this.stringValue - changeVal).toFixed(this.activeAssetAmountDec);
        } else {
          const firstStepOccurency = this.priceStep.toString().split('5')[0].length - 1;
          this.stringValue = BigNumber(+this.stringValue - +this.priceStep).toFixed(firstStepOccurency);
        }
        this.$forceUpdate();
        this.emitInput();
      }
    },
  },

  components: { EIcon, ERangeSlider, ETooltip },
};
</script>

