      }
    }

    #{$this}__arrow-up {
      &:after {
        background-color: $color-border-dark;
      }
    }

    #{$this}__arrow-icon {
      fill: #8D9098;
    }

    #{$this}__unit {
      color: $color-light-grey !important;
    }

    #{$this}__unit-left {
      background: #1C222D;
      color: $color-light-grey !important;
    }
  }
}
</style>



