<template>
  <transition-modal>
    <e-modal
      auto-height
      @close="cancel"
    >
      <template slot="inner">
        <div class="the-delete-order-popup">
          <div class="the-delete-order-popup__title">
            Are you Sure?
          </div>
          <div class="the-delete-order-popup__btns">
            <div class="the-delete-order-popup__btn">
              <e-button
                type="rect"
                fill="red"
                size="m"
                width="full"
                fontWeight="400"
                @click="cancel"
              >
                Cancel
              </e-button>
            </div>
            <div class="the-delete-order-popup__btn">
              <e-button
                type="rect"
                fill="green"
                size="m"
                width="full"
                fontWeight="400"
                @click="submitCancelOrder"
              >
                Okay
              </e-button>
            </div>
          </div>
        </div>
      </template>
    </e-modal>
  </transition-modal>
</template>

<script>
import { mapActions, mapMutations } from 'vuex';

import EModal from '@/components/EModal';
import EButton from '@/components/EButton';

import TransitionModal from '@/containers/transitions/TransitionModal';

export default {
  name: 'the-futures-delete-order-popup',

  methods: {
    ...mapMutations({ cancel: 'futuresOrders/CANCEL_CANCEL_ORDER' }),
    ...mapActions({ submitCancelOrder: 'futuresOrders/submitCancelOrder' }),
  },

  components: {
    TransitionModal,
    EButton,
    EModal,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-delete-order-popup {
  padding: 42px 20px 31px;

  &__title {
    font-size: 24px;
    line-height: 1;
    color: #0D105B;
    position: relative;
    padding-bottom: 22px;
    display: inline-block;
    margin-bottom: 24px;
    font-weight: 400;

    &:before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 121px;
      height: 1px;
      background-color: rgba(#0D105B, .2);
    }
  }

  &__btns {
    display: flex;
    margin: 0 -5px;
  }

  &__btn {
    width: 50%;
    padding: 0 5px;
  }
}
</style>



