<template>
  <div class="the-history-tab">
    <div class="the-history-tab__wrapper">
      <the-history-table-tab
        :history="historyPage"
        class="the-history-tab__table"
        :class="{ 'is-loading': historyPageLoading }"
      />

      <e-pagination
        v-if="pageCount > 1"
        v-model="pageNumber"
        slot="footer"
        :page-count="pageCount"
        class="the-history-tab__pagination"
      />

      <!-- Прелоудер который отображается на во время загрузки данных -->
      <div
        class="the-history-tab__preloader"
        v-if="historyPageLoading"
      >
        <e-preloader />
      </div>
    </div>
  </div>
</template>

<script>
/**
 * Component: Отображение страницы истории транзакций пользователя
 *
 * @version 1.0.2
 */
import { mapState, mapGetters, mapActions } from 'vuex';
import { SECTION_FUNDS, SECTION_TRADES, SECTION_AFFILIATES, SECTION_FUTURES_TRADES } from '@/api/backend/constants';

// Components
import EButtonTabs from '@/components/EButtonTabs';
import EIcon from '@/components/EIcon';
import EPagination from '@/components/EPagination';
import EPreloader from '@/components/EPreloader';
import TheHistoryTableTab from '@/containers/blocks/TheHistoryTableTab';

// Transitions
import TransitionContent from '@/containers/transitions/TransitionContent';

// Constants
// const TAB_ALL = 'All';
const TAB_TRADING = 'Trading';
const TAB_FUTURES_TRADING = 'Futures Trading';
const TAB_FUNDS = 'Funds';
const TAB_AFFILIATE = 'Affiliate';

const TAB_TO_SECTION = {
  [TAB_TRADING]: SECTION_TRADES,
  [TAB_FUTURES_TRADING]: SECTION_FUTURES_TRADES,
  [TAB_FUNDS]: SECTION_FUNDS,
  [TAB_AFFILIATE]: SECTION_AFFILIATES,
};

const ALL_TABS = [TAB_FUTURES_TRADING];

export default {
  name: 'the-history-tab',

  data() {
    return {
      activeTab: TAB_FUTURES_TRADING,
    };
  },

  computed: {
    ...mapState({
      historyPage: state => state.history.activePage,
      historyPageLoading: state => state.history.historyPageLoading,
      pageCount: state => state.history.numberOfPages,
    }),
    ...mapGetters({
      userAddress: 'wallets/activeWallet',
      theme: 'ui/getTheme',
    }),

    pageNumber: {
      get: vm => vm.$store.state.history.activePageNumber,
      set(pageNumber) {
        this.fetchPage(pageNumber);
      },
    },
  },

  watch: {
    activeTab: 'fetchFirstPage',
    userAddress: 'fetchFirstPage',
  },

  created() {
    this.ALL_TABS = ALL_TABS;
    this.fetchFirstPage();
  },

  methods: {
    ...mapActions({
      fetchHistory: 'history/getHistory',
    }),

    fetchFirstPage() {
      this.fetchPage(0);
    },

    fetchPage(pageNumber) {
      this.fetchHistory({
        pageNumber,
        section: TAB_TO_SECTION[this.activeTab],
      });
    },
  },

  components: {
    EButtonTabs,
    EIcon,
    EPagination,
    EPreloader,
    TheHistoryTableTab,
    TransitionContent,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-history-tab {
  position: relative;
  height: calc(100% - 32px);
  overflow-x: scroll;

  &__wrapper {
    display: flex;
    flex-flow: column;
    min-height: 100%;
  }

  &__table {
    min-width: 800px;
    flex: 1;
    transition: opacity 0.5s ease-in-out;

    &.is-loading {
      opacity: 0.2;
    }
  }

  &__tabs {
    margin-bottom: 18px;
    flex: none;
  }

  &__pagination {
    margin-top: 24px;
    margin-bottom: 28px;
    flex: none;
  }

  &__preloader {
    position: absolute;
    top: 0;
    left: 0;
    right: 10px;
    bottom: 0;
  }

  &__dark {
    .e-button-tabs_default .e-button-tabs__item {
      background: #1A1A1A;
      color: #8D9098;

      &:hover {
        color: #fff;
      }

      &.is-active {
        background: #232323;
        color: #fff;

        &:hover {
          color: #fff;
        }
      }
    }

    .the-history-tab-table__heading {
      color: $color-dark-grey;
    }

    .e-history-table-item {
      background: #232323;

      &:hover {
        background: #393939;
      }
    }

    .e-history-table-item__operation-icon_sell,
    .e-history-table-item__operation-icon_buy {
      svg path:nth-child(2) {
        fill: #232323;
      }
    }

    .e-history-table-item__amount,
    .e-history-table-item__price,
    .e-history-table-item__net-amount,
    .e-history-table-item__number {
      color: #8D9098;
    }

    .e-history-table-item__amount .e-big-number__leading-zeros,
    .e-history-table-item__price .e-big-number__leading-zeros,
    .e-history-table-item__net-amount .e-big-number__leading-zeros,
    .e-history-table-item__combiner {
      color: #6a6b70;
    }

    .e-history-table-item__fees,
    .e-history-table-item__gas-fees {
      color: #DA4830;

      &.isZero {
        color: #838383;

        .e-big-number__leading-zeros {
          color: #838383;
        }
      }
    }

    .e-history-table-item__fees .e-big-number__leading-zeros,
    .e-history-table-item__gas-fees .e-big-number__leading-zeros {
      color: #7e3529;
    }

    .e-hint-adress,
    .e-history-table-item__hint {
      background: #232323;
      color: #8D9098;
      box-shadow: 0 3px 10px rgba(0, 0, 0, 0.14);
    }

    .the-history-tab-table__empty {
      background: #232323;
    }

    .e-panel-error__icon svg g path:nth-child(1) {
      fill: #474a4f;
    }
  }
}
</style>



