/**
 * Perform different actions based on different conditions
 *
 * @param {*} exp The expression
 * @param {Array<Array>} cases Example: [v => v > 10, 100]
 * @param {*} [def] Default value
 * @return {*}
 */
export default (exp, cases, def) => {
  for (let i = 0; i < cases.length; i += 1) {
    const [caseCond, caseVal] = cases[i];

    if (caseCond(exp)) {
      return caseVal;
    }
  }

  return def;
};



