<template>
  <div class="e-panel-error" :class="[ `e-panel-error__${theme}` ]">
    <div class="e-panel-error__body">
      <div class="e-panel-error__icon-wrapp">
        <div class="e-panel-error__icon">
          <e-icon :src="icon"/>
        </div>
      </div>

      <div class="e-panel-error__title">
        <slot name="title"></slot>
      </div>

      <div class="e-panel-error__descr">
        <slot name="descr"></slot>
      </div>

      <slot name="after"/>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

import iconTranding from '@/icons/e-icon_trading.svg';

import EIcon from '@/components/EIcon';

export default {
  name: 'e-panel-error',

  props: {
    buttons: {
      type: Boolean,
      default: false,
    },
    icon: {
      type: String,
      default: iconTranding,
    },
  },

  data() {
    return {
      iconTranding,
    };
  },

  computed: {
    ...mapGetters({
      theme: 'ui/getTheme',
    }),
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-panel-error {
  $this: &;

  background-color: #fff;
  height: 100%;
  padding: 43px;
  box-sizing: border-box;
  box-shadow: 0 1px 3px rgba(black, .08);
  position: relative;
  transition: background-color .15s ease-in-out;

  &__icon-wrapp {
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__icon {
    width: 36px;
    height: 20px;
    margin-right: 1px;
  }

  &__body {
    display: flex;
    align-items: center;
    flex-direction: column;
    position: absolute;
    padding: 0 43px;
    box-sizing: border-box;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);


    @include r(1540) {
      padding: 0 12px;
    }
  }

  &__title {
    font-size: 18px;
    color: $color-blue;
    font-weight: 400;
    display: inline-block;
    position: relative;
    padding-bottom: 8px;
    margin-bottom: 10px;
    transition: color .15s ease-in-out;

    &:before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 2px;
      background-color: $color-blue;
      border-radius: 100px;
      transition: background-color .15s ease-in-out;
    }
  }

  &__descr {
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    color: $color-ultramarine;
    line-height: 1.5;
    transition: color .15s ease-in-out;
  }

  &__dark {
    background: $dark-background-lighter;

    #{$this}__title {
      color: $color-blue-dark-mode;

      &::before {
        background-color: $color-blue-dark-mode;
      }
    }

    #{$this}__descr {
      color: $color-dark-grey;
    }

    .the-trades__error-btn-text {
      color: #fff;
    }

    .the-trades__error-separator {
      color: $color-dark-grey;
    }

    .the-trades__error-btn-arrow svg path:nth-child(2){
      fill: #232323;
    }

    #{$this}__title {
      color: $color-blue-dark-mode;

      &:before {
        background-color: $color-blue-dark-mode;
      }
    }
  }
}
</style>



