<template>
  <transition-modal>
    <div class="the-trade-details-popup">
      <e-receipt
        :isConfirmed="trade.isConfirmed"
        :status="true"
        :btnLink="buttonLink"
        title="Trade details"
        @cancel="closeWindow"
      >
        <template slot="header">
          <div class="the-trade-details-popup__tabs">
            <!-- <e-button-tabs
              :tabs="buttonTabs"
              mod="default"
              @changeTab="handleChangeTab"
            /> -->
          </div>

          <!-- BEGIN: Оторажение номинальное информации по трейду (общей для мейкера и тейкера) -->
          <div class="the-trade-details-popup__container">
            <e-receipt-item
              :buy="trade.type === BUY_TOKEN_ORDER"
              :sell="trade.type === SELL_TOKEN_ORDER"
              :token-name="trade.type === BUY_TOKEN_ORDER ? `Buy ${activeContractName}` : `Sell ${activeContractName}`"
              :icon="true"
            >
              <template slot="title">
                Order Type
              </template>
              <template slot="value"></template>
            </e-receipt-item>

             <e-receipt-item :tokenName="trade.takerAddress === activeWalletAddress ? 'TAKER' : 'MAKER'">
              <template slot="title">
                Your order position
              </template>
              <template slot="value">

              </template>
            </e-receipt-item>

            <e-receipt-item tokenName="USD">
              <template slot="title">
                Price for 1 {{ activeContractName }}
              </template>
              <template slot="value">
                <e-big-number
                  :value="trade.price"
                  :decimals="FUTURES_PRICE_DECIMALS"
                  :decimal-places="2"
                />
              </template>
            </e-receipt-item>

            <e-receipt-item :tokenName="activeContractName">
              <template slot="title">
                Amount {{ activeContractName }}
              </template>
              <template slot="value">
                <e-big-number
                  :value="trade.amountFilled"
                  :decimals="FUTURES_AMOUNT_DECIMALS"
                  :decimal-places="6"/>
              </template>
            </e-receipt-item>
          </div>
          <!-- END: Оторажение номинальное информации по трейду (общей для мейкера и тейкера) -->
        </template>
      </e-receipt>
    </div>
  </transition-modal>
</template>

<script>
import { mapMutations, mapGetters, mapState } from 'vuex';

import EBigNumber from '@/components/EBigNumber';
import EReceipt from '@/components/EReceipt';
import EReceiptItem from '@/components/EReceiptItem';
import EButtonTabs from '@/components/EButtonTabs';

import TransitionModal from '@/containers/transitions/TransitionModal';

import { ETHERSCAN_URL } from '@/config';

export default {
  name: 'the-futures-trade-details-popup',

  data() {
    return {
      buttonTabs: ['taker', 'maker'],
      activeTabIndex: 0,
    };
  },

  computed: {
    ...mapGetters({
      trade: 'futuresTrades/currentTradeDetails',
      activeContractName: 'futuresActiveContract/contractName',
      activeWalletAddress: 'wallets/activeWalletAddress',
    }),

    ...mapState({
      activeContract: state => state.futuresActiveContract.activeContract,
    }),

    buttonLink() {
      const hash = this.trade.txHash;
      if (!hash) {
        return '';
      } else if (hash === '0x0') {
        return hash;
      }
      return `${ETHERSCAN_URL}/tx/${hash}`;
    },
  },

  methods: {
    ...mapMutations({
      close: 'futuresTrades/HIDE_TRADE_DETAILS',
    }),

    handleChangeTab(tabIndex) {
      this.activeTabIndex = tabIndex;
    },

    closeWindow() {
      this.activeTabIndex = 0;
      this.close();
    },
  },

  components: {
    EBigNumber,
    EReceipt,
    EReceiptItem,
    EButtonTabs,
    TransitionModal,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-trade-details-popup {
  &__tabs {
    margin-bottom: 20px;
  }
}
</style>



