<template>
  <transition-modal>
    <div
      v-if="order"
      class="the-order-confirm"
      :class="[ `the-order-confirm__${theme}` ]"
    >
      <e-receipt
        title="Confirm order"
        @confirm="submit"
        @cancel="close"
      >
        <template slot="header">
          <div class="the-order-confirm__wrapper">
            <div v-if="demoStep === 9" class="the-order-confirm__demo">
              <e-demo title="Confirm order" arrowPosition="right" :isNextDisabled="true" arrowPositionMobile="top">
                Here you have the information about your order. Check the information and press the <b>SIGN ORDER</b> button
              </e-demo>
            </div>
            <div v-if="demoStep === 13" class="the-order-confirm__demo">
              <e-demo title="Confirm Close Position" arrowPosition="right" :isNextDisabled="true" arrowPositionMobile="top">
                Press the “SIGN ORDER" button to confirm position closure.
              </e-demo>
            </div>

            <div class="the-order-confirm__wrapper-left">
              <div class="the-order-confirm__wrapper-title">
                Order Details
              </div>
              <e-receipt-item
                :buy="order.orderDetails.buyOrSell === BUY_TOKEN_ORDER"
                :sell="order.orderDetails.buyOrSell === SELL_TOKEN_ORDER"
                :token-name="order.orderDetails.buyOrSell === BUY_TOKEN_ORDER ? 'Buy' : 'Sell'"
                noIcon
              >
                <template slot="title">
                  Order Type:
                </template>
              </e-receipt-item>

              <e-receipt-item
                v-if="[ORDER_TYPE_LIMIT, ORDER_TYPE_STOP_LIMIT, ORDER_TYPE_MARKET].includes(order.orderDetails.type)"
                :tokenName="contractName.replace('USD', '')"
              >
                <template slot="title">
                  Amount:
                </template>
                <template slot="value">
                  <e-big-number :value="order.orderDetails.amount" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="6"/>
                </template>
              </e-receipt-item>

              <e-receipt-item
                v-else-if="order.orderDetails.type === ORDER_TYPE_MARKET"
              >
                <template slot="title">
                  Amount:
                </template>
                <div
                  slot="value"
                  class="the-order-confirm__value"
                >
                  —
                </div>
              </e-receipt-item>

              <e-receipt-item
                v-if="order.orderDetails.type === ORDER_TYPE_LIMIT"
                tokenName="USD"
              >
                <template slot="title">
                  Price:
                </template>
                <e-big-number
                  slot="value"
                  :value="order.orderDetails.limitPrice"
                  :decimals="FUTURES_PRICE_DECIMALS"
                  :decimal-places="activeContract.decimals"
                />
              </e-receipt-item>

              <e-receipt-item
                v-if="order.orderDetails.type === ORDER_TYPE_STOP_LIMIT"
                tokenName="USD"
              >
                <template slot="title">
                  Limit Price:
                </template>
                <e-big-number
                  slot="value"
                  :value="order.orderDetails.limitPrice"
                  :decimals="FUTURES_PRICE_DECIMALS"
                  :decimal-places="activeContract.decimals"
                />
              </e-receipt-item>

               <e-receipt-item v-else-if="order.orderDetails.type === ORDER_TYPE_MARKET">
                <template slot="title">
                  Price for 1 {{ contractName }}:
                </template>
                <div
                  slot="value"
                  class="e-receipt-item__value"
                  :style="{ width: 'auto' }"
                >
                  MARKET
                </div>
              </e-receipt-item>

              <e-receipt-item
                v-if="order.orderDetails.type === ORDER_TYPE_STOP_LIMIT"
                tokenName="USD"
              >
                <template slot="title">
                  Stop Price:
                </template>
                <e-big-number
                  slot="value"
                  :value="order.orderDetails.stopPrice"
                  :decimals="FUTURES_PRICE_DECIMALS"
                  :decimal-places="activeContract.decimals"
                />
              </e-receipt-item>

              <e-receipt-item tokenName="USD">
                <template slot="title">
                  Order value:
                </template>
                <template slot="value">
                  <e-big-number :value="orderTotal" :decimals="FUTURES_AMOUNT_DECIMALS + FUTURES_AMOUNT_DECIMALS" :decimal-places="2"/>
                </template>
              </e-receipt-item>

              <e-receipt-item v-if="!positionToClose" :tokenName="currencySlug">
                <template slot="title">
                  Margin:
                </template>
                <template slot="value">
                  <e-big-number :value="collateral" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="decimalBalance"/>
                  <span class="the-order-confirm__collateral-usd" v-if="currencySlug !== 'DAI'">
                    ≈ <e-big-number v-if="collateralUsd" :value="collateralUsd" :decimal-places="2"/> USD
                  </span>
                </template>
              </e-receipt-item>

              <e-receipt-item v-if="!positionToClose">
                <template slot="title">
                  Leverage:
                </template>
                <template slot="value">
                  <span class="the-order-confirm__margin">
                    <e-big-number :value="activeContract.margin" :decimal-places="0" />x
                  </span>
                </template>
              </e-receipt-item>
            </div>

            <div class="the-order-confirm__wrapper-right">
              <div class="the-order-confirm__wrapper-title">
                Contract Details
              </div>

              <e-receipt-item>
                <template slot="title">
                  Asset:
                </template>
                <template slot="value">
                  <span class="the-order-confirm__value-light">
                    {{activeAsset ? activeAsset.label : activeContract.asset.replace(/USDT|USD/g, '') }}
                  </span>
                </template>
              </e-receipt-item>

              <e-receipt-item>
                <template slot="title">
                  Est. Liq. Price:
                </template>
                <template slot="value">
                  <e-big-number
                    v-if="activeContract.isModel"
                    slot="value"
                    :value="liquidation"
                    :decimals="FUTURES_PRICE_DECIMALS"
                    :decimal-places="activeContract.decimals"
                  />
                  <span v-else>-</span>
                </template>
              </e-receipt-item>

              <e-receipt-item>
                <template slot="title">
                  Expires:
                </template>
                <template slot="value">
                  <div class="the-order-confirm__small-wrapper">
                    <div class="the-order-confirm__small-title">Block</div>
                    <div class="the-order-confirm__small-value">
                      <span v-if="activeContract.isModel && !activeContract.perpetual">{{order.contractDetails.expirationBlock}}</span>
                      <span v-else>-</span>
                    </div>
                  </div>
                  <div class="the-order-confirm__small-wrapper">
                    <div class="the-order-confirm__small-title">Estimated time</div>
                    <div class="the-order-confirm__small-value">
                      <span v-if="activeContract.isModel && !activeContract.perpetual">{{estimatedTime}}</span>
                      <span v-else>-</span>
                    </div>
                  </div>
                </template>
              </e-receipt-item>

              <e-receipt-item>
                <template slot="title">
                  Funding Rate:
                </template>
                <template slot="value">
                  <div class="the-order-confirm__value-funding">
                    <span v-if="activeContract.isModel">
                      <span class="the-order-confirm__value-light">{{fundingRatePct}}%</span> / 8h
                    </span>
                    <span v-else>-</span>
                  </div>
                </template>
              </e-receipt-item>

              <e-receipt-item>
                <template slot="title">
                  <span class="the-order-confirm__settle-title">Settle Price URL:</span>
                </template>
                <template slot="value">
                  <div class="the-order-confirm__settle-url" v-if="activeAsset && activeContract.isModel">
                    <a :href="activeAsset.priceUrl" target="_blank">{{activeAsset.priceUrl}}</a>
                  </div>
                  <div v-else>-</div>
                </template>
              </e-receipt-item>
            </div>
          </div>
        </template>
      </e-receipt>
    </div>
  </transition-modal>
</template>

<script>
import { mapGetters, mapState, mapMutations } from 'vuex';
import { BigNumber } from 'bignumber.js';
import moment from 'moment';
import find from 'lodash/find';

import {
  BUY_TOKEN_ORDER,
  ETH_BASE_ADDRESS,
  BASE_TOKEN_BTC,
  ORDER_TYPE_MARKET,
  ORDER_TYPE_STOP_LIMIT,
  SELL_TOKEN_ORDER,
} from '@/util/constants';
import match from '@/util/match';

import futuresCalc from '@/util/futuresCalc';
import { setTourStep } from '@/api/backend/statistics';

import iconLink from '@/icons/e-icon_link.svg';

import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';
import EModal from '@/components/EModal';
import EBigNumber from '@/components/EBigNumber';
import EReceipt from '@/components/EReceipt';
import EReceiptItem from '@/components/EReceiptItem';
import EDemo from '@/components/EDemo';

import TransitionModal from '@/containers/transitions/TransitionModal';


export default {
  name: 'the-futures-order-popup',

  computed: {
    buyOrSell: {
      get() {
        return this.$store.state.futuresActiveOrder.buyOrSell;
      },
    },

    currencySlug() {
      if (this.baseToken === ETH_BASE_ADDRESS) {
        return 'ETH';
      } else if (this.baseToken === BASE_TOKEN_BTC) {
        return 'BTC';
      }

      return 'DAI';
    },

    currencyTicker() {
      if (this.baseToken === ETH_BASE_ADDRESS) {
        return 'ETHUSD';
      } else if (this.baseToken === BASE_TOKEN_BTC) {
        return 'BTCUSD';
      }

      return 'DAI';
    },

    isETH() {
      return this.baseToken === ETH_BASE_ADDRESS;
    },

    decimalBalance() {
      switch (this.currencySlug) {
        case 'DAI': return 2;
        default: return 4;
      }
    },

    liquidation() {
      if (!this.activeContract || !this.collateral || !this.order.orderDetails.amount || !this.order.orderDetails.limitPrice) {
        return null;
      }

      const calcLiqPriceFn = match(this.activeFuturesSmartContract.version, [
        [v => v <= 7, futuresCalc.calculateLiquidationPriceV7],
      ], futuresCalc.calculateLiquidationPriceV8);

      const liqPriceCalc = calcLiqPriceFn({
        size: this.order.orderDetails.amount,
        price: this.buyOrSell === 'buyToken' ? this.bestBidPrice : this.bestAskPrice,
        side: this.order.orderDetails.side ? 1 : 0,
        entryBlock: this.order.contractDetails.creationBlock,
        collateral: this.collateral,
      }, {
        fundingBlocks: 0,
        fundingRate: this.order.contractDetails.fundingRate,
        maintenanceMargin: this.activeContract.maintenanceMargin,
        multiplier: this.order.contractDetails.multiplier,
        fundingInterval: 2057, // Used only in V8
      });

      return liqPriceCalc.liquidationPrice;
    },

    activeAsset() {
      return find(this.assets, item => item.name === this.contractName.replace('USDT', 'USD'));
    },

    estimatedTime() {
      return moment().add(this.activeContract.expiresInSeconds, 's').format('DD MMM @ HH:mm');
    },

    fundingRatePct() {
      return this.order.contractDetails.fundingRate
        ? BigNumber(this.order.contractDetails.fundingRate)
          .times(2057)
          .div('1e18')
          .times(100)
          .toFormat(3)
        : '0.033';
    },

    ...mapGetters({
      ethUsdPrice: 'futuresActiveOrder/ethUsdPrice',
      activeWallet: 'wallets/activeWallet',
      demoStep: 'ui/demoStep',
      baseToken: 'balance/baseToken',
      contractBaseToken: 'futuresActiveContract/contractBaseToken',
      contractName: 'futuresActiveContract/contractName',
      positionToClose: 'futuresPositions/positionToClose',
      order: 'futuresActiveOrder/orderConfirmation',
      previousContractHash: 'futuresPositions/previousContractHash',
      assets: 'tokens/assets',
      bestAskPrice: 'futuresOrderBook/getBestAskPrice',
      bestBidPrice: 'futuresOrderBook/getBestAskPrice',
      theme: 'ui/getTheme',
    }),

    ...mapState({
      activeContract: state => state.futuresActiveContract.activeContract,
      activeFuturesSmartContract: state => state.contracts.activeFuturesContract,
    }),

    orderTotal() { return this.order ? this.order.orderDetails.limitPrice.times(this.order.orderDetails.amount) : 0; },

    displayedOrderType() {
      const prefix = {
        [ORDER_TYPE_MARKET]: 'Market',
        [ORDER_TYPE_STOP_LIMIT]: 'Stop',
      }[this.order.orderDetails.type] || '';
      const buyOrSell = {
        [BUY_TOKEN_ORDER]: 'Buy',
        [SELL_TOKEN_ORDER]: 'Sell',
      }[this.order.orderDetails.buyOrSell];
      return `${prefix} ${buyOrSell} ${this.contractName}`;
    },

    collateral() {
      if (!this.activeContract || !this.order.orderDetails.amount || !this.order.orderDetails.limitPrice) {
        return null;
      }

      // Use same collateral function, because leverage has not 8 decimals
      const calcCollFn = match(this.activeFuturesSmartContract.version, [
        [v => v <= 7, futuresCalc.calculateCollateralV7],
      ], futuresCalc.calculateCollateralV7);

      return calcCollFn(
        this.order.orderDetails.amount,
        this.order.orderDetails.limitPrice,
        this.activeContract.margin,
        this.order.contractDetails.multiplier,
      );
    },

    collateralUsd() {
      const col = this.collateral;

      if (!col || !this.ethUsdPrice) {
        return null;
      }

      return col.shiftedBy(-8).times(this.ethUsdPrice);
    },
  },

  async created() {
    Object.assign(this, { iconLink });

    // getOneExternalTicker(this.currencyTicker)
    //   .then((ticker) => {
    //     this.ethUsdPrice = ticker.lastPrice;
    //   });
  },

  methods: {
    ...mapMutations({ close: 'futuresActiveOrder/ORDER_REJECT_CONFIRMATION' }),

    submit() {
      if (this.demoStep && this.demoStep === 9 && window.innerWidth < 1280) {
        this.$scrollTo('#positionsTable');
      }

      if (this.demoStep && !this.positionToClose) {
        setTourStep({ walletAddress: this.activeWallet.address, step: 9 });
        this.$store.commit('ui/SET_DEMO_STEP', 10);
        this.$store.commit('ui/SET_NEWS_AND_ORDERS_TAB', 0);
        this.$store.commit('futuresOrders/SET_MY_ORDERS_MODE', 0);
      }
      if (this.demoStep && this.positionToClose) {
        setTourStep({ walletAddress: this.activeWallet.address, step: 13 });
        this.$store.commit('ui/SET_DEMO_STEP', 14);
        this.$store.commit('ui/SET_NEWS_AND_ORDERS_TAB', 0);
        this.$store.commit('futuresOrders/SET_MY_ORDERS_MODE', 0);
      }

      this.$store.dispatch('futuresActiveOrder/submitOrder', this.$store.state.futuresActiveOrder.orderConfirmation);

      if (this.previousContractHash) {
        this.$router.push({ name: 'future', params: { contract: this.previousContractHash } });
      }

      this.$store.commit('futuresPositions/SET_POSITION_TO_CLOSE', null);
      this.$store.commit('futuresPositions/SET_PREVIOUS_CONTRACT_HASH', null);
    },
  },

  components: {
    TransitionModal,
    EBigNumber,
    EButton,
    EIcon,
    EModal,
    EReceipt,
    EReceiptItem,
    EDemo,
  },
};
</script>

<style lang="scss">
.the-order-confirm {
  $this: &;
  position: relative;

  .e-modal__inner {
    overflow: visible;
  }

  &__demo {
    position: absolute;
    z-index: 20;
    top: 0;
    left: 0;
    transform: translate(calc(-100% - 10px), -15px);

    @media (max-width: 1279px) {
      top: auto;
      bottom: 0;
      left: 0;
      right: auto;
      transform: translate(16px, calc(100% + 86px));
    }
  }

  &__value {
    color: #0D105B;
    font-weight: 500;
    letter-spacing: 0;
    text-transform: uppercase;
  }

  &__wrapper {
    position: relative;
    width: 100%;
    display: flex;
  }

  &__wrapper-left,
  &__wrapper-right {
    flex: 0 0 270px;
    box-sizing: border-box;
    position: relative;

    @media only screen and (max-width: '640px') {
      flex: 0 0 calc(50% - 10px);
    }
  }

  &__wrapper-left {
    padding-right: 30px;
    border-right: 1px solid #e4eaec;

    @media only screen and (max-width: '640px') {
      padding-right: 10px;
    }
  }

  &__wrapper-right {
    padding-left: 30px;

    @media only screen and (max-width: '640px') {
      padding-left: 10px;
    }
  }

  .e-receipt-item_num  &__margin {
    color: #0097FE !important;
    font-weight: 500;

    .e-big-number {
      color: #0097FE !important;
    }
  }

  .e-modal_secondary .e-modal__body {
    width: 600px;
  }

  .e-receipt__popup-title {
    text-align: left;
  }

  &__wrapper-title {
    font-size: 10px;
    font-weight: 500;
    text-transform: uppercase;
    color: #FFFFFF;
    letter-spacing: 0;
    line-height: 20px;
    position: relative;
    text-align: left;
    margin-top: -6px;
    margin-bottom: 18px;

    &:after {
      content: '';
      display: block;
      position: absolute;
      width: 30px;
      height: 2px;
      background: #00DB9C;
      bottom: -8px;
      left: 0;
    }

    @media only screen and (max-width: '640px') {
      margin-top: 0;
      margin-bottom: 8px;

      &::after {
        bottom: -4px;
      }
    }
  }

  &__small-wrapper {
    position: relative;
    top: -12px;
    margin-bottom: 8px;
    width: 150px;

    &:last-child {
      margin-bottom: -14px;
    }
  }

  &__value-funding {
    width: 150px;
  }

  &__small-title {
    font-weight: 500;
    font-size: 9px;
    color: #C66D6D;
    letter-spacing: 0;
    text-align: right;
  }

  a {
    font-size: 13px;
    letter-spacing: -.5px;
    color: #0097FE;
    text-decoration: none;
  }

  .e-receipt-item__value {
    max-width: 120px;
  }

  &__collateral-usd {
    font-size: 10px;
    font-weight: 500;
    color: #8D9098;
    letter-spacing: 0.5px;
    text-align: right;
    position: absolute;
    right: 0;
    bottom: -13px;

    .e-big-number {
      color: #8D9098 !important;
    }
  }

  &__settle-title {
    @media only screen and (max-width: '640px') {
      display: none;
    }
  }

  &__settle-url {
    max-width: 130px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    text-align: left;
    color: #0097FE;

    @media only screen and (max-width: '640px') {
      min-width: 182px;
    }
  }

  &__dark {
    #{$this}__wrapper-left {
      border-color: #242937;
    }

    #{$this}__value-light {
      color: #D9D9D9;
    }

  }
}
</style>



