<template>
  <div class="the-futures-orders" :class="[ `the-futures-orders__${theme}`]">
    <!--<template v-if="isLogin && myOrdersPage.length !== 0 && !myOrdersPageLoading">-->
    <template>
      <div
        class="the-futures-orders__body the-futures-orders__body_no-pagination"
        :class="{ 'isActiveMode': isActiveMode }"
      >
        <!--:class="{ 'is-loading': myOrdersPageLoading }"-->
        <div class="the-futures-orders__table-head">
          <div class="the-futures-orders__table-col">Asset</div>
          <div class="the-futures-orders__table-col">Side</div>
          <div class="the-futures-orders__table-col">Created</div>
          <div class="the-futures-orders__table-col">Size</div>
          <div class="the-futures-orders__table-col">Price</div>
          <div v-if="isActiveMode" class="the-futures-orders__table-col">Stop price</div>
          <div class="the-futures-orders__table-col">Margin</div>
          <div class="the-futures-orders__table-col">Filled</div>
          <div v-if="isActiveMode" class="the-futures-orders__table-col">Action</div>
        </div>

        <e-container-scroll
          v-if="isLogin && myOrdersPage.length > 0"
          ref="scroll"
          class="the-futures-orders__wrapper"
        >
          <transition-list>
            <e-futures-order-item
              v-for="{
                futuresAsset,
                orderHash,
                type,
                amount,
                amountFilled,
                canceledAt,
                createdAt,
                fulfilledAt,
                isCanceled,
                price,
                stopPrice,
                isStopped,
                isActive,
                futuresContractHash,
                leverage,
              } in myOrdersPage"
              :key="orderHash"
              v-bind="{
                futuresAssetLabel: futuresAsset.name.replace(/USDT|USD/g, ''),
                futuresAssetDecimals: futuresAsset.decimals,
                futuresAssetBaseTokenAddress: futuresAsset.baseToken.address,
                futuresAssetBaseTokenSymbol: futuresAsset.baseToken.symbol,
                orderHash,
                amount,
                canceledAt,
                createdAt,
                fulfilledAt,
                isActive,
                isCanceled,
                unitPrice: price,
                stopPrice,
                isStopped,
                futuresContractHash,
                leverage,
              }"
              :ethUsdPrice="ethUsdPrice"
              :orderType="type"
              :amountFilled="amountFilled.total"
              :amountApproved="amountFilled.approved"
              :amountPanding="amountFilled.pending"
              :decimals="18"
              :tokenShortName="activeContractName"
              :tokenImage="activeContractName"
              :allowedDelete="isActiveMode && isActive"
              :isActiveMode="isActiveMode"
            />
          </transition-list>
        </e-container-scroll>

        <TheSendETHPlaceholder v-else />

        <e-order-attention-hint
          v-if="orderAttentionOffset > 0"
          v-click-outside="() => { orderAttentionOffset = -1 }"
          :style="{ top: `${orderAttentionOffset - 12}px` }"
        />

      </div>

      <!-- BEGIN: Pagination -->
      <!--<e-pagination
        v-if="pageCount > 1"
        class="the-trades__pagination"
        v-model="activePageNumber"
        :page-count="pageCount"
        type="small"
      />-->
      <!-- END: Pagination -->
    </template>

    <!--<div-->
      <!--v-else-if="!isLogin"-->
      <!--class="the-futures-orders__error the-futures-orders__error_margin"-->
    <!--&gt;-->
      <!--<e-panel-error :icon="errorIcon">-->
        <!--<template slot="title">-->
          <!--Your Orders-->
        <!--</template>-->
        <!--<template slot="descr">Here you'll find your orders. But you need to be logged in.</template>-->
      <!--</e-panel-error>-->
    <!--</div>-->


    <!--<div-->
      <!--v-else-if="isLogin"-->
      <!--class="the-futures-orders__error"-->
    <!--&gt;-->
      <!--<e-panel-error :icon="errorIcon">-->
        <!--<template slot="title">-->
          <!--Your Orders-->
        <!--</template>-->
        <!--<template slot="descr">Here you'll find your orders.</template>-->
      <!--</e-panel-error>-->
    <!--</div>-->

    <!-- Прелоудер который отображается на во время загрузки данных -->
    <!--<div
      v-if="myOrdersPageLoading"
      class="the-futures-orders__preloader"
    >
      <e-preloader/>
    </div>-->
  </div>
</template>

<script>
/**
 * Компонент для отображения моих ордеров
 *
 * @version 1.0.4
 */
import { BigNumber } from 'bignumber.js';
import { mapGetters, mapMutations, mapActions, mapState } from 'vuex';

import { MY_ORDERS_MODE_ACTIVE } from '@/store/constants';
import { ETH_ADDRESS, ETH_BASE_ADDRESS } from '@/util/constants';
import { getOneExternalTicker } from '@/api/backend';

// Сomponents
import TheSendETHPlaceholder from '@/containers/blocks/TheSendETHPlaceholder';
import EFuturesOrderItem from '@/components/EFuturesOrderItem';
import EContainerScroll from '@/components/EContainerScroll';
import EOrderAttentionHint from '@/components/EOrderAttentionHint';
import EPagination from '@/components/EPagination';
import EPanelError from '@/components/EPanelError';
import EPreloader from '@/components/EPreloader';
import ETransaction from '@/components/ETransaction';
import EButton from '@/components/EButton';
import ECopyText from '@/components/ECopyText';

// Transitions
import TransitionList from '@/containers/transitions/TransitionList';

// Icons
import errorIcon from '@/icons/e-icon_empty-trades.svg';

export default {
  name: 'the-futures-my-orders',

  data() {
    return {
      orderAttentionOffset: -1,
      errorIcon,

      // Текущая страница для отборажения
      activePageNumber: 0,

      ethUsdPrice: null,
    };
  },

  computed: {
    /**
     * Token balance
     * @returns {{privateBalance: BigNumber, contractBalance: BigNumber, inOrders: BigNumber, sum: BigNumber}}
     */
    balance() {
      return this.$store.getters['balance/getUserBalanceByToken'](ETH_ADDRESS);
    },

    isETH() {
      return this.baseToken === ETH_BASE_ADDRESS;
    },

    isActiveMode() {
      return this.mode === MY_ORDERS_MODE_ACTIVE;
    },

    // Количество страниц для отборажения
    pageCount() {
      return Math.ceil(this.totalNumberOfOrders / this.ordersPerPage);
    },

    myOrdersPage() {
      return this.$store.getters['futuresOrders/myOrdersPage'].slice(0, 20);
    },

    myOrdersPageLoading() {
      return this.$store.getters['futuresOrders/myOrdersPageLoading'] || !this.$store.getters['blockchain/gasPriceLoaded'];
    },

    ...mapGetters({
      baseToken: 'balance/baseToken',
      isLogin: 'wallets/isLogin',
      activeWallet: 'wallets/activeWallet',
      mode: 'futuresOrders/myOrdersMode',
      ordersPerPage: 'futuresOrders/ordersPerPage',
      totalNumberOfOrders: 'futuresOrders/totalNumberOfOrders',
      activeContractName: 'futuresActiveContract/contractName',
      theme: 'ui/getTheme',
    }),

    ...mapState({
      activeContract: state => state.futuresActiveContract.activeContract,
    }),
  },

  watch: {
    mode() {
      this.setPage(0);
      this.loadMyOrdersPage();
    },

    activeContract() {
      this.setPage(0);
      this.loadMyOrdersPage();
    },

    activePageNumber(value) {
      this.setPage(value);
      this.loadMyOrdersPage();

      if (this.$refs.scroll && this.$refs.scroll.$el) {
        this.$refs.scroll.$el.scrollTop = 0;
      }
    },

    activeWallet: 'initMyOrders',
  },

  created() {
    this.loadMyOrdersPage();
    this.loadEthUsdPrice();
    this.refreshEthUsdPriceIntervalId = setInterval(this.loadEthUsdPrice.bind(this), 60e3);
    this.$store.dispatch('futuresOrders/listenToOrdersChanges');
    this.$store.dispatch('blockchain/listenToGasPriceChanges');
  },

  beforeDestroy() {
    clearInterval(this.refreshEthUsdPriceIntervalId);
    this.$store.dispatch('futuresOrders/stopListeningToOrdersChanges');
    this.$store.dispatch('blockchain/stopListeningToGasPriceChanges');
  },

  methods: {
    ...mapMutations({
      setPage: 'futuresOrders/SET_MY_ORDERS_PAGE_NUMBER',
    }),

    ...mapActions({
      initMyOrders: 'futuresOrders/initMyOrders',
      loadMyOrdersPage: 'futuresOrders/loadMyOrdersPage',
    }),

    async loadEthUsdPrice() {
      const ticker = await getOneExternalTicker('ETHUSD');
      this.ethUsdPrice = BigNumber(ticker.lastPrice);
    },

    startDeposit() {
      if (this.isLogin) {
        this.$store.dispatch('deposit/startDeposit', this.baseToken);
      } else {
        this.$router.push({ name: 'signup' });
      }
    },

    handleSignUp() {
      this.$router.push({ name: 'signup' });
    },

    handleGenerate() {
      this.$router.push({ name: 'create-wallet' });
    },
  },

  components: {
    EContainerScroll,
    EOrderAttentionHint,
    EPagination,
    EPanelError,
    EPreloader,
    ETransaction,
    TransitionList,
    EFuturesOrderItem,
    EButton,
    TheSendETHPlaceholder,
    ECopyText,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-futures-orders {
  $this: &;

  position: relative;
  height: calc(100% - 32px);
  overflow-x: auto;

  &::-webkit-scrollbar {
    width: 2px;
    height: 3px;
  }

  &::-webkit-scrollbar-track {
    background: $color-violet;
    border-radius: 2px;
  }

  &::-webkit-scrollbar-thumb {
    background: $color-blue;
    border-radius: 2px;
  }

  .transition-list-container {
    padding-bottom: 0;
  }

  &__tabs {
    padding-left: 11px;
    margin-bottom: 11px;
  }

  &__wrapper {
    /*height: calc(100% - 21px);*/
  }

  &__table-head,
  &__table-body-item {
    display: flex;
    align-items: center;
  }

  &__table-head {
    font-size: 9px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: .5px;
    color: #5C6062;
    background: #FBFBFD;
    border-bottom: 1px solid #F0F6F9;
    /*padding-right: 2px;*/
  }

  &__table-body-item {
    height: 35px;
    background: #232323;
    border-radius: 2px;

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__table-col,
  .e-futures-order__table-col {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    align-self: stretch;
    flex: 1 1 20%;
    padding: 8px 7px;
    box-sizing: border-box;
    border-right: 1px solid #F0F6F9;

    &:nth-child(1) {
      flex: 0 0 90px;
      justify-content: flex-start;

      @include r(800) {
        flex: 0 0 70px;
      }
    }

    &:nth-child(2) {
      flex: 0 0 54px;
      justify-content: flex-start;
    }

    &:nth-child(3) {
      flex: 1 1 24%;
      @include r(800) {
        flex: 0 0 145px;
      }
    }

    &:nth-child(8) {
      flex: 0 0 59px;
    }

    &:last-child {
      border: 0;
    }

    .e-big-number {
      font-size: 11px;
      font-weight: 500;
      position: relative;
    }
  }

  .e-futures-order {
    color: #09168a;
    background: #fff;
    border-bottom: 1px solid #F0F6F9;
  }

  &__error {
    height: 100%;
    width: 100%;
    box-sizing: border-box;
  }

  &__error_margin {
    margin-top: 0;
  }

  &__body {
    position: relative;
    height: 100%;
    min-width: 750px;
    transition: opacity 0.5s ease-in-out;

    &.is-loading {
      opacity: 0.2;
    }

    &.isActiveMode {
      #{$this}__table-col,
      .e-futures-order__table-col {
        &:nth-child(8) {
          flex: 0 0 80px;
        }
        &:nth-child(9) {
          flex: 0 0 59px;
        }
      }
    }
  }

  &__preloader {
    position: absolute;
    top: 0;
    left: 0;
    right: 10px;
    bottom: 0;
  }

  .e-panel-error {
    box-shadow: none;
    min-height: 138px;

    @include r(1279) {
      min-height: 150px;
    }
  }

  .e-container-scroll {
    height: calc(100% - 32px);
  }

  .e-futures-order__table-col-asset {
    font-size: 11px;
    font-weight: 500;
  }

  &__placeholder-container {
    width: 100%;
    height: calc(100% - 34px);
    min-height: 90px;
    padding: 15px 0;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 13px;
    font-weight: 500;

    @include r(1279) {
      width: calc(100vw - 20px);
    }
  }

  &__placeholder-login {
    color: #09168a;
    text-align: center;

    .e-button_size_ss {
      font-size: 11px;
      font-weight: 500;
    }
  }

  &__placeholder-deposit {
    max-width: 550px;
    color: #09168a;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__placeholder-deposit-contract {
    color: #09168a;
    max-width: 320px;
    line-height: 15px;
    text-align: center;
  }

  &__placeholder-deposit-qr {
    width: 68px;
    height: 68px;
    flex: 0 0 68px;
    background: #fff;
    padding: 4px;
    box-sizing: border-box;
    margin-right: 24px;

    @include r(1279) {
      width: 62px;
      height: 62px;
      flex: 0 0 62px;
      padding: 1px;
      margin-right: 5px;
    }
  }

  &__placeholder-deposit-content {
    .e-copy-text__head {
      display: none;
    }

    .e-copy-text__hash {
      font-size: 15px;

      @include r(1279) {
        font-size: 9px;
      }
    }

    .e-copy-text__btns {
      top: 7px;
      right: -50px;

      @include r(1279) {
        right: -10px;
      }
    }

    .e-copy-text__btn {
      min-width: 0;
    }

    .e-copy-text__in {
      padding-right: 0;
      padding-left: 0 !important;
    }

    .e-button {
      width: 42px;
      height: 14px;
      background: #0097FE !important;
      color: #fff;
      border-radius: 2px;
      padding-left: 8px;
      padding-right: 8px;

      &:hover {
        transform: scale(1);
      }

      .e-icon {
        display: none;
      }

      .e-copy-text__btn-text {
        color: #fff !important;
        height: 14px;
        font-size: 9px;

        span {
          display: block;
          height: 14px;
          box-sizing: border-box;
        }
      }
    }

    @include r(1279) {
      font-size: 11px;
    }
  }

  &__placeholder-login-buttons,
  &__placeholder-buttons {
    text-align: center;
    margin-top: 10px;

    .e-button {
      margin: 0 10px;
      padding-left: 15px;
      padding-right: 15px;
    }

    .e-button_fill_green-transparent {
      color: #0fb988;
    }
  }

  &__placeholder-buttons {
    .e-button {
      margin: 0 4px;
    }
  }

  &__placeholder-login-buttons-separator {
    text-transform: uppercase;
    font-size: 9px;
  }

  &__placeholder-no-orders {
    color: #0123e7;
  }

  &__highlight {
    color: #00DB9C;
  }

  &__link {
    text-transform: uppercase;
    font-size: 10px;
    color: #0097FE;
    text-decoration: none;
    transition: color .15s ease-in-out;

    &:hover {
      color: #16aafe;
    }
  }

  &__dark {
    &::-webkit-scrollbar-track {
      background-color: #212121;
    }

    &::-webkit-scrollbar-thumb {
      background-color: $color-dark-grey;
    }

    #{$this}__table-col,
    .e-futures-order {
      background: #121722;
      border-color: #282C38;
    }

    #{$this}__table-head {
      color: $color-dark-grey;
      background: #1C222D;
      border-bottom-color: #282C38;
    }

    #{$this}__table-head #{$this}__table-col {
      background: #1C222D;
    }

    .e-futures-order__table-col {
      border-color: #282C38;
    }

    .e-futures-order {
      color: $color-dark-grey;
    }

    .e-futures-order__table-col-asset {
      color: #fff;
    }

    #{$this}__placeholder-login,
    #{$this}__placeholder-deposit,
    #{$this}__placeholder-deposit-contract {
      color: #D9D9D9;
    }

    #{$this}__placeholder-login-buttons {
      .e-button_fill_green-transparent {
        color: #fff;
      }
    }

    #{$this}__placeholder-no-orders {
      color: #D9D9D9;
    }

    #{$this}__placeholder-deposit-content {
      .e-copy-text__hash {
        color: #fff;
      }
    }

    #{$this}__placeholder-buttons {
      .e-button_fill_green-semi-transparent {
        color: #00DB9C;
      }

      .e-button_fill_red-semi-transparent {
        color: #fff;
      }
    }
  }
}
</style>



