<template>
  <transition
    appear
    :duration="400"
  >
    <div
      class="e-transaction"
      :class="[
        type === BUY_TOKEN_ORDER ? 'e-transaction_buy' : 'e-transaction_sell',
        `e-transaction__${theme}`,
        { 'is-my': isMy, 'e-transaction__appear': isNew, },
      ]"
      @click="$emit('click', $event)"
    >
      <div class="e-transaction__bg"></div>
      <div
        class="e-transaction__col e-transaction__name"
        :class="type === BUY_TOKEN_ORDER ? 'e-transaction__name-buy' : 'e-transaction__name-sell'"
      >
        <e-big-number :value="amountFilled" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="amountDec"/>
      </div>

      <div class="e-transaction__col e-transaction__detail" :class="[type === BUY_TOKEN_ORDER ? 'e-transaction__detail-buy' : 'e-transaction__detail-sell']">
        <!--<div class="e-transaction__name-icon"><e-icon :src="type === BUY_TOKEN_ORDER ? iconBuy : iconSell"/></div>-->
        <e-big-number :value="price" :decimals="FUTURES_PRICE_DECIMALS" :decimal-places="decimalsAsset"/>
      </div>

      <div
        class="e-transaction__col e-transaction__time e-transaction__right-align"
        :class="confirmationStateClass"
      >
        <div class="e-transaction__time-text">{{ formattedDate }}</div>
        <!--<div class="e-transaction__time-status">-->
          <!--<div class="e-transaction__hint">{{ confirmationStateLabel }}</div>-->
        <!--</div>-->
      </div>

      <div
        class="e-transaction__col e-transaction__result e-transaction__right-align"
        :class="type === BUY_TOKEN_ORDER ? 'e-transaction__result-buy' : 'e-transaction__result-sell'"
      >
        <e-big-number :value="price.times(amountFilled)"
          :decimals="FUTURES_PRICE_DECIMALS + FUTURES_AMOUNT_DECIMALS"
          :decimal-places="2"
        />
      </div>

      <div class="e-transaction__col e-transaction__exchange" :class="type === BUY_TOKEN_ORDER ? 'e-transaction__result-buy' : 'e-transaction__result-sell'">
        {{exchange}}
      </div>
    </div>
  </transition>
</template>

<script>

/**
 * Component: Оторажение одного трейда внутри TheTrades
 *
 * @version 1.0.2
 */
import { mapGetters } from 'vuex';

import { BigNumber } from 'bignumber.js';
import { tradesDateHourMinSec } from '@/util/dates';
import { APPROVED_STATUS, PENDING_STATUS, DECLINED_STATUS, BUY_TOKEN_ORDER, EXCHANGE_ETHERMIUM } from '@/util/constants';

// Components
import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber';
import EMarketName from '@/components/EMarketName';

// Icons
import iconSell from '@/icons/e-icon_loss.svg';
import iconBuy from '@/icons/e-icon_profit.svg';
import iconEth from '@/icons/e-icon_earned.svg';

const confirmationStateClasses = {
  [PENDING_STATUS]: 'e-transaction__time_pending',
  [APPROVED_STATUS]: 'e-transaction__time_confirmed',
  [DECLINED_STATUS]: 'e-transaction__time_declined',
};

const confirmationStateLabels = {
  [PENDING_STATUS]: 'Pending',
  [APPROVED_STATUS]: 'Confirmed',
  [DECLINED_STATUS]: 'Declined',
};

export default {
  name: 'e-futures-transaction',

  data() {
    return {
      iconEth,
      iconBuy,
      iconSell,
      isNew: false,
    };
  },

  props: {
    // Тип трейда (покупка/проадажа)
    type: {
      type: String,
      default: BUY_TOKEN_ORDER,
    },

    // количество токена
    amountFilled: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // Цена по которой была проведена сделка
    price: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // Дата и время проведения сделки
    time: {
      type: Date,
      default: null,
    },

    // Биржа
    exchange: {
      type: String,
      default: EXCHANGE_ETHERMIUM,
    },

    confirmationState: {
      type: String,
      default: PENDING_STATUS,
      validator: v => [PENDING_STATUS, APPROVED_STATUS, DECLINED_STATUS].includes(v),
    },

    isMy: {
      type: Boolean,
      default: false,
    },

    // Количество знаков после запятой у токена
    decimals: {
      type: Number,
      default: 0,
    },

    amountDec: {
      type: Number,
      default: 0,
    },

    decimalsAsset: {
      type: Number,
      default: 2,
    },
  },

  computed: {
    confirmationStateClass() {
      return confirmationStateClasses[this.confirmationState];
    },

    confirmationStateLabel() {
      return confirmationStateLabels[this.confirmationState];
    },

    formattedDate() {
      return this.time !== null
        ? tradesDateHourMinSec(this.time)
        : '';
    },

    ...mapGetters({
      theme: 'ui/getTheme',
    }),
  },

  mounted() {
    this.isNew = true;
    setTimeout(() => { this.isNew = false; }, 120);
  },

  components: {
    EIcon,
    EBigNumber,
    EMarketName,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-transaction {
  $this: &;

  display: flex;
  min-height: 26px;
  box-sizing: border-box;
  transform: translate3d(0, 0, 0);
  position: relative;
  z-index: 1;
  border-bottom: 1px solid #F0F6F9;
  padding: 0;

  &.is-my {
    cursor: pointer;

    &:hover {
      &:before {
        background-color: #f3f6ff;
      }
    }
  }

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -2;
    border-radius: 2px;
    background-color: #fff;
    // transition: $transition-time;
    transition: all .08s ease-in-out;
  }

  &__appear {
    #{$this}__bg {
      opacity: 0.12;
    }
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    border-radius: 2px;
    opacity: 0;
  }

  &.e-transaction_sell &__bg {
    background-color: $color-red;
  }

  &.e-transaction_buy &__bg {
    background-color: $color-green;
  }

  &__col {
    white-space: nowrap;
    padding: 8px 3px;
    box-sizing: border-box;
    border-right: 1px solid #F0F6F9;

    @include r(1439) {
      padding: 8px 5px;
    }
  }

  &__market-name {
    display: inline-block;
    font-size: 11px;
    text-transform: uppercase;
    font-weight: 800;
    color: #0D105B;
    border: 1px solid #d9d9e2;
    border-radius: 4px;
    padding: 0 2px;
    line-height: 15px;
    margin-right: 2px;
    transition: border-color .15s ease-in-out, color .15s ease-in-out;

    &:hover {
      border-color: #0e30e2;
      color: #0e30e2;
    }
  }

  &__time {
    align-items: center;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-end;
    order: 4;
    flex: 0 0 54px;
    padding-right: 4px;
    padding-left: 4px;
    font-size: 11px;
  }

  &__exchange {
    font-size: 10px;
    flex: 0 0 24px;
    order: 4;
    border-right: 0;
    text-align: center;
  }

  &__time-text {
    position: relative;
    font-size: 11px;
    z-index: 2;
    font-weight: 400;
    transition: 0.3s;
    white-space: nowrap;
    /*color: $color-grey;*/

    &:hover {
      #{$this}__hint {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  &__time-status {
    border: 2px solid;
    border-radius: 50%;
    min-height: 8px;
    margin-left: 5px;
    margin-top: -1px;
    margin-right: 10px;
    min-width: 8px;
    position: relative;
    z-index: 2;

    &:hover {
      #{$this}__hint {
        visibility: visible;
        opacity: 1;
      }
    }

    @include r(1439) {
      margin-right: 3px;
    }
  }

  &__time_pending {
    #{$this}__time-status {
      background-color: lighten($color-orange, 38%);
      border-color: $color-orange;
    }
  }

  &__time_confirmed {
    #{$this}__time-status {
      background-color: lighten($color-green-bright, 38%);
      border-color: $color-green-bright;
    }
  }

  &__time_declined {
    #{$this}__time-status {
      background-color: lighten($color-red, 38%);
      border-color: $color-red;
    }
  }

  &__icon {
    position: absolute;
    right: -18px;
    top: 0;
    width: 13px;
    height: 13px;
  }

  &__name {
    font-size: 11px;
    font-weight: 400;
    text-transform: uppercase;
    position: relative;
    box-sizing: border-box;
    order: 2;
    flex: 0 0 calc(30% - 23px);
    transition: color .15s ease-in-out;
    display: flex;
    justify-content: flex-end;

    .e-big-number {
      &__fraction {
        opacity: 0.75;
        letter-spacing: 0;
        margin-left: 1px;
      }
    }
  }

  &__name_sell {
    justify-content: flex-end;

    .e-big-number {
      // color: $color-red;
    }

    #{$this}__shortname {
      // color: $color-red;
      // opacity: 0.5;
    }
  }

  &__name_buy {
    justify-content: flex-end;

    .e-big-number {
      // color: $color-green-bright;
    }

    #{$this}__shortname {
      // color: $color-green;
      // opacity: 0.5;
    }
  }

  &__name-icon {
    display: inline-block;
    position: relative;
    height: 11px;
    width: 12px;
    top: 2px;
    left: 0;
    margin-right: 3px;
  }

  &__detail {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: $color-ultramarine;
    letter-spacing: 0;
    font-size: 11px;
    font-weight: 400;
    text-transform: uppercase;
    box-sizing: border-box;
    flex: 0 0 calc(40% - 40px);
    transition: color .15s ease-in-out;
    order: 1;

    .e-big-number {
      .e-big-number__fraction {
        opacity: 0.75;
      }
    }
  }

  &_buy #{$this}__time {
    color: $color-green-bright;
  }

  &_sell #{$this}__time {
    color: $color-red;
  }

  &__detail-buy,
  &__name-buy,
  &__result-buy {
    color: $color-green-bright;
  }

  &__detail-sell,
  &__name-sell,
  &__result-sell {
    color: $color-red;
  }

  &__detail-icon {
    height: 10px;
    width: 10px;
    display: inline-block;
    position: relative;
    top: 1px;
    left: -3px;

    .e-icon svg {
      fill: #cfcfcd;
    }

    &_decrease {
      .e-icon svg {
        fill: $color-red;
      }
    }

    &_increase {
      .e-icon svg {
        fill: $color-green-bright;
      }
    }
  }

  &__result {
    letter-spacing: 0;
    font-size: 11px;
    font-weight: 400;
    text-transform: uppercase;
    position: relative;
    box-sizing: border-box;
    order: 3;
    flex: 0 0 calc(30% - 15px);
    transition: color .15s ease-in-out;

    .e-big-number {
      &__fraction {
        opacity: 0.75;
        letter-spacing: 0;
        margin-left: 1px;
      }
    }
  }

  &__result-icon {
    width: 10px;
    height: 10px;
    display: inline-block;
    position: absolute;
    top: 3px;
    right: -1px;

    .e-icon svg {
      fill: $color-semi-dark-blue;
      transition: fill .15s ease-in-out;
    }
  }

  &__right-align {
    text-align: right;
  }

  &__hint {
    position: absolute;
    visibility: hidden;
    opacity: 0;
    transition: 0.3s;
    top: -7px;
    right: 12px;
    background-color: #2D2F72;
    color: #fff;
    font-size: 11px;
    font-weight: 400;
    line-height: 1;
    box-shadow: 0 3px 10px rgba(148, 165, 180, 0.14);
    border-radius: 3px;
    padding: 5px 10px 5px;
    white-space: nowrap;
  }

  &__dark {
    border-color: #282C38;

    &::before {
      background: $dark-background-lighter;
    }

    &.e-transaction_buy {
      #{$this}__name-icon {
        .e-icon_profit-fill {
          fill: #1FCE43;
        }
      }
    }

    #{$this}__result-icon .e-icon svg {
      fill: $color-blue-dark-mode;
    }

    /*#{$this}__time-text {
      color: #8d9098;
    }*/

    #{$this}__name-icon svg path:nth-child(2) {
      fill: #232323;
    }

    .e-market-name__exchange {
      border-color: #3a3b3c;
      color: #8d9098;

      &:hover {
        border-color: $color-blue-dark-mode;
        color: $color-blue-dark-mode;
      }
    }

    &.is-my {
      &:hover {
        &:before {
          background-color: #1a1a1a;
        }
      }
    }

    #{$this}__hint {
      color: #fff;
      background: $color-blue-dark-mode;
    }

    #{$this}__time_pending #{$this}__time-status {
      background-color: rgba($color-orange, 0.3);
    }

    #{$this}__time_confirmed #{$this}__time-status {
      background-color: rgba($color-green-bright, .3);
    }

    #{$this}__time_declined #{$this}__time-status {
      background-color: rgba($color-red, .3);
    }

    #{$this}__col {
      background: #121722;
      border-color: #282C38;
    }

    &.e-transaction_buy #{$this}__time {
      color: #00DB9C;
    }

    #{$this}__detail-buy,
    #{$this}__name-buy,
    #{$this}__result-buy {
      color: #00DB9C;
    }
  }
}
</style>




