<template>
  <e-modal
    class="the-trade-details-popup"
    :secondaryDesign="true"
    :autoHeight="true"
    :btnClose="false"
    :darkBackground="true"
    @close="close"
  >
    <div slot="inner">
      <div class="e-receipt">

        <div class="e-receipt__header">
          <div class="e-receipt__popup-title">
            {{title}}
            <div
              class="e-receipt__trade-status"
              :class="isConfirmed ? 'e-receipt__trade-status_confirmed'
                : isConfirmed === null ? 'e-receipt__trade-status_pending'
                  : 'e-receipt__trade-status_decline'"
              v-if="status"
            >
              {{ isConfirmed ? 'Approved'
                  : isConfirmed === null ? 'Pending' : 'Decline' }}
            </div>
          </div>
        </div>

        <div class="e-receipt__body">
          <div class="e-receipt__body-wrapper e-receipt__body-wrapper_background-dark">
            <slot name="header"></slot>
          </div>
          <div
            v-if="$slots.body"
            class="e-receipt__body-wrapper"
            >
            <div class="e-receipt__item">
              <slot name="body"></slot>
            </div>
            <div class="e-receipt__line">
              <e-icon :src="iconDashedLine" />
            </div>
            <div class="e-receipt__item">
              <slot name="footer"></slot>
            </div>
          </div>
        </div>

        <div class="e-receipt__footer">
          <div
            v-if="btnLink === '0x0'"
            class="e-receipt__internal-trade"
          >
            Internal trade
          </div>
          <button
            v-else-if="btnLink"
            class="e-receipt__link e-receipt__pin"
            @click="openLink"
          >
            <div class="e-receipt__link-icon">
              <e-icon :src="iconLink" />
            </div>
            <span>Etherscan Link</span>
          </button>
          <div
            v-else-if="btnLink !== ''"
            class="e-receipt__btns"
          >
            <button class="e-receipt__btn e-receipt__btn_cancel e-receipt__pin" @click="close">
              cancel
            </button>
            <button
              class="e-receipt__btn e-receipt__btn_confirm e-receipt__pin"
              @click="confirm"
              data-test="confirmOrder"
            >
              sign order
            </button>
          </div>
        </div>

      </div>
    </div>
  </e-modal>
</template>

<script>
import iconLink from '@/icons/e-icon_link-second.svg';
import iconDashedLine from '@/icons/e-icon_dashed-line.svg';

import EModal from '@/components/EModal';
import EIcon from '@/components/EIcon';

export default {
  name: 'e-receipt',

  props: {
    title: {
      type: String,
      default: '',
    },

    status: {
      type: Boolean,
      default: false,
    },

    isConfirmed: {
      type: Boolean,
      default: null,
    },

    // Ссылка, которая должна открываться при нажатии на кнопку
    btnLink: {
      type: String,
      default: null,
    },
  },

  created() {
    Object.assign(this, {
      iconLink,
      iconDashedLine,
    });
  },

  methods: {
    close() {
      this.$emit('cancel');
    },

    confirm() {
      this.$emit('confirm');
    },

    /*
     * Функция открывает в новой вкладке ссылку переданную в кнопку попапа
     */
    openLink() {
      const win = window.open(this.btnLink, '_blank');
      if (win) {
        win.focus();
      }
    },
  },

  components: {
    EModal,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-receipt{
  $this: &;

  &__header {
    background-color: $color-white;
    box-shadow: 0 0 3px 0 rgba(0,0,0,0.2);
    border-radius: 3px 3px 0 0;
    color: $color-dark-blue;
    font-size: 16px;
    font-weight: 800;
    line-height: 1.375;
    padding: 20px 30px;
    position: relative;
  }

  &__trade-status {
    font-size: 13px;
    font-weight: 700;
    padding-left: 20px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 30px;

    &:before {
      background-color: lighten($color-green-bright, 38%);
      border: 2px solid $color-green-bright;
      border-radius: 50%;
      content: '';
      height: 8px;
      position: absolute;
      top: 2px;
      left: 0;
      width: 8px;
    }
  }

  &__trade-status_pending {
    &:before {
      background-color: lighten($color-orange, 38%);
      border-color: $color-orange;
    }
  }

  &__trade-status_decline {
    &:before {
      background-color: lighten($color-red, 38%);
      border-color: $color-red;
    }
  }

  &__body-wrapper {
    background-color: $color-white;
    box-shadow: 0 12px 24px 0 rgba(#94A5B4, 0.14);
    padding: 30px;

    @include r(640) {
      padding: 18px 10px;
    }
  }

  &__body-wrapper_background-dark {
    background-color: $color-light-blue;
  }

  &__body_background-light {
    #{$this}__info-title {
      color: #46484C;
      font-size: 13px;
      font-weight: 800;
    }
  }

  &__internal-trade {
    align-items: center;
    background-color: $color-grey-light;
    color: $color-blue;
    display: flex;
    flex-direction: row;
    font-size: 12px;
    font-weight: 800;
    padding: 17px;
    text-transform: uppercase;
    transition: background-color ease $transition-time, color ease $transition-time;
    text-decoration: none;
    justify-content: center;
    width: 100%;
    box-sizing: border-box;
  }

  &__pin {
    align-items: center;
    background-color: $color-grey-light;
    color: $color-blue;
    display: flex;
    flex-direction: row;
    font-size: 12px;
    font-weight: 800;
    padding: 17px;
    text-transform: uppercase;
    transition: background-color ease $transition-time, color ease $transition-time;
    text-decoration: none;
    justify-content: center;
    width: 100%;
    box-sizing: border-box;
  }

  &__link-icon {
    height: 7px;
    margin-right: 6px;
    width: 14px;

    .e-icon svg {
      fill: $color-blue;
      transition: fill ease $transition-time;
    }
  }

  &__btns {
    display: flex;
  }

  &__btn_confirm {
    background-color: $color-green-bright;
    color: #ffffff;

    &:hover {
      background-color: darken($color-green-bright, 5%);
      color: #ffffff;
    }
  }

  &__icon {
    display: inline-block;
    height: 11px;
    margin-right: 8px;
    width: 12px;
  }

  &__line {
    margin-top: 10px;
    margin-bottom: 20px;
    height: 2px;
  }
}
</style>



