<template>
  <div
    class="the-futures-orders__placeholder-container"
    :class="[ `the-futures-orders__${theme}`]"
  >
    <div v-if="!isLogin" class="the-futures-orders__placeholder-login">
      To <span class="the-futures-orders__highlight">open a position</span>, first you need to:
      <div class="the-futures-orders__placeholder-login-buttons">
        <e-button fill="green-pale" type="squared" size="ss" @click="showLoginFlow">Connect a Wallet</e-button>
        <span class="the-futures-orders__placeholder-login-buttons-separator">or</span>
        <e-button fill="green-transparent" type="squared" size="ss" @click="handleGenerate">Create a New Wallet</e-button>
      </div>
    </div>

     <div v-else-if="balance.privateBalance.isZero() && balance.availableBalance.isZero() && myOrdersPage.length === 0" class="the-futures-orders__placeholder-deposit">

      <div class="the-futures-orders__placeholder-deposit-qr">
        <qriously :value="activeWallet.address" :size="60" />
      </div>

      <div class="the-futures-orders__placeholder-deposit-content">
        <span v-if="APP_ENV === 'main'">
          Send a minimum of <span class="the-futures-orders__highlight">0.2 ETH</span> to your wallet address:
        </span>

        <span v-else>
          We have sent you 100 ETH (demo) to try out the platform. Please wait a minute for the funds to reach your account. If the balance doesn’t update automatically press the refresh button.
        </span>

        <e-copy-text
          :hash="activeWallet.address"
          compact
          small-hash-size
          btn-name="Copy"
        />

        <a v-if="APP_ENV === 'main'" href="https://dmexhelp.freshdesk.com/support/solutions/articles/47000309730-how-to-get-eth-" target="_blank" class="the-futures-orders__link">
          Buy eth with bank transfer
        </a>
        <br v-if="APP_ENV === 'main'">
        <a v-if="APP_ENV === 'main'" :href="`https://indacoin.com/gw/fiat/?partner=bitcoinist&cur_from=USD&cur_to=ETH&amount=100.00&user_id=${activeWallet.address}&address=${activeWallet.address}`" target="_blank" class="the-futures-orders__link">
          Buy eth with credit card
        </a>
      </div>
    </div>

     <div v-else-if="balance.availableBalance.isZero() && myOrdersPage.length === 0" class="the-futures-orders__placeholder-deposit-contract">
      To create an order, you need to <span class="the-futures-orders__highlight">deposit {{isETH ? 'ETH' : btcBaseToken ? 'BTC' : 'DAI' }}</span> from your Wallet Balance to your Contract Balance

      <div class="the-futures-orders__placeholder-buttons">
        <e-button fill="green-semi-transparent" type="squared" size="ss" @click="startDeposit">Deposit Now</e-button>
        <a href="https://dmexhelp.freshdesk.com/a/solutions/articles/47000160750" target="_blank">
          <e-button fill="red-semi-transparent" type="squared" size="ss">How it Works?</e-button>
        </a>
      </div>
    </div>

    <div
      v-else
      class="the-futures-orders__placeholder-no-orders"
    >
      {{isPositions ? 'No open positions' : 'No active orders'}}
    </div>
  </div>
</template>

<script>
/**
 * Компонент для отображения моих ордеров
 *
 * @version 1.0.4
 */
import { mapGetters, mapMutations, mapActions, mapState } from 'vuex';
import Qriously from 'vue-qriously/src/components/v-qriously';

import { MY_ORDERS_MODE_ACTIVE } from '@/store/constants';
import { ETH_ADDRESS, ETH_BASE_ADDRESS, DAI_BASE_ADDRESS } from '@/util/constants';

// Сomponents
import EButton from '@/components/EButton';
import ECopyText from '@/components/ECopyText';

// Icons
import errorIcon from '@/icons/e-icon_empty-trades.svg';

export default {
  name: 'the-futures-my-orders',

  data() {
    return {
      orderAttentionOffset: -1,
      errorIcon,

      // Текущая страница для отборажения
      activePageNumber: 0,
    };
  },

  props: {
    isPositions: { type: Boolean },
  },

  computed: {
    /**
     * Token balance
     * @returns {{privateBalance: BigNumber, contractBalance: BigNumber, inOrders: BigNumber, sum: BigNumber}}
     */
    balance() {
      return this.$store.getters['balance/getUserBalanceByToken'](ETH_ADDRESS);
    },

    isETH() {
      return this.baseToken === ETH_BASE_ADDRESS;
    },

    isDAI() {
      return this.baseToken === DAI_BASE_ADDRESS;
    },

    isActiveMode() {
      return this.mode === MY_ORDERS_MODE_ACTIVE;
    },

    // Количество страниц для отборажения
    pageCount() {
      return Math.ceil(this.totalNumberOfOrders / this.ordersPerPage);
    },

    myOrdersPage() {
      return this.$store.getters['futuresOrders/myOrdersPage'].slice(0, 20);
    },

    myOrdersPageLoading() {
      return this.$store.getters['futuresOrders/myOrdersPageLoading'] || !this.$store.getters['blockchain/gasPriceLoaded'];
    },

    ...mapGetters({
      baseToken: 'balance/baseToken',
      btcBaseToken: 'balance/btcBaseToken',
      isLogin: 'wallets/isLogin',
      activeWallet: 'wallets/activeWallet',
      mode: 'futuresOrders/myOrdersMode',
      ordersPerPage: 'futuresOrders/ordersPerPage',
      totalNumberOfOrders: 'futuresOrders/totalNumberOfOrders',
      activeContractName: 'futuresActiveContract/contractName',
      theme: 'ui/getTheme',
    }),

    ...mapState({
      activeContract: state => state.futuresActiveContract.activeContract,
    }),
  },

  watch: {
    mode() {
      this.setPage(0);
      this.loadMyOrdersPage();
    },

    activeContract() {
      this.setPage(0);
      this.loadMyOrdersPage();
    },

    activePageNumber(value) {
      this.setPage(value);
      this.loadMyOrdersPage();

      if (this.$refs.scroll && this.$refs.scroll.$el) {
        this.$refs.scroll.$el.scrollTop = 0;
      }
    },

    activeWallet: 'initMyOrders',
  },

  created() {
    this.loadMyOrdersPage();
    // this.$store.dispatch('futuresOrders/listenToOrdersChanges');
    // this.$store.dispatch('blockchain/listenToGasPriceChanges');
  },

  beforeDestroy() {
    // this.$store.dispatch('futuresOrders/stopListeningToOrdersChanges');
    // this.$store.dispatch('blockchain/stopListeningToGasPriceChanges');
  },

  methods: {
    ...mapMutations({
      setLoginFlow: 'ui/SET_LOGIN_FLOW',
      setPage: 'futuresOrders/SET_MY_ORDERS_PAGE_NUMBER',
      showLoginFlow: 'ui/SHOW_LOGIN_FLOW',
    }),

    ...mapActions({
      initMyOrders: 'futuresOrders/initMyOrders',
      loadMyOrdersPage: 'futuresOrders/loadMyOrdersPage',
    }),

    startDeposit() {
      if (this.isLogin) {
        this.$store.dispatch('deposit/startDeposit', this.baseToken);
      } else {
        this.$router.push({ name: 'signup' });
      }
    },

    handleSignUp() {
      this.setLoginFlow(0);
      // this.$router.push({ name: 'signup' });
    },

    handleGenerate() {
      this.setLoginFlow(5);
    },
  },

  components: {
    EButton,
    Qriously,
    ECopyText,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-futures-orders {
  $this: &;

  position: relative;

  &__placeholder-container {
    width: 100%;
    height: calc(100% - 34px);
    min-height: 90px;
    padding: 15px 0;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 13px;
    font-weight: 500;

    @include r(1279) {
      width: calc(100vw - 20px);
    }
  }

  &__placeholder-login {
    color: #09168a;
    text-align: center;

    .e-button_size_ss {
      font-size: 11px;
      font-weight: 500;
    }
  }

  &__placeholder-deposit {
    max-width: 550px;
    color: #09168a;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__placeholder-deposit-contract {
    color: #09168a;
    max-width: 320px;
    line-height: 15px;
    text-align: center;
  }

  &__placeholder-deposit-qr {
    width: 68px;
    height: 68px;
    flex: 0 0 68px;
    background: #fff;
    padding: 4px;
    box-sizing: border-box;
    margin-right: 24px;

    @include r(1279) {
      width: 62px;
      height: 62px;
      flex: 0 0 62px;
      padding: 1px;
      margin-right: 5px;
    }
  }

  &__placeholder-deposit-content {
    .e-copy-text__head {
      display: none;
    }

    .e-copy-text__hash {
      font-size: 15px;

      @include r(1279) {
        font-size: 9px;
      }
    }

    .e-copy-text__btns {
      top: 7px;
      right: -50px;

      @include r(1279) {
        right: -10px;
      }
    }

    .e-copy-text__btn {
      min-width: 0;
    }

    .e-copy-text__in {
      padding-right: 0;
      padding-left: 0 !important;
    }

    .e-button {
      width: 42px;
      height: 14px;
      background: #0097FE !important;
      color: #fff;
      border-radius: 2px;
      padding-left: 8px;
      padding-right: 8px;

      &:hover {
        transform: scale(1);
      }

      .e-icon {
        display: none;
      }

      .e-copy-text__btn-text {
        color: #fff !important;
        height: 14px;
        font-size: 9px;

        span {
          display: block;
          height: 14px;
          box-sizing: border-box;
        }
      }
    }

    @include r(1279) {
      font-size: 11px;
    }
  }

  &__placeholder-login-buttons,
  &__placeholder-buttons {
    text-align: center;
    margin-top: 10px;

    .e-button {
      margin: 0 10px;
      padding-left: 15px;
      padding-right: 15px;
    }

    .e-button_fill_green-transparent {
      color: #0fb988;
    }
  }

  &__placeholder-buttons {
    .e-button {
      margin: 0 4px;
    }
  }

  &__placeholder-login-buttons-separator {
    text-transform: uppercase;
    font-size: 9px;
  }

  &__placeholder-no-orders {
    color: #0123e7;
  }

  &__highlight {
    color: #00DB9C;
  }

  &__link {
    text-transform: uppercase;
    font-size: 10px;
    color: #0097FE;
    text-decoration: none;
    transition: color .15s ease-in-out;

    &:hover {
      color: #16aafe;
    }
  }

  &__dark {
    color: #D9D9D9;

    #{$this}__placeholder-login,
    #{$this}__placeholder-deposit,
    #{$this}__placeholder-deposit-contract {
      color: #D9D9D9;
    }

    #{$this}__placeholder-login-buttons {
      .e-button_fill_green-transparent {
        color: #fff;
      }
    }

    #{$this}__placeholder-no-orders {
      color: #D9D9D9;
    }

    #{$this}__placeholder-deposit-content {
      .e-copy-text__hash {
        color: #fff;
      }
    }

    #{$this}__placeholder-buttons {
      .e-button_fill_green-semi-transparent {
        color: #00DB9C;
      }

      .e-button_fill_red-semi-transparent {
        color: #fff;
      }
    }
  }
}
</style>



