<template>
  <div class="the-history-table-tab" :class="[ `the-history-table-tab__${theme}`]">

    <div class="the-history-table-tab__table-head">
      <div class="the-history-table-tab__table-col the-history-table-tab__left-side">
        Date
      </div>
      <div class="the-history-table-tab__table-col the-history-table-tab__left-side">
        Operation
      </div>
      <div class="the-history-table-tab__table-col the-history-table-tab__right-side">
        Amount
      </div>
      <div class="the-history-table-tab__table-col the-history-table-tab__right-side">
        Price
      </div>
      <div class="the-history-table-tab__table-col the-history-table-tab__right-side">
        Fee
      </div>
      <div class="the-history-table-tab__table-col the-history-table-tab__right-side">
        Total
      </div>
      <div class="the-history-table-tab__table-col the-history-table-tab__right-side">
        Status
      </div>
    </div>
    <e-container-scroll
      v-if="history.length"
      class="the-history-table-tab__content"
      ref="scroll"
      :settings="settings"
    >
      <div class="the-history-table-tab__container">
        <div
          v-for="(item, index) in historyWithTokens"
          :key="index"
          class="the-history-table-tab__item"
        >
          <e-history-table-item-tab
            :date="item.createdAt"
            :action="item.action"
            :section="item.section"

            :token="item.amountToken.shortName"
            :decimals="item.amountToken.decimals"
            :amount="item.amount"
            :futuresAssetName="item.futuresAssetName"
            :futuresAssetDecimals="item.futuresAssetDecimals"
            :unit-price="item.unitPrice"
            :is-able-to-have-unit-price="item.isAbleToHaveUnitPrice"

            :fee-token="item.incomeToken.shortName"
            :fee-decimals="item.incomeToken.decimals"
            :fee="item.fee"

            :gas-fee-token="item.gasFeeToken && item.gasFeeToken.shortName"
            :gas-fee-decimals="item.gasFeeToken && item.gasFeeToken.decimals"
            :gas-fee="item.gasFee"

            :net-amount-token="item.incomeToken.shortName"
            :net-amount-decimals="item.incomeToken.decimals"
            :net-amount="item.netAmount"

            :transactionHash="item.transactionHash"
            :status="item.isConfirmed"
            :last-item="item.isLastOnPage"
            :theme="theme"
          />
        </div>
      </div>
    </e-container-scroll>

    <div v-else class="the-history-table-tab__empty-message">
      You don’t have any orders
    </div>

  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import initial from 'lodash/initial';
import last from 'lodash/last';
import CryptoJS from 'crypto-js';

// Components
import EHistoryTableItemTab from '@/components/EHistoryTableItemTab';
import EContainerScroll from '@/components/EContainerScroll';
import EPanelError from '@/components/EPanelError';

// Icons
import iconNoHistory from '@/icons/e-icon_spider.svg';

export default {
  name: 'the-history-table-tab',

  data() {
    return {
      settings: {
        wheelPropagation: true,
        suppressScrollX: true,
      },
    };
  },

  props: {
    history: {
      type: Array,
    },

    isAffiliate: {
      type: Boolean,
    },
  },

  computed: {
    historyWithTokens() {
      const history = this.history;
      if (history.length > 0) {
        const updatedLastElement = {
          ...last(history),
          isLastOnPage: true,
        };
        return [...initial(history), updatedLastElement];
      }
      return history;
    },

    ref() {
      return String(CryptoJS.MD5(this.userAddress)).substring(0, 7);
    },

    ...mapGetters({
      userAddress: 'wallets/activeWalletAddress',
      theme: 'ui/getTheme',
    }),
  },

  created() {
    Object.assign(this, { iconNoHistory });
  },

  components: {
    EPanelError,
    EContainerScroll,
    EHistoryTableItemTab,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-history-table-tab {
  $this: &;

  $number-column-size: 17%;
  $number-column-padding: 30px;

  display: flex;
  flex-flow: column;
  color: $color-dark-grey;

  &__table-head {
    font-size: 9px;
    font-weight: 400;
    /*padding-right: 2px;*/
    text-transform: uppercase;
    letter-spacing: .5px;
    background: #1C222D;
    border-bottom: 1px solid #282C38;

    #{$this}__table-col {
      border-right: 1px solid #282C38;
    }

    .e-tooltip__icon {
      position: relative;
      top: 1px;
      left: 1px;
    }

    .e-tooltip__content {
      margin-top: -5px;
    }
  }

  &__table-head,
  &__table-body-item {
    display: flex;
    align-items: center;
  }

  &__table-body-item {
    height: 34px;
    background: #121722;
    border-bottom: 1px solid #282C38;

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__table-col {
    display: flex;
    align-items: center;
    align-self: stretch;
    flex: 1 1 14.2%;
    padding: 8px 7px;
    box-sizing: border-box;
    border-right: 1px solid #1e202b;
    position: relative;

    &:last-child {
      border: 0;
    }

    &:nth-child(3),
    &:nth-child(4),
    &:nth-child(6) {
      color: #fff;
    }

    .e-big-number {
      position: relative;
    }
  }

  &__column {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    flex: none;
    font-weight: 800;
  }

  &__left-side {
    justify-content: flex-start;
  }

  &__right-side {
    justify-content: flex-end;
  }

  &__block {
    background-color: black;
    height: 2000px;
  }

  &__center-side {
    justify-content: center;
  }

  &__heading {
    font-size: 8px;
    text-transform: uppercase;
    color: $color-dark-blue;
    font-weight: 700;
  }

  &__date {
    flex: 0 0 11%;
    padding-left: 17px;
    box-sizing: border-box;
  }

  &__operation {
    flex: 0 0 8%;
  }

  &__amount {
    flex: 0 0 $number-column-size;
    justify-content: flex-end;
    padding-right: $number-column-padding;
    box-sizing: border-box;
  }

  &__price {
    flex: 0 0 $number-column-size;
    justify-content: flex-end;
    padding-right: $number-column-padding;
    box-sizing: border-box;
  }

  &__fees {
    flex: 0 0 $number-column-size;
    justify-content: flex-end;
    padding-right: $number-column-padding;
    box-sizing: border-box;
  }

  &__gas-fees {
    flex: 0 0 $number-column-size;
    justify-content: flex-end;
    padding-right: $number-column-padding;
    box-sizing: border-box;
  }

  &__net-amount {
    flex: 0 0 $number-column-size;
    justify-content: flex-end;
    padding-right: $number-column-padding;
    box-sizing: border-box;
  }

  &__status {
    flex: 0 0 auto;
    width: 11%;
    padding-right: 16px;
    box-sizing: border-box;
  }

  /*&__item {
    &:not(:last-child) {
      margin-bottom: 7px;
    }
  }*/

  &__container {
    position: relative;
  }

  &__content {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }

  .e-panel-error {
    @include r(767) {
      width: calc(100vw - 50px);
    }
  }

  .e-panel-error__title {
    @include r(767) {
      font-size: 15px;
    }
  }

  &__empty {
    height: 100%;
    min-height: 250px;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.08);
    border-radius: 3px;
    margin: 0 15px 0 5px;
    background-color: #ffffff;

    .e-panel-error__title:before {
      width: 90px;
      left: 0;
      right: 0;
      margin-left: auto;
      margin-right: auto;

      @include r(767) {
        bottom: 4px;
      }
    }

    .e-panel-error__icon-wrapp {
      width: 90px;
      height: 90px;
    }

    .e-panel-error__icon {
      width: 90px;
      height: 90px;
    }
  }

  &__empty-message {
    width: 100%;
    height: calc(100% - 34px);
    flex: 1;
    min-height: 90px;
    padding: 15px 0;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 13px;
    font-weight: 500;
    color: #D9D9D9;
  }

  &__light {
    color: $color-light-grey;

    #{$this}__table-head {
      background: #FBFBFD;
      border-color: #F0F6F9;

      #{$this}__table-col {
        border-color: #F0F6F9;

        &:nth-child(3),
        &:nth-child(4),
        &:nth-child(6) {
          color: $color-light-grey;
        }
      }
    }

    #{$this}__empty-message {
      color: #0123e7;
    }
  }
}
</style>



