<template>
  <transition
    appear
    :duration="10000"
  >
    <div
      class="e-transaction"
      :class="[
        buyOrSell === BUY_TOKEN_ORDER ? 'e-transaction_buy' : 'e-transaction_sell',
        `e-transaction__${theme}`,
        { 'is-my': isMy },
      ]"
      @click="$emit('click', $event)"
    >
      <div class="e-transaction__bg"></div>
      <div
        class="e-transaction__col e-transaction__name"
        :class="buyOrSell === BUY_TOKEN_ORDER ? 'e-transaction__name_buy' : 'e-transaction__name_sell'"
      >
        <e-big-number :value="value" :decimals="decimals" :decimal-places="3"/>
      </div>

      <div class="e-transaction__col e-transaction__detail">
        <e-market-name :exchange="exchange"/>

        <div class="e-transaction__name-icon"><e-icon :src="buyOrSell === BUY_TOKEN_ORDER ? iconBuy : iconSell"/></div>

        <e-big-number :value="price" :decimal-places="9"/>
      </div>

      <div
        class="e-transaction__col e-transaction__time e-transaction__right-align"
        :class="confirmationStateClass"
      >
        <div class="e-transaction__time-text">{{ formattedDate }}</div>
        <div
          v-if="isMy"
          class="e-transaction__time-status"
        >
          <div class="e-transaction__hint">{{ confirmationStateLabel }}</div>
        </div>
      </div>

      <div class="e-transaction__col e-transaction__result e-transaction__right-align">
        <e-big-number :value="ethValue" :decimals="ETH_DECIMALS" :decimal-places="3"/>
        <div class="e-transaction__result-icon"><e-icon :src="iconEth"/></div>
      </div>
    </div>
  </transition>
</template>

<script>

/**
 * Component: Оторажение одного трейда внутри TheTrades
 *
 * @version 1.0.2
 */
import { mapGetters } from 'vuex';

import { BigNumber } from 'bignumber.js';
import { tradesDateSmall } from '@/util/dates';
import { APPROVED_STATUS, PENDING_STATUS, DECLINED_STATUS, BUY_TOKEN_ORDER, EXCHANGE_ETHERMIUM } from '@/util/constants';

// Components
import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber';
import EMarketName from '@/components/EMarketName';

// Icons
import iconSell from '@/icons/e-icon_loss.svg';
import iconBuy from '@/icons/e-icon_profit.svg';
import iconEth from '@/icons/e-icon_earned.svg';

const confirmationStateClasses = {
  [PENDING_STATUS]: 'e-transaction__time_pending',
  [APPROVED_STATUS]: 'e-transaction__time_confirmed',
  [DECLINED_STATUS]: 'e-transaction__time_declined',
};

const confirmationStateLabels = {
  [PENDING_STATUS]: 'Pending',
  [APPROVED_STATUS]: 'Confirmed',
  [DECLINED_STATUS]: 'Declined',
};

export default {
  name: 'e-transaction',

  data() {
    return {
      iconEth,
      iconBuy,
      iconSell,
    };
  },

  props: {
    // Типа трейда (покупка/проадажа)
    buyOrSell: {
      type: String,
      default: BUY_TOKEN_ORDER,
    },

    // количество токена
    value: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // количество эфира
    ethValue: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // Цена по которой была проведена сделка
    price: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // Дата и время проведения сделки
    time: {
      type: Date,
      default: null,
    },

    // Биржа
    exchange: {
      type: String,
      default: EXCHANGE_ETHERMIUM,
    },

    confirmationState: {
      type: String,
      default: PENDING_STATUS,
      validator: v => [PENDING_STATUS, APPROVED_STATUS, DECLINED_STATUS].includes(v),
    },

    isMy: {
      type: Boolean,
      default: false,
    },

    // Количество знаков после запятой у токена
    decimals: {
      type: Number,
      default: 0,
    },
  },

  computed: {
    confirmationStateClass() {
      return confirmationStateClasses[this.confirmationState];
    },

    confirmationStateLabel() {
      return confirmationStateLabels[this.confirmationState];
    },

    formattedDate() {
      return this.time !== null
        ? tradesDateSmall(this.time)
        : '';
    },

    ...mapGetters({
      theme: 'ui/getTheme',
    }),
  },

  components: {
    EIcon,
    EBigNumber,
    EMarketName,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-transaction {
  $this: &;
}
</style>




