//   'mainSeriesProperties.baselineStyle.topFillColor2': 'rgba(111, 179, 111, .1)',
//   'mainSeriesProperties.baselineStyle.bottomFillColor1': 'rgba(111, 179, 111, .1)',
//   'mainSeriesProperties.baselineStyle.bottomFillColor2': '#DC4727',
//   'mainSeriesProperties.baselineStyle.topLineColor': '#00D89A',
//   'mainSeriesProperties.baselineStyle.bottomLineColor': '#DC4727',
//   'paneProperties.vertGridProperties.color': '#212121',
//   'paneProperties.horzGridProperties.color': '#212121',
//   'scalesProperties.backgroundColor': '#DC4727',
//   'scalesProperties.lineColor': '#1E1E1E',
//   'scalesProperties.textColor': '#3E4041',
// };
//
// const studiesOverridesLight = {
//   'volume.volume.color.0': '#DC4727',
//   'volume.volume.color.1': '#00D89A',
// };

export default {
  name: 'the-futures-tradingview',

  data() {
    return {
      //  Bars = 0, Candles = 1, Line = 2, Area = 3, Heiken Ashi = 8, Hollow Candles = 9, Renko = 4, Kagi = 5
      // Point&Figure = 6, Line Break = 7, Baseline = 10
      graphicType: 1,
    };
  },

  computed: {
    ...mapGetters({
      activeContractName: 'futuresActiveContract/contractName',
      theme: 'ui/getTheme',
    }),

    ...mapState({
      activeContract: state => state.futuresActiveContract.activeContract,
    }),
  },

  watch: {
    activeContractName() {
      window.TradingViewInstance.options.symbol = this.activeContract.chartSymbol;
      window.TradingViewInstance.reload();
    },
    theme() {
      if (this.theme === 'light') {
        window.TradingViewInstance.options.theme = 'Light';
        window.TradingViewInstance.reload();
      }

      if (this.theme === 'dark') {
        window.TradingViewInstance.options.theme = 'Dark';
        window.TradingViewInstance.reload();
      }
    },
  },

  beforeDestroy() {
    if (window.TradingViewInstance) {
      window.TradingViewInstance.remove();
    }
    window.TradingViewInstance = undefined;
  },

  mounted() {
    if (!window.TradingView) {
      const chartingLibrary = document.createElement('script');
      chartingLibrary.setAttribute('src', 'https://s3.tradingview.com/tv.js');
      document.head.appendChild(chartingLibrary);
    }

    const tradingViewInitInterval = setInterval(() => {
      if (window.TradingView) {
        clearInterval(tradingViewInitInterval);
      }

      if (!window.TradingViewInstance && window.TradingView) {
        this.tradingViewInit();
      }
    }, 100);
  },

  methods: {
    getTimezone() {
      const winterOffset = new Date('2010-01-01T00:00:00').getTimezoneOffset();
      const summerOffset = new Date('2010-06-01T00:00:00').getTimezoneOffset();
      return timezonesOffsets[`${winterOffset}:${summerOffset}`];
    },

    tradingViewInit() {
      // eslint-disable-next-line
      window.TradingViewInstance = new TradingView.widget({
        width: '100%',
        height: '100%',
        symbol: this.activeContract.chartSymbol,
        interval: '60',
        timezone: 'Etc/UTC',
        style: '1',
        locale: 'en',
        // toolbar_bg: 'rgba(14,14,14,1)',
        enable_publishing: false,
        allow_symbol_change: true,
        container_id: 'tradingview_684bd',
        theme: this.theme === 'light' ? 'Light' : 'Dark',
      });
    },
  },
};
</script>

<style lang='scss'>
@import '~@/scss/styles';

.the-trading-view {
  position: relative;
  height: 100%;

  &__chart {
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 21;
  }
}
</style>




