<template>
  <div
    class="e-history-table-item-tab"
    :class="{
      'e-history-table-item-tab_last-item' : lastItem,
      'e-history-table-item-tab__light' : theme === 'light',
    }"
  >
    <div class="e-history-table-item-tab__column e-history-table-item-tab__date" v-html="dateToString(date)"/>

    <!-- BEGIN: Icons for oparations -->
    <!--<div class="e-history-table-item-tab__column e-history-table-item-tab__operation" v-html="action.toUpperCase()">-->

    <div class="e-history-table-item-tab__column e-history-table-item-tab__operation">
      <span :class="[ `e-history-table-item-tab__operation-${action}` ]">{{action.toUpperCase()}}</span>
    </div>
    <!--
      <div
        v-if="action === ACTION_SELL"
        class="e-history-table-item-tab__operation-icon e-history-table-item-tab__operation-icon_sell"
      >
        <e-icon
          :src="iconLoss"
        />
        <div class="e-history-table-item-tab__hint">sell</div>
      </div>

      <div
        v-else-if="action === ACTION_BUY"
        class="e-history-table-item-tab__operation-icon e-history-table-item-tab__operation-icon_buy"
      >
        <e-icon
          :src="iconProfit"
        />
        <div class="e-history-table-item-tab__hint">buy</div>
      </div>

      <div
        v-else-if="action === ACTION_DEPOSIT"
        class="e-history-table-item-tab__operation-icon e-history-table-item-tab__operation-icon_deposit"
      >
        <e-icon
          :src="iconOperation"
        />
        <div class="e-history-table-item-tab__hint">deposit</div>
      </div>

      <div
        v-else-if="action === ACTION_WITHDRAW"
        class="e-history-table-item-tab__operation-icon e-history-table-item-tab__operation-icon_withdraw"
      >
        <e-icon
          :src="iconOperation"
        />
        <div class="e-history-table-item-tab__hint">withdrawal</div>
      </div>

      <div
        v-else-if="action === ACTION_AFFILIATE"
        class="e-history-table-item-tab__operation-icon e-history-table-item-tab__operation-icon_affiliate"
      >
        <e-icon
          :src="iconBag"
        />
        <div class="e-history-table-item-tab__hint">affiliate</div>
      </div>
      -->
    <!--</div>-->
    <!-- END: Icons for oparations -->

    <div class="e-history-table-item-tab__column e-history-table-item-tab__number e-history-table-item-tab__amount">
      <template v-if="decimals != null">
        <e-big-number
          :value="amount"
          :decimals="decimals"
          :min-decimal-places="DEFAULT_MIN_DECIMALS"
          :max-decimal-places="DEFAULT_MAX_DECIMALS"
          leadingZerosClass
        />
        &nbsp;
        <span class="e-history-table-item-tab__combiner">{{ futuresAssetName.replace(/USDT|USD/g, '') }}</span>
      </template>
      <template v-else>
        <span class="e-history-table-item-tab__combiner">N/A</span>
      </template>
    </div>

    <div class="e-history-table-item-tab__column e-history-table-item-tab__number e-history-table-item-tab__price">
      <template v-if="isAbleToHaveUnitPrice && unitPrice">
        <e-big-number
          :value="unitPrice"
          :decimal-places="futuresAssetDecimals"
          leadingZerosClass
        />
        &nbsp;
        <span class="e-history-table-item-tab__combiner">
          {{section !== 'futures_trade' ? 'ETH' : 'USD'}}
        </span>
      </template>
      <template v-else-if="isAbleToHaveUnitPrice">
        <span class="e-history-table-item-tab__combiner">N/A</span>
      </template>
    </div>

    <div class="e-history-table-item-tab__column e-history-table-item-tab__number e-history-table-item-tab__fees" :class="{ isZero: fee !== null && fee.isZero() }">
      <template v-if="fee != null && feeDecimals != null">
        <e-big-number
          :value="fee !== null && fee.isZero() ? fee : fee.negated()"
          :decimals="decimals"
          :min-decimal-places="8"
          :max-decimal-places="8"
          leadingZerosClass
        />
        &nbsp;
        <span class="e-history-table-item-tab__combiner">
         {{feeToken}}
        </span>
      </template>
      <template v-else-if="fee != null && feeDecimals == null">
        <span class="e-history-table-item-tab__combiner">N/A</span>
      </template>
    </div>

    <!--<div class="e-history-table-item-tab__column e-history-table-item-tab__number e-history-table-item-tab__gas-fees" :class="{ isZero: gasFee !== null && gasFee.isZero() }">
      <template v-if="gasFee != null && gasFeeDecimals != null">
        <e-big-number
          :value="gasFee !== null && gasFee.isZero() ? gasFee : gasFee.negated()"
          :decimals="gasFeeDecimals"
          :min-decimal-places="DEFAULT_MIN_DECIMALS"
          :max-decimal-places="DEFAULT_MAX_DECIMALS"
          leadingZerosClass
        />
        <span class="e-history-table-item-tab__combiner">
          {{section !== 'futures_trade' ? gasFeeToken : 'USD'}}
        </span>
      </template>
      <template v-else-if="gasFee != null && gasFeeDecimals == null">
        <span class="e-history-table-item-tab__combiner">N/A</span>
      </template>
    </div>-->

    <div class="e-history-table-item-tab__column e-history-table-item-tab__number e-history-table-item-tab__net-amount">
      <template v-if="netAmountDecimals != null">
        <e-big-number
          :value="amount.times(unitPrice)"
          :decimals="decimals"
          :decimal-places="2"
          leadingZerosClass
        />
        &nbsp;
        <span class="e-history-table-item-tab__combiner">
          {{section !== 'futures_trade' ? netAmountToken : 'USD'}}
        </span>
      </template>
      <template v-else>
        <span class="e-history-table-item-tab__combiner">N/A</span>
      </template>
    </div>

    <div class="e-history-table-item-tab__column e-history-table-item-tab__status">
      <div
        v-if="transactionHash"
        class="e-history-table-item-tab__link" @click="openLink(`${ETHERSCAN_URL}/tx/${transactionHash}`)"
      >
        <e-icon :src="iconLink"/>
        <e-hint-adress
          title="Transaction Hash"
          side="right"
          :adress="transactionHash"
          link-type="tx"
        />
      </div>
      <div
        class="e-history-table-item-tab__status-circle"
        :class="{
          'e-history-table-item-tab__status-circle_approved' : status === true,
          'e-history-table-item-tab__status-circle_pending' : status === null,
          'e-history-table-item-tab__status-circle_declined' : status === false,
        }"
      >
        <div class="e-history-table-item-tab__hint">{{statusString}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { BigNumber } from 'bignumber.js';
import { dateToShortStringLine as dateToString } from '@/util/dates';
import {
  ACTION_DEPOSIT,
  ACTION_WITHDRAW,
  ACTION_BUY,
  ACTION_SELL,
  ACTION_AFFILIATE,
} from '@/api/backend/constants';
import { ZERO } from '@/util/numbers';

// Components
import EBigNumber from '@/components/EBigNumber';
import EIcon from '@/components/EIcon';
import EHintAdress from '@/components/EHintAdress';

// Icons
import iconBag from '@/icons/e-icon_bag.svg';
import iconLink from '@/icons/e-icon_link-site.svg';
import iconLoss from '@/icons/e-icon_loss.svg';
import iconOperation from '@/icons/e-icon_arrow-operation.svg';
import iconProfit from '@/icons/e-icon_profit.svg';

const ALL_ACTIONS = [
  ACTION_DEPOSIT,
  ACTION_WITHDRAW,
  ACTION_BUY,
  ACTION_SELL,
  ACTION_AFFILIATE,
];

export default {
  name: 'e-history-table-item-tab',

  props: {
    date: {
      type: Date,
      default: null,
    },

    action: {
      type: String,
      required: true,
      validator: a => ALL_ACTIONS.includes(a),
    },

    section: {
      type: String,
      required: true,
    },

    token: {
      type: String,
      default: () => '',
    },

    decimals: {
      type: Number,
    },

    amount: {
      type: BigNumber,
      default: ZERO,
    },

    futuresAssetName: String,
    futuresAssetDecimals: {
      type: Number,
      default: 2,
    },

    isAbleToHaveUnitPrice: {
      type: Boolean,
      default: false,
    },

    unitPrice: {
      type: BigNumber,
      default: ZERO,
    },

    fee: {
      type: BigNumber,
      default: null,
    },

    feeToken: {
      type: String,
      default: null,
    },

    feeDecimals: {
      type: Number,
    },

    gasFee: {
      type: BigNumber,
      default: null,
    },

    gasFeeToken: {
      type: String,
      default: null,
    },

    gasFeeDecimals: {
      type: Number,
    },

    netAmount: {
      type: BigNumber,
      default: null,
    },

    status: {
      type: Boolean,
      default: false,
    },

    netAmountToken: {
      type: String,
      default: null,
    },

    netAmountDecimals: {
      type: Number,
    },

    transactionHash: {
      type: String,
    },

    theme: {
      type: String,
    },

    lastItem: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    statusString() {
      let res = 'pending';
      if (this.status) {
        res = 'approved';
      } else if (this.status === false) {
        res = 'declined';
      }
      return res;
    },
  },

  created() {
    Object.assign(this, {
      ACTION_DEPOSIT,
      ACTION_WITHDRAW,
      ACTION_BUY,
      ACTION_SELL,
      ACTION_AFFILIATE,

      // icons
      iconLoss,
      iconProfit,
      iconOperation,
      iconLink,
      iconBag,
    });
  },

  methods: {
    dateToString,
    openLink(url) {
      const win = window.open(url, '_blank');
      if (win) {
        win.focus();
      }
    },
  },

  components: {
    EBigNumber,
    EIcon,
    EHintAdress,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-history-table-item-tab {
  $this: &;
  $number-column-size: 17%;

  display: flex;
  height: 34px;
  /*box-shadow: 0 2px 3px rgba(0, 0, 0, 0.08);*/
  /*border-radius: 3px;*/
  box-sizing: border-box;
  font-size: 11px;
  transition: 0.3s;
  background: #121722;
  color: #CACCD3;
  border-bottom: 1px solid #282C38;

  &_last-item {
    .e-hint-adress {
      top: auto;
      bottom: calc(100% + 3px);
    }

    #{$this}__hint {
      top: auto;
      bottom: calc(100% + 5px);
    }
  }

  &__column {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    flex: none;
    flex: 1 1 14.2%;
    padding: 8px 7px;
    border-right: 1px solid #1e202b;

    &:last-child {
      border-right: none;
    }
  }

  /*&__date {
    flex: 0 0 auto;
    width: 11%;
    padding-left: 17px;
    box-sizing: border-box;
    font-size: 10px;
    color: $color-grey;
    font-weight: 600;
    line-height: 1.4;
    letter-spacing: 0.5px;
  }*/

  &__operation {
    justify-content: left;
  }

  &__operation-icon {
    position: relative;
    width: 16px;
    height: 18px;

    &:hover {
      #{$this}__hint {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__operation-icon_sell .e-icon {
    width: 18px;
    height: 16px;
    transform: translate(-1px, 1px) rotate(-90deg);
  }

  &__operation-icon_buy .e-icon {
    width: 18px;
    height: 16px;
    transform: translate(-1px, 1px) rotate(-90deg);
  }

  &__operation-icon_affiliate .e-icon {
    fill: rgb(131, 132, 171);
    transition: 0.3s;

    &:hover {
      fill: #0123e7;
    }
  }

  &__operation-icon_deposit {
    fill: rgb(131, 132, 171);
    transition: 0.3s;

    &:hover .e-icon svg path {
      fill: #0123e7;
    }
  }

  &__operation-icon_withdraw .e-icon {
    transform: rotate(-180deg);
    transition: 0.3s;

    &:hover svg path {
      fill: #0123e7;
    }
  }

  &__number {
    justify-content: flex-end;
    overflow: hidden;
    color: $color-green-dark;

    .e-big-number {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }

  &__operation-sell,
  &__operation-buy {
    width: 36px;
    height: 14px;
    box-sizing: border-box;
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    text-transform: uppercase;
    font-size: 9px;
    letter-spacing: 0;
    text-align: center;
  }

  &__operation-buy {
    color: #00DB9C;
    background: rgba(31,206,67,0.10);
  }

  &__operation-sell {
    color: #DC4727;
    background: rgba(220,71,39,0.10);
  }

  /*&__amount {
    .e-big-number__leading-zeros {
      color: rgba(13, 16, 91, 0.5);
    }
  }*/

  /*&__price {
    color: $color-ultramarine;

    .e-big-number__leading-zeros {
      color: rgba(13, 16, 91, 0.5);
    }
  }*/

  &__fees {
    color: #ef6e5b;
    /*padding-left: 1px;*/

    .e-big-number__leading-zeros {
      color: #ef6e5b;
    }

    &.isZero {
      color: rgba(#fff, .5);

      .e-big-number__leading-zeros {
        color: rgba(#fff, .5);
      }
    }
  }

  &__gas-fees {
    color: #914337;
    /*padding-left: 2px;*/

    .e-big-number__leading-zeros {
      color: rgba(145, 67, 55, 0.5);
    }

    &.isZero {
      color: rgba(13, 16, 91, 0.5);

      .e-big-number__leading-zeros {
        color: rgba(13, 16, 91, 0.5);
      }
    }
  }

  &__net-amount {
    /*font-weight: 700;*/
    /*padding-left: 2px;*/
    /*color: $color-ultramarine;*/

    .e-big-number__leading-zeros {
      color: rgba(13, 16, 91, 0.5);
    }
  }

  &__status {
    /*flex: 0 0 auto;
    width: 11%;*/
    justify-content: flex-end;
    /*padding-right: 19px;
    box-sizing: border-box;
    display: flex;*/
  }

  &__combiner {
    /*font-weight: 800;
    color: $color-dark-blue;
    margin-left: 6px;
    text-transform: uppercase;
    min-width: 44px;*/
    text-align: left;
  }

  &__status-circle {
    flex: 0 0 auto;
    position: relative;
    border-radius: 50%;
    height: 8px;
    width: 8px;

    &:hover {
      #{$this}__hint {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__status-circle_approved {
    background-color: #baf5c6;
    border: 2px solid #1fce43;
  }

  &__status-circle_declined {
    background-color: rgba(#da4830, 0.38);
    border: 2px solid #da4830;
  }

  &__status-circle_pending {
    background-color: rgba(#ffde44, 0.38);
    border: 2px solid #ffde44;
  }

  &__hint {
    position: absolute;
    visibility: hidden;
    opacity: 0;
    transition: $transition-time;
    top: calc(100% + 5px);
    right: 0;
    background-color: #fff;
    color: $color-dark-blue;
    font-size: 12px;
    font-weight: 400;
    line-height: 1;
    box-shadow: 0 3px 10px rgba(148, 165, 180, 0.14);
    border-radius: 3px;
    padding: 5px 10px 5px;
    white-space: nowrap;
  }

  &__link {
    flex: 0 0 auto;
    width: 21px;
    height: 21px;
    display: block;
    margin-right: 20px;
    position: relative;
    cursor: pointer;

    .e-icon svg {
      fill: #cfcfde;
      transition: $transition-time;
    }

    &:hover {
      .e-icon svg {
        fill: $color-blue;
      }

      .e-hint-adress {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  .e-hint-adress,
  .e-history-table-item-tab__hint {
    z-index: 2;
  }

  &__light {
    color: #09168a;
    background: #fff;
    border-color: #F0F6F9;

    #{$this}__column {
      border-color: #F0F6F9;
    }

    #{$this}__fees {
      color: #914337;
      /*padding-left: 1px;*/

      .e-big-number__leading-zeros {
        color: rgba(145, 67, 55, 0.5);
      }

      &.isZero {
        color: rgba(13, 16, 91, 0.5);

        .e-big-number__leading-zeros {
          color: rgba(13, 16, 91, 0.5);
        }
      }
    }
  }
}
</style>



