<template>
  <div
    class="e-hint-adress"
    :class="[side ? 'e-hint-adress_' + side : '']"
  >
    <div class="e-hint-adress__title">{{title}}</div>
    <a
      :href="`${ETHERSCAN_URL}/${linkType}/${adress}`"
      target="_blank"
      class="e-hint-adress__token"
      @click.stop
    >
      {{sliceAdress}}
    </a>
  </div>
</template>

<script>
import shortenAddress from '@/util/shortenAddress';

const SIDES = ['left', 'right', 'top-right'];

export default {
  name: 'e-hint-adress',

  props: {
    title: {
      type: String,
      default: null,
    },

    adress: {
      type: String,
      default: null,
    },

    side: {
      type: String,
      default: 'left',
      validator: v => SIDES.includes(v),
    },

    linkType: {
      type: String,
      default: 'token',
    },
  },

  computed: {
    sliceAdress() {
      return shortenAddress(this.adress, 10, 10);
    },
  },
};
</script>


<style lang="scss">
@import '~@/scss/styles';

.e-hint-adress {
  position: absolute;
  opacity: 0;
  visibility: hidden;
  transition: $transition-time;
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 0 3px 10px rgba(#94A5B4, .14);
  box-sizing: border-box;
  min-height: 38px;
  min-width: 106px;
  text-align: center;
  padding: 5px 10px 2px;

  &_left {
    top: calc(100% + 3px);
  }

  &_top-right {
    top: -3px;
    transform: translateY(-100%);
    right: 0;
  }

  &_right {
    top: calc(100% + 3px);
    right: 0;
  }

  &__title {
    font-size: 10px;
    font-weight: 400;
    color: #94A5B4;
    line-height: 1;
    margin-bottom: 3px;
  }

  &__token {
    font-weight: 700;
    font-size: 11px;
    display: inline-block;
    text-decoration: none;
    line-height: 1;
    white-space: nowrap;
    max-width: 150px;
    color: #17DA9D;
    transition: 0.3s;
    position: relative;

    &:hover {
      color: $color-blue;
    }
  }
}
</style>



